import {Change} from './Change';

export class ChangeProcessPlanGroup extends Change {
    constructor(group) {
        super('Change process plan group');
        this.group = group;
        this.prevGroup = null;
    }

    applyProcessPlanGroupChange(processPlan) {
        this.prevGroup = processPlan.group;
        return {...processPlan, group: this.group};
    }
    revertProcessPlanGroupChange(processPlan) {
        return {...processPlan, group: this.prevGroup};
    }
}
