import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Select, {components} from 'react-select';
import _ from 'lodash';

import './CheckableMultiSelect.css';
import CheckBox from '../checkBox/CheckBox';
import ModalPdfOverview from "../modalPdfOverview/ModalPdfOverview.container";

const ELEMENTS = ['checkbox', 'select'];

class CheckableMultiSelect extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedElementIndex: 0,
            openedSelect: false,
            modalIsOpen: false,
            modalOpenCount: 0
        };
    }

    setSelect = (select) => {
        this.select = select;
    };

    render() {
        let className = 'checkable-multi-select ' + (this.props.className || ' ');

        let flatOptions = _.chain(this.props.options)                    // Ungroup options into plain list for easier search
            .map('options')
            .flatten()
            .value();
        let selectedOptions = this.props.value.values.map(
            value => _.find(flatOptions, option => _.isEqual(option.value, value.type))
        );
        const MultiValueContainer = (props) => {
            let modalDetails = _.find(this.props.value.values, value => value.type === 'overview_report');
            return (
                <div>
                    <components.MultiValueContainer {...props}>
                        {props.children[0]}
                        {props.children[0]._owner.key === 'overview_report' ?
                            <div className="report-edit-icon" onMouseDown={(e) => this.handleMultiValueClick(e, props)}>
                                <i className="material-icons">edit</i>
                            </div>
                            : <i/>}
                        {props.children[1]}
                    </components.MultiValueContainer>
                    {this.state.modalIsOpen && props.children[0]._owner.key === 'overview_report' ?
                        <ModalPdfOverview
                            translate={this.props.translate}
                            transitionId={this.props.parentId}
                            details={modalDetails.details}
                            template={modalDetails.template}
                            templateOptions={this.props.templateOptions}
                            modalIsOpen={this.state.modalIsOpen}
                            modalIsOpenCount={this.state.modalOpenCount}
                            onCount={this.handleSetModalCount}
                            onSelectModal={this.handleSetModal}
                        /> : <></>}
                </div>);
        };

        return (
            <div className={className} tabIndex="0" onKeyDown={this.handleKeyDown}>
                <CheckBox
                    id={this.props.id}
                    value={this.props.value.checked}
                    label={this.props.label}
                    groupLabelId={this.props.groupLabelId}
                    focused={ELEMENTS[this.state.selectedElementIndex] === 'checkbox' && this.props.focused}
                    disabled={this.props.disabled}
                    translate={this.props.translate}
                    onChange={this.handleCheckedChange}
                    onSelect={this.props.onSetSelection}/>
                <Select
                    id={this.props.id}
                    components={{MultiValueContainer}}
                    options={this.props.options}
                    value={selectedOptions}
                    ref={this.setSelect}
                    isDisabled={!this.props.disabled ? !this.props.value.checked : true}
                    onChange={this.handleSelectedOptionsChange}
                    onClose={this.handleSelectClose}
                    onOpen={this.handleSelectOpen}
                    onSelect={this.props.onSetSelection}
                    isMulti/>
            </div>
        );
    }

    componentDidUpdate(prevProps, prevState) {
        if (ELEMENTS[prevState.selectedElementIndex] !== 'select' && ELEMENTS[this.state.selectedElementIndex] === 'select') {
            this.select.focus();
        }
        if (prevProps.disabled !== this.props.disabled && this.props.disabled) {
            this.handleSelectedOptionsChange([]);
            this.props.onChange({...this.props.value, values: [], checked: false});
        }
    }

    handleCheckedChange = (checked) => {
        this.props.onChange({...this.props.value, checked});
    };

    handleSelectedOptionsChange = (selectedOptions) => {
        this.props.onChange({
            ...this.props.value, values: _.map(selectedOptions, option => {
                if (option.value === 'overview_report') {
                    return {
                        type: option.value,
                        details: '',
                        template: _.find(this.props.templateOptions, option => _.isEqual(option.value, '')).value
                    }
                } else {
                    return {type: option.value}
                }
            })
        });
    };

    handleKeyDown = (e) => {
        switch (e.key) {
            case 'ArrowDown':
                if (this.state.openedSelect) {
                    e.stopPropagation();
                } else if (this.state.selectedElementIndex < ELEMENTS.length - 1 && this.props.value.checked) {
                    e.stopPropagation();
                    this.setState({...this.state, selectedElementIndex: this.state.selectedElementIndex + 1});
                }
                break;
            case 'ArrowUp':
                if (this.state.openedSelect) {
                    e.stopPropagation();
                } else if (this.state.selectedElementIndex > 0) {
                    e.stopPropagation();
                    this.setState({...this.state, selectedElementIndex: this.state.selectedElementIndex - 1});
                }
                break;
            default:
                break;
        }

        if ((e.which === 38 || e.which === 40) && this.openedSelect) {
            e.stopPropagation();
        }
    };

    handleSelectOpen = () => {
        if (this.props.value.values.length !== this.props.options.length) {
            this.setState({...this.state, openedSelect: true});
        }
    };

    handleSelectClose = () => {
        this.setState({...this.state, openedSelect: false});
    };

    handleMultiValueClick = (e, props) => {
        e.stopPropagation();
        e.preventDefault();
        this.setState({
            ...this.state,
            modalIsOpen: true,
            modalOpenCount: 0
        });
    };

    handleSetModal = (modal) => {
        this.setState({...this.state, modalIsOpen: modal});
    };

    handleSetModalCount = () => {
        this.setState({...this.state, modalOpenCount: this.state.modalOpenCount + 1});
    }

}

const VariableShape = PropTypes.shape({
    id: PropTypes.string.isRequired,
    key: PropTypes.string.isRequired,
    value: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired
});

const ValueShape = PropTypes.shape({
    checked: PropTypes.bool.isRequired,
    values: PropTypes.arrayOf(PropTypes.oneOfType([PropTypes.string, PropTypes.object, VariableShape]))
});

const OptionShape = PropTypes.shape({
    label: PropTypes.string,
    value: PropTypes.oneOfType([PropTypes.string, VariableShape])
});

CheckableMultiSelect.propTypes = {
    id: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
    value: ValueShape,
    options: PropTypes.arrayOf(OptionShape),
    groupLabelId: PropTypes.string,
    className: PropTypes.string,
    focused: PropTypes.bool,
    groupLabel: PropTypes.string,
    onChange: PropTypes.func.isRequired,
    onSetSelection: PropTypes.func.isRequired,
    parentId: PropTypes.string.isRequired
};

export default CheckableMultiSelect;
