import {connect} from 'react-redux';
import VariableItem from "./VariableItem";
import { duplicateExternalObject, editExternalObject} from "../../../../../../redux/modules/availableObjects";

const mapDispatchToProps = (dispatch, {variable, onSelect, onVariableDuplicated, onVariableUpdated}) => ({
    onSetVariable: () => onSelect(variable),
    onDuplicate: () => dispatch(duplicateExternalObject('variable', variable.id, onVariableDuplicated)),
    onEdit: () => dispatch(editExternalObject('variable', variable.id, onVariableUpdated))
});
export default connect(null, mapDispatchToProps)(VariableItem)
