import React, {Component} from 'react';
import './ZoomToolbar.css';
import PropTypes from 'prop-types';

const ZOOM_TIME_DELAY = 300;

class ZoomToolbar extends Component {
    render() {
        return (
            <div className='zoomPanel'>
                <div className='zoomIcon'
                     title={this.props.translate("NG_PPE_Zoom_out_long")}
                     role='button'
                     aria-label={this.props.translate("NG_PPE_Zoom_out_long")}
                     onMouseDown={this.onZoomOut}
                     onMouseUp={this.onStopZoomOut}
                     onMouseOut={this.onStopZooming}
                     onTouchStart={this.onZoomOut}
                     onTouchEnd={this.onStopZoomOut}>
                    <i className="material-icons">zoom_out</i>
                </div>
                <div className='zoomIcon'
                     title={this.props.translate("NG_PPE_Reset_zoom_long")}
                     role='button'
                     aria-label={this.props.translate("NG_PPE_Reset_zoom_long")}
                     onClick={this.onReset}>
                    <span>{this.props.translate("NG_PPE_Reset_zoom_long")}</span>
                </div>
                <div className='zoomIcon'
                     title={this.props.translate("NG_PPE_Zoom_in_long")}
                     role='button'
                     aria-label={this.props.translate("NG_PPE_Zoom_in_long")}
                     onMouseDown={this.onZoomIn}
                     onMouseUp={this.onStopZoomIn}
                     onMouseOut={this.onStopZooming}
                     onTouchStart={this.onZoomIn}
                     onTouchEnd={this.onStopZoomIn}>
                    <i className="material-icons">zoom_in</i>
                </div>
                <div className='zoomIcon'
                     title={this.props.translate("NG_PPE_Fit_to_size_long")}
                     role='button'
                     aria-label={this.props.translate("NG_PPE_Fit_to_size_long")}
                     onClick={this.onFitTheScreen}>
                    <i className="material-icons">zoom_out_map</i>
                </div>
            </div>
        );
    }

    onZoomIn = () => {
        this.mouseDownTimeout = setInterval(() => this.props.onChangeZoom(-1), ZOOM_TIME_DELAY);
    };

    onStopZooming = () => {
        clearInterval(this.mouseDownTimeout);
    };

    onStopZoomIn = () => {
        this.props.onChangeZoom(-1);
        clearInterval(this.mouseDownTimeout);
    };

    onZoomOut = () => {
        this.mouseDownTimeout = setInterval(() => this.props.onChangeZoom(1), ZOOM_TIME_DELAY);
    };

    onStopZoomOut = () => {
        this.props.onChangeZoom(1);
        clearInterval(this.mouseDownTimeout);
    };

    onFitTheScreen = () => {
        this.props.onFitTheScreen();
    };

    onReset = () => {
        this.props.onReset();
    };
}

ZoomToolbar.propTypes = {
    translate: PropTypes.func.isRequired,
    onChangeZoom: PropTypes.func.isRequired,
    onFitTheScreen: PropTypes.func.isRequired,
    onReset: PropTypes.func.isRequired,
};

export default ZoomToolbar;
