import {Change} from './Change';
import _ from 'lodash';

export class ChangeStepProperty extends Change {
    constructor(stepId, name, value) {
        super('Change step property');
        this.stepId = stepId;
        this.name = name;
        this.value = value;

        this.currentValue = null;
    }

    applyStepPropertyChange(stepsProperties) {
        return _.map(stepsProperties, step => {
            if (step.stepId === this.stepId && step.name === this.name) {
                this.currentValue = step.value;
                return {...step, value: this.value};
            } else {
                return step;
            }
        });
    }
    revertStepPropertyChange(stepsProperties) {
        return _.map(stepsProperties, step => {
            if (step.stepId === this.stepId && step.name === this.name) {
                return {...step, value: this.currentValue};
            } else {
                return step;
            }
        });
    }

    applyUpdateConnection(connections) {
        return !_.isUndefined(_.find(connections, connection => connection.startStepId === this.stepId)) ?
            _.map(connections, connection => {
                if (connection.startStepId === this.stepId) {
                    return {...connection, endStepId: this.value};
                } else {
                    return connection;
                }
            }) : connections
    }

    revertUpdateConnection(connections) {
        return !_.isUndefined(_.find(connections, connection => connection.startStepId === this.stepId)) ?
            _.map(connections, connection => {
                if (connection.startStepId === this.stepId) {
                    return {...connection, endStepId: this.currentValue};
                } else {
                    return connection;
                }
            }) : connections
    }
}