import React, {Component} from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';

import {translatedTransitionCheckboxType} from "../../../../dictionary"
import CheckBox from '../checkBox/CheckBox';

import './CheckBoxGroup.css';

class CheckBoxGroup extends Component {
    constructor(props) {
        super(props);

        this.checkboxes = [];
        this.state = {
            selectedCheckBoxIndex: 0
        }
    }

    setCheckBoxGroup = (checkBoxGroup) => {
        this.checkBoxGroup = checkBoxGroup;
    };

    render() {
        let className = 'checkbox-group ' + (this.props.className || ' ');

        if (this.checkboxes.length !== 0) {
            this.checkboxes = [];
        }

        return (
            <div className={className} ref={this.setCheckBoxGroup} tabIndex="0" onKeyDown={this.handleKeyDown}>
                <label id={`checkbox-group-label-${this.props.label}`}>{this.props.translate(translatedTransitionCheckboxType[this.props.label])}</label>
                <div>{_.map(this.props.options, this.renderOption)}</div>
            </div>
        );
    }

    renderOption = (option) => {
        this.checkboxes.push(option.value);
        return (
            <CheckBox key={option.value}
                      id={this.props.id}
                      label={option.label}
                      groupLabelId={`checkbox-group-label-${this.props.label}`}
                      focused={this.checkboxes[this.state.selectedCheckBoxIndex] === option.value && this.props.focused}
                      value={_.includes(this.props.values, option.value)}
                      selectedOnOtherTransitions={_.includes(this.props.selectedOptions, option.value)}
                      disabled={this.props.disabled}
                      translate={this.props.translate}
                      onChange={this.handleChangeOption(option.value)}
                      onSelect={this.props.onSetSelection}/>
        );
    };

    handleChangeOption = (optionValue) => (checked) => {
        if (checked) {
            this.props.onChange([...this.props.values, optionValue]);
        } else {
            this.props.onChange(_.filter(this.props.values, value => value !== optionValue));
        }
    };

    handleKeyDown = (e) => {
        switch (e.key) {
            case 'ArrowDown':
                if (this.state.selectedCheckBoxIndex < this.checkboxes.length - 1) {
                    e.stopPropagation();
                    this.setState({...this.state, selectedCheckBoxIndex: this.state.selectedCheckBoxIndex + 1});
                }
                break;
            case 'ArrowUp':
                if (this.state.selectedCheckBoxIndex > 0) {
                    e.stopPropagation();
                    this.setState({...this.state, selectedCheckBoxIndex: this.state.selectedCheckBoxIndex - 1});
                }
                break;
            default:
                break;
        }
    };
}

const OptionShape = PropTypes.shape({
    label: PropTypes.string.isRequired,
    value: PropTypes.string.isRequired
});

CheckBoxGroup.propTypes = {
    id: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
    values: PropTypes.arrayOf(PropTypes.string),
    options: PropTypes.arrayOf(OptionShape),
    className: PropTypes.string,
    focused: PropTypes.bool,
    selectedOptions: PropTypes.arrayOf(PropTypes.string),
    translate: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
    onSetSelection: PropTypes.func.isRequired
};

export default CheckBoxGroup;