import React, {Component} from 'react';
import PropTypes from 'prop-types';

import './IconButton.css';

class IconButton extends Component {
    render() {
        let isActiveIcon = (this.props.disabled) ? 'icon-disabled': 'icon-active';
        let isActiveBtn= (this.props.disabled) ? null : this.props.onClick;
        let focused =(this.props.focused) ? ' icon-focused ': '';
        return (
            <div title={this.props.title}
                 role='button'
                 aria-label={this.props.title}
                 className={isActiveIcon + focused}
                 onClick={isActiveBtn}
            >
                <i className="material-icons">{this.props.icon}</i>
            </div>
        );
    }
}

IconButton.propTypes = {
    title: PropTypes.string.isRequired,
    disabled: PropTypes.bool.isRequired,
    onClick: PropTypes.func.isRequired
};

export default IconButton;
