import {connect} from 'react-redux';
import {getTranslate} from 'react-localize-redux';
import {exportProcessPlan, getProcessPlanJson, isProcessPlanLoaded,showProcessPlanInfo, lockProcessPlan, getProcessPlanNameValidationTooltip
} from "../../redux/modules/processPlan";
import {isAvailableObjectsLoaded} from "../../redux/modules/availableObjects";
import HeaderPanel from "./HeaderPanel";
import {applyChange} from "../../redux/modules/changeStack";
import {ChangeProcessPlanName} from "../../redux/model/ChangeProcessPlanName";
import {setPasswordFlag} from "../../redux/modules/processPlan";
import {nativeApiAvailable} from '../../api';
import {endTransitionCreation, getTempTransition} from "../../redux/modules/tempTransition";
import _ from 'lodash';

const mapStateToProps = (state) => {
    let headerPanel = state.processPlan;
    return {
        name: headerPanel.name,
        locked: headerPanel.locked,
        hasPassword: headerPanel.hasPassword,
        fetching: !(isProcessPlanLoaded(state) && isAvailableObjectsLoaded(state)),
        isHidden: headerPanel.isHidden,
        isExportAvailable: !nativeApiAvailable,
        isExistTempTransition: !_.isEmpty(getTempTransition(state)),
        modalText: getProcessPlanJson(state),
        validationTooltip: getProcessPlanNameValidationTooltip(state),
        translate: getTranslate(state.localize)
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        onChangeProcessPlanName: (name) => {
            dispatch(applyChange(new ChangeProcessPlanName(name)));
        },
        onChangeLockType: (lockedType) => {
            dispatch(lockProcessPlan(lockedType));
        },
        onChangePassword: (hasPassword) => {
            dispatch(setPasswordFlag(hasPassword));
        },
        isHideProcessPlanInfo: (type) => {
            dispatch(showProcessPlanInfo(type))
        },
        onProcessPlanExport: () => dispatch(exportProcessPlan()),
        destroyTempTransition : () => {dispatch(endTransitionCreation(null))}
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(HeaderPanel)