import {Change} from './Change';
import _ from 'lodash';

export class AddConnection extends Change {
    constructor(tempTransition) {
        super('Add connection');
        this.tempTransition = tempTransition;
    }

    applyAddTransitionConnectionLayout(transitions) {
        if (_.filter(transitions, transition => transition.startStepId === this.tempTransition.start.stepId).length){
            return _.map(transitions, connectionLayout => connectionLayout.startStepId === this.tempTransition.start.stepId ?
                {...connectionLayout, endStepId: this.tempTransition.end.stepId,  endPosition: this.tempTransition.end.position} : connectionLayout);
        }else{
           return  _.concat(transitions, {startStepId: this.tempTransition.start.stepId,endStepId: this.tempTransition.end.stepId,  endPosition: this.tempTransition.end.position});
        }
    }
    revertAddTransitionConnectionLayout(transitions) {
        return _.omitBy(transitions, connectionLayout => connectionLayout.startStepId === this.tempTransition.start.stepId);
    }

    applyAddConnectedStepProperty(properties){
        this.properties = properties;
        return _.map(properties, property => property.stepId === this.tempTransition.start.stepId && property.name === 'step_id' ? {
            ...property,
            value: this.tempTransition.end.stepId
        } : property);
    }
    revertAddConnectedStepProperty(properties){
        return this.properties ;
    }
}