import {Change} from './Change';
import _ from 'lodash';

export class MoveStepChange extends Change {
    constructor(stepId, startPosition, newPosition) {
        super('Move step');
        this.stepId = stepId;
        this.newPosition = newPosition;
        this.oldPosition = startPosition;
    }
    applyStepsLayout(stepsLayout) {
        return _.mapValues(stepsLayout, (stepLayout, id) => {
            if (id === this.stepId) {
                return {...stepLayout, position: this.newPosition};
            }
            return stepLayout;
        });
    }
    revertStepsLayout(stepsLayout) {
        return _.mapValues(stepsLayout, (stepLayout, id) => {
            if (id === this.stepId) {
                return {...stepLayout, position: this.oldPosition};
            }
            return stepLayout;
        });
    }
}