import React, {Component} from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';

import './ExternalObjectSelect.css';
import Select from '../select/Select';
import IconButton from './iconButton/IconButton';
import {
    translatedExternalAddingElement,
    translatedExternalDuplicatingElement,
    translatedExternalEditingElement
} from "../../../../dictionary"

class ExternalObjectSelect extends Component {
    render() {
        let disabled = _.isNil(this.props.value) || this.props.disabled;
        return (
            <div
                onClick={this.handleClick}>
                <div className='external-object-editor'>
                    <IconButton
                        icon="add"
                        title={this.props.translate(translatedExternalAddingElement[this.props.type])}
                        disabled={this.props.disabled}
                        onClick={this.props.onCreate}/>
                    <IconButton
                        icon="insert_drive_file"
                        title={this.props.translate(translatedExternalDuplicatingElement[this.props.type])}
                        disabled={disabled}
                        onClick={this.props.onDuplicate}/>
                    <IconButton
                        icon="create"
                        title={this.props.translate(translatedExternalEditingElement[this.props.type])}
                        disabled={disabled}
                        onClick={this.props.onEdit}/>
                </div>
                <Select
                    id={this.props.id}
                    className='property'
                    label={this.props.label}
                    value={this.props.value}
                    options={this.props.options}
                    disabled={this.props.disabled}
                    translate={this.props.translate}
                    onChange={this.props.onChange}
                    onSetSelection={this.props.onSetSelection}/>
            </div>
        );
    }

    handleClick = () => {
        this.props.onSetSelection();
    };
}

ExternalObjectSelect.propTypes = {
    id: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
    value: PropTypes.string,
    translate: PropTypes.func.isRequired,
    onCreate: PropTypes.func.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDuplicate: PropTypes.func.isRequired,
    onSetSelection: PropTypes.func.isRequired
};

export default ExternalObjectSelect;
