import {handleActions} from 'redux-actions';
import _ from 'lodash';
import {getAvailableObjectOptions, getAvailableStepOptions} from "./availableObjects";

export const SELECTABLE_PROPERTIES_NUMBER_BY_STEP_PROPERTY_NAME = {
    'checkbox': 1,
    'input': 1,
    'select': 1,
    'variable-input': 1
};

export const propertyEditControls = {
    omit_fixups: () => ({
        type: 'variable-checkbox',
        label: 'Do not apply Fixups'
    }),
    id: (state, step) => ({
        type: 'external-object-select',
        label: 'Name',
        options: getAvailableObjectOptions(state, step.type)
    }),
    step_id: (state, step) => ({
        type: 'select',
        label: 'Pick up PDF from',
        options: getAvailableStepOptions(state, step.id)
    }),
    suffix: () => ({
        type: 'variable-input',
        label: 'Suffix'
    }),
    prefix: () => ({
        type: 'variable-input',
        label: 'Prefix'
    }),
    folder: () => ({
        type: 'variable-input',
        label: 'Folder'
    }),
    overwrite_existing_file: () => ({
        type: 'variable-checkbox',
        label: 'Overwrite existing file'
    }),
    replace_file_name: () => ({
        type: 'variable-checkbox',
        label: 'Replace file name with prefix and suffix'
    }),
    enable_step: () => ({
        type: 'variable-checkbox',
        label: 'Enable step'
    }),
};

//- State
const initialState = [];

//- Reducer
export default handleActions({
    PROCESS_PLAN_LOAD: (state, action) => {
        return action.payload.stepProperties;
    },
    CHANGE_APPLY: (state, {payload: change} ) => {
        switch(change.description) {
            case 'Add step':
                return change.applyAddStepProperty(state);
            case 'Change step property':
                return change.applyStepPropertyChange(state);
            case 'Change transition position':
                return state;
            case 'Remove step':
                return change.applyRemoveStepProperty(state);
            case 'Add connection':
                return change.applyAddConnectedStepProperty(state);
            case 'Change connection position':
                return change.applyChangeConnectedStepProperty(state);
            case 'Remove connection':
                return change.applyRemoveConnectionProperty(state);
            case 'Insert new step into transition':
                return change.applyInsertStepProperty(state);
            case 'Generate auto layout':
                return change.applyProcessPlanLayoutAutoGenerationStepProperties(state);
            default:
                return state;
        }
    },
    CHANGE_REVERT: (state, {payload: change} ) => {
        switch(change.description) {
            case 'Add step':
                return change.revertAddStepProperty(state);
            case 'Change step property':
                return change.revertStepPropertyChange(state);
            case 'Change transition position':
                return state;
            case 'Remove step':
                return change.revertRemoveStepProperty(state);
            case 'Add connection':
                return change.revertAddConnectedStepProperty(state);
            case 'Change connection position':
                return change.revertChangeConnectedStepProperty(state);
            case 'Remove connection':
                return change.revertRemoveConnectionProperty(state);
            case 'Insert new step into transition':
                return change.revertInsertStepProperty(state);
            case 'Generate auto layout':
                return change.revertProcessPlanLayoutAutoGenerationStepProperties(state);
            default:
                return state;
        }
    }
}, initialState);

//- Selectors
export const getAllStepProperties = state => state.stepProperties;

export const getStepProperties = (state, stepId) => {
    return _.chain(getAllStepProperties(state))
        .filter(property => property.stepId === stepId)
        .map(property => ({
            stepId: stepId,
            name: property.name,
            value: property.value
        }))
        .value();
};

export const getStepPropertyByName = (state, stepId, propertyName) =>
    _.find(getStepProperties(state, stepId),property => property.name === propertyName);

export const getStepAvailabilityById = (state, stepId) => {
    let enableStepProperty = getStepPropertyByName(state, stepId, 'enable_step');
    return _.isNil(enableStepProperty) ? true : enableStepProperty.value;
};
