import React, {Component} from 'react';
import PropTypes from 'prop-types';

import './VariableItem.css';
import {getVariableDisplayText} from "../../../../../../redux/modules/availableObjects";
import IconButton from "../../../ExternalObjectSelect/iconButton/IconButton";

const ICONS = ['select', 'duplicate', 'edit'];

class VariableItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedElementIndexIcon: 0
        };
    }

    setVariableItem = (variableItem) => {
        this.variableItem = variableItem;
    };

    render() {
        let className = `variable-item  ${(this.props.focused ? 'selected-variable-item' : '')}`;
        let variableText = getVariableDisplayText(this.props.variable);
        return (
            <li ref={this.setVariableItem}
                key={this.props.variable.id}
                className={className}
                tabIndex="0"
                onKeyDown={this.handleMenuKeyDown}>
                <div className='variable-value'
                     title={variableText}
                     onClick={this.handleClick}>
                    {variableText}
                </div>
                <div className='variable-menu'>
                    <IconButton
                        icon="insert_drive_file"
                        title={this.props.translate("NG_PPE_VariableDuplicate_long")}
                        disabled={false}
                        focused={ICONS[this.state.selectedElementIndexIcon] === 'duplicate' && this.props.focused}
                        onClick={this.props.onDuplicate}/>
                    <IconButton
                        icon="create"
                        title={this.props.translate("NG_PPE_VariableEdit_long")}
                        disabled={false}
                        focused={ICONS[this.state.selectedElementIndexIcon] === 'edit' && this.props.focused}
                        onClick={this.props.onEdit}/>
                </div>
            </li>
        );
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.variableItem.focus();
        }
    }

    handleClick = () => {
        this.props.onSetVariable(this.props.variable, this.props.position, this.props.inputValue, this.props.inputType);
    };

    handleMenuKeyDown = (e) => {
        switch (e.key) {
            case 'ArrowRight':
                if (this.state.selectedElementIndexIcon < ICONS.length - 1) {
                    this.setState({...this.state, selectedElementIndexIcon: this.state.selectedElementIndexIcon + 1});
                }
                break;
            case 'ArrowLeft':
                if (this.state.selectedElementIndexIcon > 0) {
                    this.setState({...this.state, selectedElementIndexIcon: this.state.selectedElementIndexIcon - 1});
                }
                break;
            case 'Enter':
                switch (ICONS[this.state.selectedElementIndexIcon]) {
                    case 'duplicate':
                        this.props.onDuplicate();
                        break;
                    case 'edit':
                        this.props.onEdit();
                        break;
                    case 'select':
                        this.props.onSetVariable(this.props.variable, this.props.position, this.props.inputValue, this.props.inputType);
                        break;
                    default:
                        break;
                }
                this.setState({...this.state, selectedElementIndexIcon: 0});
                break;
            default:
                break;

        }
    };
}

VariableItem.propTypes = {
    variables: PropTypes.array,
    onVariableAdd: PropTypes.func,
    isActive: PropTypes.bool,
    focused: PropTypes.bool,
    position: PropTypes.number,
    inputValue: PropTypes.string,
    inputType: PropTypes.string,
    translate: PropTypes.func.isRequired,
    onSetVariable: PropTypes.func,
    onDuplicate: PropTypes.func.isRequired,
    onEdit: PropTypes.func.isRequired
};

export default VariableItem;
