import React from 'react';
import ReactDOM from 'react-dom';
import {createStore, applyMiddleware} from 'redux';
import {Provider} from 'react-redux';
import thunk from 'redux-thunk';
import promise from 'redux-promise';
import {createEpicMiddleware} from 'redux-observable';
import {polyfill as keyboardEventKeyPolyfill } from 'keyboardevent-key-polyfill';
import {polyfill} from 'mobile-drag-drop';
import {unregister} from './registerServiceWorker';
import {LocalizeProvider} from "react-localize-redux";
import './fonts/fonts.css';

import 'bootstrap/dist/css/bootstrap.css';
import './index.css';

import App from './components/App.container';

import reducer from './redux/modules';
import {rootEpic} from './redux/modules/index';
import {loadProcessPlan} from './redux/modules/processPlan';
import {loadViewSettings} from './redux/modules/drawingArea';
import {loadAvailableObjects} from './redux/modules/availableObjects';

keyboardEventKeyPolyfill();

// Init store
let epicMiddleware = createEpicMiddleware();
let store = createStore(reducer, applyMiddleware(thunk, promise,epicMiddleware));
epicMiddleware.run(rootEpic);
window.store = store;

// Load available objects
store.dispatch(loadAvailableObjects());

// Load process plan
let sampleName;

let url = window.location.search;
let sampleParam = url.split(/[?&]+/).find(param => /sample=.*/.test(param));
if (sampleParam) {
    sampleName = sampleParam.substring('sample='.length);
}

store.dispatch(loadProcessPlan(sampleName));

// Load view settings
store.dispatch(loadViewSettings());

// Render application
ReactDOM.render(
    <Provider store={store}>
       <LocalizeProvider store={store}>
          <App/>
       </LocalizeProvider>
    </Provider>,
    document.getElementById('root')
);

unregister();
polyfill();
