import {Change} from './Change';

export class ChangeProcessPlanAuthor extends Change {
    constructor(author) {
        super('Change process plan author');
        this.author = author;
        this.prevAuthor = null;
    }

    applyProcessPlanAuthorChange(processPlan) {
        this.prevAuthor = processPlan.author;
        return {...processPlan, author: this.author};
    }
    revertProcessPlanAuthorChange(processPlan) {
        return {...processPlan, author: this.prevAuthor};
    }
}