import {connect} from 'react-redux'
import Transition from './Transition'

import {calculateTransitionPoint} from "../../../redux/modules/transitionsLayout";
import {calculatePosition, endTransitionCreation} from "../../../redux/modules/tempTransition";
import {getStepLayoutById} from "../../../redux/modules/stepsLayout";
import {calculateTransitionPoints} from "./Transition.container";
import {RECT_OFFSET} from "../step/Step";
import {
    getActionType,
    UPDATING_TRANSITION_START_POINT
} from "../../../redux/modules/transitionActionType";
import {
    changeTempTransitionPositionByShortcut, endTransitionCreationByShortcut,
    getScale, getSvgPosition, updateTempTransitionByShortcut
} from "../../../redux/modules/drawingArea";
import {getTranslate} from 'react-localize-redux';

export const mapStateToProps = (state) => {
    let tempTransition = state.tempTransition;
    let actionType = getActionType(state);

    if (tempTransition) {
        let endPosition = tempTransition.end.position || calculatePosition(tempTransition.start.x, tempTransition.start.y, tempTransition.end.x, tempTransition.end.y);
        let startPosition = tempTransition.start.position || calculatePosition(tempTransition.end.x, tempTransition.end.y, tempTransition.start.x, tempTransition.start.y);

        let startStepLayout = !(actionType === UPDATING_TRANSITION_START_POINT) ? getStepLayoutById(state, tempTransition.start.stepId) : {
            position: {x: tempTransition.start.x - 2 * RECT_OFFSET, y: tempTransition.start.y - RECT_OFFSET},
            size: {width: 0, height: 0}
        };

        let endStepLayout = {
            position: {x: tempTransition.end.x - 2 * RECT_OFFSET, y: tempTransition.end.y - RECT_OFFSET},
            size: {width: 0, height: 0}
        };

        let tempTransitionLayout = {
            start: {position: startPosition},
            end: {position: endPosition}
        };

        return {
            points: calculateTransitionPoints(startStepLayout, endStepLayout, tempTransitionLayout),
            className: '',
            start: calculateTransitionPoint(startStepLayout, startPosition),
            end: calculateTransitionPoint(endStepLayout, endPosition),
            arrowType: endPosition,
            selected: true,
            selectable: true,
            focused: true,
            scale: getScale(state),
            svgPosition: getSvgPosition(state),
            highlighted: false,
            showTransitionInfo: false,
            translate: getTranslate(state.localize)
        }
    }
    return {};
};

const mapDispatchToProps = (dispatch) => {
    return {
        onArrowKeyDown: (direction) => {
            if (direction === 'right' || direction === 'left') {
                dispatch(updateTempTransitionByShortcut(direction));
            } else {
                dispatch(changeTempTransitionPositionByShortcut(direction));
            }
        },
        onRemoveTransition: () => {
            dispatch(endTransitionCreation(null));
        },
        onEnterKeyDown: () => {
            dispatch(endTransitionCreationByShortcut());
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Transition)