import {connect} from 'react-redux';
import StepButton from "./StepButton";
import {dropStep, selectStep} from "../../../../redux/modules/steps";
import uuidv4 from "uuid/v4";
import {selectDrawingArea} from "../../../../redux/modules/drawingArea";
import {getTranslate} from 'react-localize-redux';

const mapStateToProps = (state) => {
    return {
        originalViewportSize: state.drawingArea.originalViewportSize,
        originalViewportPosition: state.drawingArea.originalViewportPosition,
        translate: getTranslate(state.localize)
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        onMobileDrop: (type, position) => {
            let id = uuidv4();
            dispatch(dropStep(id, type, position));
            dispatch(selectDrawingArea('clicked'));
            dispatch(selectStep(id));
        }
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(StepButton);