import {createAction, handleActions} from 'redux-actions';
import {createSelector} from "reselect";
import {ofType} from 'redux-observable';
import {from} from 'rxjs';
import {concatMap, switchMap} from 'rxjs/operators';
import _ from 'lodash'
import api from "../../api";
import {getSteps, getStepTypeById} from './steps';
import {applyChange} from './changeStack';
import {ChangeStepProperty} from '../model/ChangeStepProperty';
import {getSelectedStepId} from './drawingArea';
import {propertiesByStepType} from '../../api/processPlanConversion';
import {translatedStepTypes} from "../../dictionary";
import {getTranslate} from 'react-localize-redux';

const BROKEN_REFERENCE_TO_AVAILABLE_OBJECTS = 'NG_PPE_Broken_reference_to_available_object_long';

export const VARIABLE_SCRIPT_DEFAULT_VALUE = '0';

export const VariableTypes = {
    VARIABLE: 'variable',
    SCRIPT: 'script',
    QCSCRIPT: 'qcscript'
};

const stepTypeToObjectType = {
    'processplan': 'processplans',
    'profile': 'profiles',
    'action': 'actions',
    'variable': 'variables',
    'fixup': 'fixups',
    'check': 'checks',
    'quickcheck': 'variables',
    'addfiles': 'variables'
};

//- Actions
export const loadAvailableObjects = createAction("AVAILABLE_OBJECTS_LOAD", () => api.loadObjects());
export const setAvailableObjects = createAction("AVAILABLE_OBJECTS_LOAD", (objects) => objects);
export const createExternalObject = createAction("AVAILABLE_OBJECTS_CREATE", (type) => api.createExternalObject(type), (type, onSuccess) => ({onSuccess}));
export const duplicateExternalObject = createAction("AVAILABLE_OBJECTS_DUPLICATE", (type, id) => api.duplicateExternalObject(type, id), (type, id, onSuccess) => ({onSuccess}));
export const editExternalObject = createAction("AVAILABLE_OBJECTS_EDIT", (type, id) => api.updateExternalObject(type, id), (type, id, onSuccess) => ({id, onSuccess}));

//- State
const initialState = {
    processplans: [],
    profiles: [],
    actions: [],
    variables: [],
    fixups: [],
    checks: [],
    quickchecks: [],
    groups: [],
    reportTemplates: [],
    loaded: false
};

//- Reducer
export default handleActions({
    AVAILABLE_OBJECTS_LOAD: (state, action) => {
        return {...action.payload, loaded: true};
    }
}, initialState);

//- Epics
export const handleExternalObjectsUpdateEpic = (action$, state$) =>
    action$.pipe(
        ofType(
            createExternalObject.toString(),
            duplicateExternalObject.toString(),
            editExternalObject.toString()
        ),
        switchMap(({payload: newObjectId, meta}) =>
            from(api.loadObjects()).pipe(                      // re-load available objects,
                concatMap((updatedObjects) => {
                    const resultActions = [
                        setAvailableObjects(updatedObjects),   // ... set them to redux state,
                    ];
                    if (!_.isNil(newObjectId)) {               // ... and finally select created object if needed.
                        if (!_.isNil(meta) && _.isFunction(meta.onSuccess)) {
                            resultActions.push(() => meta.onSuccess(_.find(updatedObjects.variables, ['id', newObjectId])));
                        } else {
                            resultActions.push(applyChange(new ChangeStepProperty(getSelectedStepId(state$.value), 'id', newObjectId)));
                        }
                    } else {
                        resultActions.push(() => meta.onSuccess(_.find(updatedObjects.variables, ['id', meta.id])));
                    }
                    return resultActions;
                })
            )
        )
    );

//- Selectors
export const getAvailableObjects = state => state.availableObjects;
export const isAvailableObjectsLoaded = state => getAvailableObjects(state).loaded;

export const getAvailableObjectsByStepType = (state, stepType) => {
    let objects = getAvailableObjects(state)[stepTypeToObjectType[stepType]];
    switch (stepType) {
        case 'variable':
        case 'addfiles':
            return _.filter(objects, variable => variable.type !== VariableTypes.QCSCRIPT);
        case 'quickcheck':
            return _.filter(objects, variable => variable.type === VariableTypes.QCSCRIPT);
        default:
            return objects;
    }
};

export const getReferencedObjectByStepId = (state, stepId) => {
    let stepProperty = _.find(state.stepProperties, property => property.name === 'id' && property.stepId === stepId);
    let stepType = getStepTypeById(state, stepId);
    return _.find(getAvailableObjectsByStepType(state, stepType), object => object.id === stepProperty.value);
};

export const getStepText = (state, stepId) => {
    let translate = getTranslate(state.localize);
    let stepType = getStepTypeById(state, stepId);
    let referencedObject = getReferencedObjectByStepId(state, stepId);
    if (_.isNil(referencedObject)) {
        return translate(translatedStepTypes[stepType]);
    }
    return getObjectText(stepType, referencedObject);
};

export const getAvailableObjectOptions = (state, selectedStepType) => {
    return _.map(getAvailableObjectsByStepType(state, selectedStepType), object => ({
        value: object.id,
        label: getObjectText(selectedStepType, object)
    }));
};

export const getAvailableStepOptions = (state, selectedStepId) => {
    return _.chain(getSteps(state))
            .omit(selectedStepId)
            .map((step, id) => ({
                label: getStepText(state, id),
                value: id
            }))
            .value();
};

export const getStepAvailableObjectReferenceValidationTooltip = (state, stepId) => {
    let translate = getTranslate(state.localize);
    let stepType = getStepTypeById(state, stepId);
    return !_.isNil(stepType)
         && _.includes(propertiesByStepType[stepType], 'id')
         && _.isNil(getReferencedObjectByStepId(state, stepId)) ? [translate(BROKEN_REFERENCE_TO_AVAILABLE_OBJECTS)] : [];
};

export const getAvailableGroupOptions = createSelector(
    getAvailableObjects,
    availableObjects => _.map(availableObjects.groups, group => ({label: group.name, value: group.id}))
);

export const getAvailableDialogOptions = createSelector(
    getAvailableObjects,
    availableObjects => _.map(availableObjects.dialogs, dialog => ({label: dialog.name, value: dialog.id}))
);

export const getVariables = createSelector(
    getAvailableObjects,
    availableObjects => _.filter(availableObjects.variables, variable => variable.type !== VariableTypes.QCSCRIPT)
);

export const getVariableById = (state, varId) => _.find(getAvailableObjects(state).variables, ['id', varId]);

export const getVariableDisplayText = variable => {
    switch (variable.type) {
        case VariableTypes.VARIABLE:
            return `<${variable.key}:${variable.value}>`;
        case VariableTypes.SCRIPT:
            return `{${variable.key}}`;
        case VariableTypes.QCSCRIPT:
            return `[${variable.key}]`;
        default:
            console.warn(`Unknown variable type ${variable.type}`);
            return variable.key;
    }
};

export const getObjectText = (type, object) =>
    _.includes(['variable', 'addfiles', 'quickcheck'], type) ? getVariableDisplayText(object) : object.name;

export const getReportTemplatesOptions = createSelector(
    getAvailableObjects,
    availableObjects => _.map(availableObjects.reportTemplates, reportTemplate => ({label: reportTemplate.name, value: reportTemplate.id}))
);
export const getProcessPlanAvailableObjects = createSelector(
    getAvailableObjects,
    availableObjects => availableObjects.processplans
);
