import {connect} from 'react-redux';
import ProcessPlanDetails from "./ProcessPlanDetails";
import {applyChange} from "../../../redux/modules/changeStack";
import {ChangeProcessPlanAuthor} from "../../../redux/model/ChangeProcessPlanAuthor";
import {ChangeProcessPlanComment} from "../../../redux/model/ChangeProcessPlanComment";
import {ChangeProcessPlanEmail} from "../../../redux/model/ChangeProcessPlanEmail";
import {ChangeProcessPlanGroup} from "../../../redux/model/ChangeProcessPlanGroup";
import {ChangeProcessPlanDialog} from "../../../redux/model/ChangeProcessPlanDialog";
import {getAvailableGroupOptions} from "../../../redux/modules/availableObjects";
import {getAvailableDialogOptions} from "../../../redux/modules/availableObjects";
import {getTranslate} from 'react-localize-redux';

const mapStateToProps = (state) => {
    let headerPanel = state.processPlan;
    return {
        group: headerPanel.group,
        groups: getAvailableGroupOptions(state),
        dialog: headerPanel.dialog,
        dialogs: getAvailableDialogOptions(state),
        author: headerPanel.author,
        email: headerPanel.email,
        comment: headerPanel.comment,
        translate: getTranslate(state.localize)
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        onChangeAuthor: (author) => {
            dispatch(applyChange(new ChangeProcessPlanAuthor(author)));
        },
        onChangeEmail: (email) => {
            dispatch(applyChange(new ChangeProcessPlanEmail(email)));
        },
        onChangeComment: (comment) => {
            dispatch(applyChange(new ChangeProcessPlanComment(comment)));
        },
        onChangeGroup: (group) => {
            dispatch(applyChange(new ChangeProcessPlanGroup(group)))
        },
        onChangeDialog: (dialog) => {
            dispatch(applyChange(new ChangeProcessPlanDialog(dialog)))
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ProcessPlanDetails)
