import {handleActions} from 'redux-actions';
import _ from 'lodash';

//- State
const initialState = {};

//- Reducers
export default handleActions({
    PROCESS_PLAN_LOAD: (state, action) => {
        return _.chain(action.payload.stepProperties)
            .filter(property => property.name === 'step_id')
            .map(property => ({startStepId: property.stepId, endStepId: property.value}))
            .map(connection => ({
                ...connection,
                endPosition: null}))
            .value();
    },
    CHANGE_APPLY: (state, {payload: change} ) => {
        switch(change.description) {
            case 'Remove step':
                return change.applyRemoveStepConnectionLayout(state);
            case 'Add step':
                return change.applyAddStepConnectionLayout(state);
            case 'Add connection':
                return change.applyAddTransitionConnectionLayout(state);
            case 'Change connection position':
                return change.applyConnectionLayoutPositionChange(state);
            case 'Remove connection':
                return change.applyRemoveConnection(state);
            case 'Insert new step into transition':
                return change.applyInsertStepConnectionLayout(state);
            case 'Generate auto layout':
                return change.applyLayoutAutoGenerationForConnections(state);
            case 'Change step property':
                return change.applyUpdateConnection(state);
            default:
                return state;
        }
    },
    CHANGE_REVERT: (state, {payload: change} ) => {
        switch(change.description) {
            case 'Remove step':
                return change.revertRemoveStepConnectionLayout(state);
            case 'Add step':
                return change.revertAddStepConnectionLayout(state);
            case 'Add connection':
                return change.revertAddTransitionConnectionLayout(state);
            case 'Change connection position':
                return change.revertConnectionLayoutPositionChange(state);
            case 'Remove connection':
                return change.revertRemoveConnection(state);
            case 'Insert new step into transition':
                return change.revertInsertStepConnectionLayout(state);
            case 'Generate auto layout':
                return change.revertLayoutAutoGenerationForConnections(state);
            case 'Change step property':
                return change.revertUpdateConnection(state);
            default:
                return state;
        }
    }
}, initialState);

//- Selectors
export const getConnectionsLayout = state => state.connectionsLayout;

export const getConnectionLayout = (state, connection) =>
    _.find(getConnectionsLayout(state), connectionLayout => connection.startStepId === connectionLayout.startStepId && connection.endStepId === connectionLayout.endStepId);

export const getConnectionEndPosition = (state, connection) => {
    let connectionLayout = getConnectionLayout(state, connection);
    return _.isNil(connectionLayout)? null: connectionLayout.endPosition;
};

export const isLeftTopAngleVisible = (state, stepId) => {
    return !_.isUndefined(_.find(getConnectionsLayout(state), (connection) => connection.endStepId === stepId));
};