import {createAction, handleActions} from 'redux-actions';
import {createSelector} from 'reselect';
import {isProcessPlanLoaded} from './processPlan';
import _ from 'lodash';
import {RECT_OFFSET} from "../../components/drawingArea/step/Step";
import {applyChange} from './changeStack';
import {AddStepChange} from '../model/AddStep';
import {InsertNewStepIntoTransition} from '../model/InsertNewStepIntoTransition';
import {
    getIdOfIntersectedTransition,
    getSelectedTransition,
    getTransitionsByEndStepId,
    getTransitionsByStartStepId
} from "./transitions";
import {availableTransitionsByStepType} from "../../api/processPlanConversion";
import {getTransitionTypesByStartStepId} from "./transitionProperties";
import {getStepAvailableObjectReferenceValidationTooltip} from "./availableObjects";
import {getStepAvailabilityById} from './stepProperties';
import {getTranslate} from 'react-localize-redux';
import {translatedNativeTransitionTypes} from '../../dictionary';
import {getScale, getSnapToGrid, getSvgPosition} from '../modules/drawingArea';
import {AddTransitionForInsertStep} from '../model/AddTransitionForInsertStep';
import {RemoveStepChange} from "../model/RemoveStep";

const NO_INCOMING_TRANSITIONS = 'NG_PPE_No_incoming_transition_long';
const NO_TRANSITIONS_FROM_START = 'NG_PPE_No_transition_from_start_long';
const NO_TRANSITIONS_TO_END = 'NG_PPE_No_transition_to_end_long';
const NOT_ALL_POSSIBLE_OUTGOING_TRANSITIONS_TYPES = 'NG_PPE_NOT_ALL_POSSIBLE_OUTGOING_TRANSITIONS_TYPES_long';

//- Actions
export const selectStep = createAction('STEP_SELECT', id => id);
export const highlightStep = createAction('STEP_HIGHLIGHT', id => id);
export const dropStep = (id, type, position) => (dispatch, getState) => {
    dispatch(applyChange(new AddStepChange(id, type, position, getState().drawingArea.scale,
        getState().drawingArea.viewportPosition, getState().drawingArea.snapToGrid)));
};
export const dropStepIntoTransition = (stepId, type, position, transitionId, newTransitionId) => (dispatch, getState) => {
    dispatch(applyChange(new InsertNewStepIntoTransition(stepId, type, position, transitionId, newTransitionId, getScale(getState()),
        getSvgPosition(getState()), getSnapToGrid(getState()))))
};
export const dropExistingStepIntoTransition = (stepId, newTransitionId) => (dispatch, getState) => {
    dispatch(applyChange(new AddTransitionForInsertStep(stepId, getStepTypeById(getState(), stepId),
        getIdOfIntersectedTransition(getState()), newTransitionId)))
};
export const removeStep = (id) => (dispatch, getState) => {
    dispatch(applyChange(new RemoveStepChange(id, getState().transitionProperties, getState().transitionsLayout, getState().steps)));
};

export const checkIsAnchorsIntersectedByTransition = createAction("ANCHORS_INTERSECT", (id, transitions, transitionsLayout) => ({
    id,
    transitions,
    transitionsLayout
}));
export const checkIsAnchorsImposed = (id) => (dispatch, getState) => {
    dispatch(checkIsAnchorsIntersectedByTransition(id, getState().transitions, getState().transitionsLayout))
};

//- State
const initialState = {};

//- Reducers
export default handleActions({
    PROCESS_PLAN_LOAD: (state, action) => {
        return action.payload.steps;
    },
    STEP_SELECT: (state, action) => {
        if (_.isNil(action.payload) || action.payload === 'start') {
            return _.mapValues(state, step => ({...step, selected: false}));
        }
        if (state[action.payload].selected) {
            return state;
        }

        return _.mapValues(state, (step, id) => {
            if (id === action.payload) {
                return {...step, selected: true};
            } else {
                return step.selected ? {...step, selected: false} : step;
            }
        });
    },
    STEP_HIGHLIGHT: (state, action) => {
        if (_.isNil(action.payload)) {
            return _.mapValues(state, step => ({...step, highlighted: false}));
        }
        return _.mapValues(state, step => step.id === action.payload ? {...step, highlighted: true} : step);
    },
    CHANGE_APPLY: (state, {payload: change}) => {
        switch (change.description) {
            case 'Remove step':
                return change.applyRemoveStep(state);
            case 'Add step':
                return change.applyAddStep(state);
            case 'Generate auto layout':
                return change.applyLayoutAutoGenerationForSteps(state);
            case 'Insert new step into transition':
                return change.applyInsertStep(state);
            case 'Create transitions for insert step':
                return change.applyInsertStepForAnchors(state);
            default:
                return state;
        }
    },
    CHANGE_REVERT: (state, {payload: change}) => {
        switch (change.description) {
            case 'Remove step':
                return change.revertRemoveStep(state);
            case 'Add step':
                return change.revertAddStep(state);
            case 'Generate auto layout':
                return change.revertLayoutAutoGenerationForSteps(state);
            case 'Insert new step into transition':
                return change.revertInsertStep(state);
            case 'Create transitions for insert step':
                return change.revertInsertStepForAnchors(state);
            default:
                return state;
        }
    },
    ANCHORS_INTERSECT: (state, action) => {
        let imposedTransition = _.find(action.payload.transitions, transition => transition.intersected);
        let imposedDirection = imposedTransition ? _.find(action.payload.transitionsLayout, (layout, transitionId) => transitionId === imposedTransition.id) : false;
        return _.mapValues(state, step => {
            if (step.id === action.payload.id) {
                return imposedDirection ?
                    {
                        ...step,
                        intersectedAnchors: [imposedDirection.start.position, imposedDirection.end.position]
                    } :
                    {
                        ...step,
                        intersectedAnchors: []
                    }
            } else return step
        })
    }
}, initialState);

//- Selectors
export const getSteps = state => state.steps;

export const getAllStepIds = createSelector(
    isProcessPlanLoaded,
    getSteps,
    (loaded, steps) => loaded ? ['start', ...Object.keys(steps), 'end'] : []
);

export const getAllFilePickupStepIds = createSelector(
    isProcessPlanLoaded,
    getSteps,
    (loaded, steps) => loaded ? _.chain(steps).filter(step => step.type === 'filepickup').map(step => step.id).value() : []
);

export const getSelectedStep = createSelector(
    getSteps,
    steps => _.find(steps, step => step.selected === true)
);

export const getStepById = (state, stepId) => {
    return _.find(getSteps(state), step => step.id === stepId);
};

export const getStepTypeById = (state, stepId) => {
    let step = getStepById(state, stepId);
    return _.isNil(step) ? null : step.type;
};

export const isStepHighlighted = (state, id) => {
    let selectedTransition = getSelectedTransition(state);
    return !_.isNil(selectedTransition) && (selectedTransition.startStepId === id || selectedTransition.endStepId === id);
};

export const getRequiredTransitions = (state, stepId) => {
    let stepAvailability = getStepAvailabilityById(state, stepId);
    if (stepAvailability === true) {
        // Step enabled => require all meaningful step transition
        return _.without(availableTransitionsByStepType[getStepTypeById(state, stepId)], 'if_disabled');
    } else if (stepAvailability === false) {
        // Step disabled => require if_disabled transition only
        return ['if_disabled'];
    } else {
        // Step availability is defined by variable => require all transitions
        return availableTransitionsByStepType[getStepTypeById(state, stepId)];
    }
};

export const getStepValidationTooltips = (state, id) => {
    let translate = getTranslate(state.localize);
    switch (id) {
        case 'start':
            return _.isEmpty(getTransitionsByStartStepId(state, id)) ? [translate(NO_TRANSITIONS_FROM_START)] : [];
        case 'end':
            return _.isEmpty(getTransitionsByEndStepId(state, id)) ? [translate(NO_TRANSITIONS_TO_END)] : [];
        default:
            let tooltips = [];
            if (_.isEmpty(getTransitionsByEndStepId(state, id))) {
                tooltips.push(translate(NO_INCOMING_TRANSITIONS));
            }

            let requiredTransitionTypes = getRequiredTransitions(state, id);
            let definedTransitionTypes = getTransitionTypesByStartStepId(state, id);
            if (_.difference(requiredTransitionTypes, definedTransitionTypes).length !== 0) {
                let translatedTypes = _.chain(requiredTransitionTypes).map(type => translate(translatedNativeTransitionTypes[type])).join(', ').value();
                tooltips.push(translate(NOT_ALL_POSSIBLE_OUTGOING_TRANSITIONS_TYPES, {events: translatedTypes}));
            }
            return tooltips;
    }
};

export const getAllStepsValidationTooltips = createSelector(
    state => state,
    getAllStepIds,
    (state, allStepIds) => _.chain(allStepIds)
        .map(id => [id, [...getStepValidationTooltips(state, id), ...getStepAvailableObjectReferenceValidationTooltip(state, id)]])
        .fromPairs()
        .value()
);

//- Utils
export const calculateAnchorCoordinatesByPosition = (position, stepLayout) => {
    switch (position) {
        case 'top':
            return {
                x: stepLayout.position.x + stepLayout.size.width / 2 + 2 * RECT_OFFSET,
                y: stepLayout.position.y + RECT_OFFSET
            };
        case 'right':
            return {
                x: stepLayout.position.x + stepLayout.size.width + RECT_OFFSET,
                y: stepLayout.position.y + stepLayout.size.height / 2 + RECT_OFFSET
            };
        case 'bottom':
            return {
                x: stepLayout.position.x + stepLayout.size.width / 2 + 2 * RECT_OFFSET,
                y: stepLayout.position.y + stepLayout.size.height + RECT_OFFSET
            };
        case 'left':
            return {
                x: stepLayout.position.x + RECT_OFFSET,
                y: stepLayout.position.y + stepLayout.size.height / 2 + RECT_OFFSET
            };
        default:
            return null;
    }
};