import _ from 'lodash';
import uuidv4 from 'uuid/v4';
import {getAllStepsValidationTooltips, getStepTypeById} from "../redux/modules/steps";
import {getAllTransitionsPropertiesValidationTooltips} from "../redux/modules/transitionProperties";
import {getProcessPlanNameValidationTooltip} from "../redux/modules/processPlan";

const ON_ERROR = "on_error";
const ON_SUCCESS = "on_success";
const ON_WARNING = "on_warning";
const ON_FAILURE = "on_failure";
const ON_INFO = "on_info";
const ON_HIT = "on_hit";
const ELSE = "else";
const OUT = "out";
const IF_DISABLED = "if_disabled";

export const STEP_TYPES = {
    PROCESS_PLAN: 'processplan',
    PROFILE: 'profile',
    VARIABLE: 'variable',
    CHECK: 'check',
    ACTION: 'action',
    FIXUP: 'fixup',
    PDF_COPY: 'pdfcopy',
    QUICKCHECK: 'quickcheck',
    ADDFILES: 'addfiles',
    FILE_PICK_UP: 'filepickup',
    RENAME: 'rename'
};

export const availableTransitionsByStepType = {
    'processplan': [ON_ERROR, ELSE, IF_DISABLED], //replaced ON_SUCCESS with ON_ERROR
    'profile': [ON_ERROR, ON_WARNING, ON_INFO, ON_SUCCESS, IF_DISABLED],
    'check': [ON_HIT, ON_SUCCESS, IF_DISABLED],
    'fixup': [ON_SUCCESS, ON_FAILURE, IF_DISABLED],
    'action': [ON_SUCCESS, ON_FAILURE, IF_DISABLED],
    'variable': [ON_SUCCESS, ON_FAILURE, IF_DISABLED],
    'filepickup': [ON_SUCCESS, ON_FAILURE, IF_DISABLED],
    'pdfcopy': [ON_SUCCESS, ON_FAILURE, IF_DISABLED],
    'start': [OUT],
    'quickcheck': [ON_SUCCESS, ON_FAILURE, IF_DISABLED],
    'addfiles': [ON_SUCCESS, ON_FAILURE, IF_DISABLED],
    'rename': [ON_SUCCESS, ON_FAILURE, IF_DISABLED],
};

export const propertiesByStepType = {
    'processplan': ['id', 'enable_step'],
    'profile': ['id', 'omit_fixups', 'enable_step'],
    'action': ['id', 'enable_step'],
    'variable': ['id', 'enable_step'],
    'fixup': ['id', 'enable_step'],
    'check': ['id', 'enable_step'],
    'filepickup': ['index', 'enable_step'],
    'pdfcopy': ['prefix', 'suffix', 'folder', 'overwrite_existing_file', 'replace_file_name', 'enable_step'],
    'quickcheck': ['id', 'enable_step'],
    'addfiles': ['id', 'enable_step'],
    'rename': ['prefix', 'suffix', 'overwrite_existing_file', 'replace_file_name', 'enable_step'],
};

export const defaultPropertyValues = {
    'enable_step': true
};

const booleanProperties = ['omit_fixups', 'overwrite_existing_file', 'replace_file_name', 'enable_step'];

export const postProcessingOptionsByStartStepType = {
    'processplan': [],
    'profile': ['certify', 'create_report'],
    'action': ['create_report'],
    'variable': ['create_report'],
    'fixup': ['create_report'],
    'check': ['create_report'],
    'filepickup': [],
    'pdfcopy': [],
    'quickcheck': [],
    'addfiles': [],
    'rename': [],
};

export const availableReportsByStartStepType = {
    'processplan': [],
    'profile': ['txt_report', 'xml_report', 'overview_report', 'mask_report', 'comment_report', 'layer_report', 'compare_report', 'inventory_report'],
    'check': ['txt_report', 'xml_report', 'overview_report', 'mask_report', 'comment_report', 'layer_report', 'compare_report', 'inventory_report'],
    'fixup': ['txt_report', 'xml_report', 'overview_report', 'mask_report', 'comment_report', 'layer_report', 'compare_report', 'inventory_report'],
    'action': ['compare_report', 'inventory_report'],
    'variable': ['compare_report', 'inventory_report'],
    'filepickup': [],
    'pdfcopy': [],
    'quickcheck': [],
    'addfiles': [],
    'rename': [],
};

export const generationOptions = {
    startPosition: {x: 10, y: 10},
    startSize: {width: 120, height: 40},
    offsets: {x: 250, y: 100},
    size: {width: 200, height: 40},
    columns: 4
};

export function generateStepsLayout(processPlan, options) {
    const stepsLayout = {};
    const {startPosition, startSize} = options;
    stepsLayout.start = {
        position: startPosition,
        size: startSize
    };

    let {offsets, size, columns} = options;
    let previousStepPosition = stepsLayout.start.position;
    let numOfSteps = processPlan.sequence.length;
    let direction = 1;
    for (let index = 0; index <= numOfSteps - 1; index++) {
        let position = {
            x: previousStepPosition.x + (direction * offsets.x),
            y: previousStepPosition.y + offsets.y
        };

        if (((index + 1) % (columns - 1)) === 0) {
            direction = -direction;
        }
        stepsLayout[index] = {position, size};
        previousStepPosition = stepsLayout[index].position;
    }

    let position = {
        x: previousStepPosition.x + (direction * offsets.x),
        y: previousStepPosition.y + offsets.y
    };
    stepsLayout['end'] = {
        position,
        size: startSize
    };
    return stepsLayout;
}

const generateTransitionLayout = (transitions) => {
    return _.chain(transitions)
        .groupBy('startStepId')
        .map((stepTransitions) =>
            _.map(stepTransitions, (transition, index) => {
                let transitionLayout = {
                    start: {},
                    end: {}
                };
                switch (index % 3) {
                    case 0:
                        transitionLayout.start.position = 'bottom';
                        transitionLayout.end.position = 'top';
                        break;
                    case 1:
                        transitionLayout.start.position = 'right';
                        transitionLayout.end.position = 'right';
                        break;
                    case 2:
                        transitionLayout.start.position = 'left';
                        transitionLayout.end.position = 'left';
                        break;
                    default:
                        break;
                }
                return {transitionId: transition.id, transitionLayout};
            })
        )
        .flatten()
        .keyBy(({transitionId}) => transitionId)
        .mapValues(({transitionLayout}) => transitionLayout)
        .value();
};

export const convertProcessPlan = (processPlan) => {
    if (!processPlan.layout) {
        processPlan.layout = {
            stepsLayout: generateStepsLayout(processPlan, generationOptions)
        };
    }
    let steps = {};
    let transitions = {};
    let stepsLayout = {};
    let transitionsLayout = {};
    let stepProperties = [];
    let transitionProperties = [];

    let stepIds = [];

    let baseInfo = {
        id: processPlan.id,
        name: processPlan.name,
        author: processPlan.author,
        comment: processPlan.comment,
        email: processPlan.email,
        locked: processPlan.locked,
        hasPassword: processPlan.hasPassword,
        group: processPlan.group,
        dialog: processPlan.dialog,
        addFilesStep: processPlan.addFilesStep
    };

    // Build steps & stepProperties
    processPlan.sequence.forEach(step => {
        let stepId = uuidv4();
        steps[stepId] = {
            id: stepId,
            type: step.type,
            selected: false,
            highlighted: false,
            intersectedAnchors: []
        };
        stepIds.push(stepId);
        propertiesByStepType[step.type].forEach(property => {
            switch (property) {
                case 'index':
                    stepProperties.push({stepId: stepId, name: 'step_id', value: stepIds[step[property]]});
                    break;
                default:
                    stepProperties.push({stepId: stepId, name: property, value: step[property]});
            }
        });
    });

    // Build steps layout
    _.forOwn(processPlan.layout.stepsLayout, (layout, stepReference) => {
        let stepId = stepReference === 'end' || stepReference === 'start' ? stepReference : stepIds[stepReference];
        stepsLayout[stepId] = layout;
    });
    // Build transitions and their properties
    transitions['out'] = {
        id: 'out',
        startStepId: 'start',
        endStepId: stepIds.length > 0 ? stepIds[0] : 'end', // 3rd stepId is the id of the first actual step
        selected: false,
        highlighted: false,
        intersected: false
    };
    transitionProperties.push({
        transitionId: 'out',
        name: 'types',
        value: ['out']
    });
    processPlan.sequence.forEach((step, index) => {
        if (!_.isUndefined(processPlan.layout.transitionsLayout)) {
            let addedTransitions = [];
            availableTransitionsByStepType[step.type].forEach(transitionType => {
                // Check if transition was already prepared
                let transitionTypes = _.find(processPlan.layout.transitionsLayout[index], transition => _.includes(transition.types, transitionType));
                if (transitionTypes) {
                    let transitionId = null;
                    let availableTransitionTypes = transitionTypes.types;
                    addedTransitions.forEach(transition => {
                        if (_.includes(availableTransitionTypes, transition.transitionType)) {
                            transitionId = transition.transitionId;
                        }
                    });

                    let sourceTransition = step[transitionType];
                    if (sourceTransition) {
                        if (transitionType === 'if_disabled') {
                            // For 'if_disabled' transition we do not allow post processing options
                            sourceTransition = _.pick(sourceTransition, 'next_step');
                        }
                        let endStepIndex = sourceTransition.next_step;
                        if (_.isNil(transitionId)) {
                            // Post processing options are different from other transitions, create new transition object
                            let endStepId;
                            switch (endStepIndex) {
                                case -1:
                                    endStepId = stepIds[index + 1] || 'end';
                                    break;
                                case -2:
                                    endStepId = 'end';
                                    break;
                                default:
                                    endStepId = stepIds[endStepIndex];
                                    break;
                            }
                            transitionId = uuidv4();
                            transitions[transitionId] = {
                                id: transitionId,
                                startStepId: stepIds[index],
                                endStepId: endStepId,
                                selected: false,
                                highlighted: false,
                                intersected: false
                            };
                            addedTransitions.push({transitionId, sourceTransition, transitionType});
                            // Initialize transition properties with default values
                            transitionProperties.push({
                                transitionId: transitionId,
                                name: 'types',
                                value: [transitionType]
                            });

                            if (transitionType !== 'if_disabled') {
                                let selectedPostProcessingOptions = _.map(sourceTransition.post_process, option => option.type);
                                _.forEach(postProcessingOptionsByStartStepType[step.type], option => {
                                    let value;
                                    switch (option) {
                                        case 'create_report':
                                            value = {
                                                values: _.filter(sourceTransition.post_process, option =>
                                                    !_.isString(option.type) || _.includes(availableReportsByStartStepType[step.type], option.type)
                                                )
                                            };
                                            value.checked = !_.isEmpty(value.values);
                                            break;
                                        case 'certify':
                                            value = _.includes(selectedPostProcessingOptions, option);
                                            break;
                                        default:
                                            value = null;
                                            break;
                                    }
                                    transitionProperties.push({
                                        transitionId: transitionId,
                                        name: option,
                                        value
                                    });
                                });
                            }
                        }
                        else {
                            // Post processing options are the same as in already created transition, 'link' type to existing transition
                            let typeProperty = _.find(transitionProperties, property => property.name === 'types' && property.transitionId === transitionId);
                            typeProperty.value.push(transitionType);
                        }
                    }
                }
            });
        } else {
            let addedTransitions = [];
            availableTransitionsByStepType[step.type].forEach(transitionType => {
                // Check if transition was already prepared
                let sourceTransition = step[transitionType];
                if (sourceTransition) {
                    if (transitionType === 'if_disabled') {
                        // For 'if_disabled' transition we do not allow post processing options
                        sourceTransition = _.pick(sourceTransition, 'next_step');
                    }
                    let endStepIndex = sourceTransition.next_step;
                    let transitionId = _.chain(addedTransitions)
                        .find(transition =>
                            transitionType === 'if_disabled' ?
                                transition.sourceTransition.next_step === endStepIndex :
                                _.isEqual(transition.sourceTransition, sourceTransition)
                        )
                        .get('transitionId')
                        .value();
                    if (_.isNil(transitionId)) {
                        // Post processing options are different from other transitions, create new transition object
                        let endStepId;
                        switch (endStepIndex) {
                            case -1:
                                endStepId = stepIds[index + 1] || 'end';
                                break;
                            case -2:
                                endStepId = 'end';
                                break;
                            default:
                                endStepId = stepIds[endStepIndex];
                                break;
                        }
                        transitionId = uuidv4();
                        transitions[transitionId] = {
                            id: transitionId,
                            startStepId: stepIds[index],
                            endStepId: endStepId,
                            selected: false,
                            highlighted: false,
                            intersected: false
                        };
                        addedTransitions.push({transitionId, sourceTransition});
                        // Initialize transition properties with default values
                        transitionProperties.push({
                            transitionId: transitionId,
                            name: 'types',
                            value: [transitionType]
                        });

                        if (transitionType !== 'if_disabled') {
                            let selectedPostProcessingOptions = _.map(sourceTransition.post_process, option => option.type);
                            _.forEach(postProcessingOptionsByStartStepType[step.type], option => {
                                let value;
                                switch (option) {
                                    case 'create_report':
                                        value = {
                                            values: _.filter(sourceTransition.post_process, option =>
                                                !_.isString(option.type) || _.includes(availableReportsByStartStepType[step.type], option.type)
                                            )
                                        };
                                        value.checked = !_.isEmpty(value.values);
                                        break;
                                    case 'certify':
                                        value = _.includes(selectedPostProcessingOptions, option);
                                        break;
                                    default:
                                        value = null;
                                        break;
                                }
                                transitionProperties.push({
                                    transitionId: transitionId,
                                    name: option,
                                    value
                                });
                            });
                        }
                    }
                    else {
                        // Post processing options are the same as in already created transition, 'link' type to existing transition
                        let typeProperty = _.find(transitionProperties, property => property.name === 'types' && property.transitionId === transitionId);
                        typeProperty.value.push(transitionType);
                    }
                }
            });
        }
    });
    // Build transitions layout
    if (_.isNil(processPlan.layout.transitionsLayout)) {
        transitionsLayout = generateTransitionLayout(transitions);

    } else {
        _.forOwn(processPlan.layout.transitionsLayout, (stepTransitionsLayout, stepReference) => {
            stepTransitionsLayout.forEach(transitionLayout => {
                let stepId = stepReference === 'end' || stepReference === 'start' ? stepReference : stepIds[stepReference];

                let transitionId;
                if (stepReference === 'start') {
                    // Start step always has single transition 'out'
                    transitionId = 'out';

                } else {
                    // Not start state, look for transition by step ID & types array
                    let stepTransitionIds = _.chain(transitions)
                        .filter(transition => transition.startStepId === stepId)
                        .map(transition => transition.id)
                        .value();

                    transitionId = _.chain(transitionProperties)
                        .filter(property => _.includes(stepTransitionIds, property.transitionId))
                        .filter(property => property.name === 'types')
                        .filter(property => _.xor(property.value, transitionLayout.types).length === 0)
                        .first()
                        .get('transitionId')
                        .value();
                }

                transitionsLayout[transitionId] = {
                    start: transitionLayout.start,
                    end: transitionLayout.end
                };
            })
        });
    }
    return {
        baseInfo,
        steps,
        transitions,
        stepsLayout,
        transitionsLayout,
        stepProperties,
        transitionProperties
    };
};

const validateProcessPlan = state => {
    let validationResult = true;

    // Check if all steps has incoming or outgoing transitions
    let allStepsIds = _.map(state.steps, step => step.id);
    let allStepsIdsToStartFrom = [...allStepsIds, 'start'];
    let allStepIdsToEndAt = [...allStepsIds, 'end'];
    _.map(state.transitions, transition => {
        _.remove(allStepsIdsToStartFrom, stepId => stepId === transition.startStepId);
        _.remove(allStepIdsToEndAt, stepId => stepId === transition.endStepId);
    });

    if (allStepIdsToEndAt.length !== 0 || allStepsIdsToStartFrom.length !== 0) {
        validationResult = false;
    }

    // Check if there are transitions for all available types for each step depending on availability
    if (_.includes(_.map(getAllStepsValidationTooltips(state), validationValue => _.isEmpty(validationValue)), false)) {
        validationResult = false;
    }

    // Check whether transitions properties are valid
    let createReportProperty = _.filter(state.transitionProperties, property => property.name === 'create_report');
    _.map(createReportProperty, property => {
        if (property.value.checked && property.value.values.length === 0) {
            validationResult = false;
        }
    });

    // Check if there are no broken reference to available objects
    let stepIdProperties = _.filter(state.stepProperties, property => property.name === 'id');
    _.map(stepIdProperties, property => {
        if (_.isNil(property.value)) {
            validationResult = false;
        }
    });

    if (getProcessPlanNameValidationTooltip(state)) {
        validationResult = false;
    }
    ;

    return validationResult;
};

const getErrorTypes = state => {
    let stepsErrors =  _.map(getAllStepsValidationTooltips(state), stepError => stepError );
    let transitionErrors = _.map(getAllTransitionsPropertiesValidationTooltips(state), transitionError => transitionError);
    let processPlanNameErrors = getProcessPlanNameValidationTooltip(state)
        ? state.processPlan.name.value === ''
            ? state.localize.translations['NG_PPE_NameEmptyWarning_long']
            : state.localize.translations['NG_PPE_NameExistWarning_long']
        : [];

    return _.chain(_.concat(stepsErrors, transitionErrors, processPlanNameErrors))
        .flatten()
        .uniq()
        .value();
};


export const convertToJson = (state) => {
    if (validateProcessPlan(state)) {
        let processPlan = {};

        processPlan.id = state.processPlan.id;
        processPlan.name = state.processPlan.name;
        processPlan.author = state.processPlan.author;
        processPlan.comment = state.processPlan.comment;
        processPlan.email = state.processPlan.email;
        processPlan.locked = state.processPlan.locked;
        processPlan.hasPassword = state.processPlan.hasPassword;
        processPlan.group = state.processPlan.group;
        processPlan.dialog = state.processPlan.dialog;
        processPlan.addFilesStep = state.processPlan.addFilesStep;

        processPlan.layout = {};
        processPlan.layout.stepsLayout = {};
        processPlan.layout.transitionsLayout = {};
        processPlan.sequence = [];

        let stepsSequence = [state.transitions.out.endStepId];

        _.map(state.steps, step => {
            if (step.id !== 'start' && step.id !== 'end' && !_.includes(stepsSequence, step.id)) {
                stepsSequence.push(step.id);
            }
        });
        _.map(stepsSequence, (stepId, index) => {
            // Build process plan sequence step
            let stepType = getStepTypeById(state, stepId);
            let step = {
                type: stepType,
                ..._.chain(propertiesByStepType[stepType])
                    .keyBy(_.identity)
                    .mapValues(propertyName => {
                        let stepProperty = _.find(state.stepProperties, property => property.name === propertyName && property.stepId === stepId);
                        let stepPropertyValue = _.isNil(stepProperty) ? null : stepProperty.value;

                        if (_.includes(booleanProperties, propertyName) && _.isNil(stepPropertyValue)) {
                            stepPropertyValue = false;
                        }

                        if (propertyName === 'index') {
                            let referencedStepProperty = _.find(state.stepProperties, property => property.name === 'step_id' && property.stepId === stepId);
                            if (referencedStepProperty) {
                                let referencedStepId = referencedStepProperty.value;
                                if (!_.isNil(referencedStepId)) {
                                    stepPropertyValue = _.indexOf(stepsSequence, referencedStepId);
                                }
                            }
                        }
                        return stepPropertyValue;
                    })
                    .omitBy(_.isNil)
                    .value()
            };

            _.map(availableTransitionsByStepType[stepType], transitionType => {
                let transitionsIdsByStartStepId = _.chain(state.transitions)
                    .filter(transition => transition.startStepId === stepId)
                    .map(transition => transition.id)
                    .value();
                let transitionsIdsByType = _.chain(state.transitionProperties)
                    .filter(property => property.name === 'types' && _.includes(property.value, transitionType))
                    .map(property => property.transitionId)
                    .value();
                let transition = state.transitions[_.intersection(transitionsIdsByStartStepId, transitionsIdsByType)[0]];
                if (transition) {
                    step[transitionType] = {};
                    let nextStepIndex;
                    if (transition.endStepId === 'end' && index !== stepsSequence.length - 1) {
                        nextStepIndex = -2;
                    } else {
                        nextStepIndex = _.indexOf(stepsSequence, transition.endStepId);
                        nextStepIndex = nextStepIndex === index + 1 ? -1 : nextStepIndex;
                    }
                    step[transitionType].next_step = nextStepIndex;
                    if (transitionType !== 'if_disabled') {
                        _.map(postProcessingOptionsByStartStepType[stepType], optionName => {
                            switch (optionName) {
                                case 'create_report':
                                    let property = _.find(state.transitionProperties, property => property.transitionId === transition.id && property.name === optionName);
                                    if (property.value.checked) {
                                        step[transitionType].post_process = [
                                            ...(step[transitionType].post_process || []),
                                            ..._.map(property.value.values, value => (value))
                                        ];
                                    }
                                    break;
                                case 'certify':
                                    if (_.find(state.transitionProperties, property => property.transitionId === transition.id && property.name === optionName).value) {
                                        step[transitionType].post_process = [...(step[transitionType].post_process || []), {type: optionName}];
                                    }
                                    break;
                                default:
                                    break;
                            }
                        });
                    }
                }
            });

            if (!_.isNil(step.type)) {
                processPlan.sequence[index] = step;
            }

            //Build process plan transitions layout
            let transitionsIdsByStartStepId = _.chain(state.transitions)
                .filter(transition => transition.startStepId === stepId)
                .map(transition => transition.id)
                .value();

            processPlan.layout.transitionsLayout[index] = [
                ..._.map(transitionsIdsByStartStepId, transitionId => ({
                        ...state.transitionsLayout[transitionId],
                        types: _.find(state.transitionProperties, property => property.transitionId === transitionId && property.name === 'types').value
                    })
                )
            ]
        });

        //Add transition layout for start step
        processPlan.layout.transitionsLayout.start = [{
            ...state.transitionsLayout.out,
            types: ['out']
        }];


        //Build process plan steps layout
        _.mapValues(state.stepsLayout, (layout, stepId) => {
            let layoutIndex;
            if (stepId === 'start' || stepId === 'end') {
                layoutIndex = stepId;
            } else {
                layoutIndex = _.indexOf(stepsSequence, stepId);
            }

            processPlan.layout.stepsLayout[layoutIndex] = layout;
        });
        return JSON.stringify(processPlan, null, '\t');
    } else {
        return JSON.stringify({
            name: state.processPlan.name,
            errors: true,
            errorMessages: getErrorTypes(state)
        }, null, '\t')
    }
};
