import {connect} from 'react-redux'
import Step from './Step'
import {moveStep, updateStepsLayoutPosition, updateStepsLayoutSize} from '../../../redux/modules/stepsLayout';
import {getStepText} from "../../../redux/modules/availableObjects";
import {
    checkIsAnchorsImposed,
    dropExistingStepIntoTransition,
    isStepHighlighted,
    removeStep,
    selectStep
} from "../../../redux/modules/steps";
import {
    checkIsTransitionImposed,
    getStepDependenceFromTransitions,
    highlightElement,
    isExistImposedTransition,
    selectTransition
} from "../../../redux/modules/transitions";
import {isLeftTopAngleVisible} from "../../../redux/modules/connectionsLayout";
import {
    getScale,
    getSelectedStepId,
    getSnapToGrid,
    getSvgPosition,
    selectAnotherElement,
    selectDrawingArea,
    startTempTransitionByShortcut
} from "../../../redux/modules/drawingArea";
import {
    changeTransitionPosition,
    endTransitionCreation,
    removeTempTransition,
    startTempTransition,
    updateTempTransitionEndPoint
} from "../../../redux/modules/tempTransition";

import {availableTransitionsByStepType} from "../../../api/processPlanConversion";
import {getTransitionTypesByStartStepId} from "../../../redux/modules/transitionProperties";
import _ from 'lodash';
import {getStepAvailabilityById, getStepPropertyByName} from "../../../redux/modules/stepProperties";
import {
    modifyingConnection,
    modifyingTransition,
    setCreatingConnectionType,
    setCreatingTransitionType
} from "../../../redux/modules/transitionActionType";
import {DRAWING_AREA, getFocusedArea, setFocusOnGlobalArea} from "../../../redux/modules/processPlan";
import {applyChange} from '../../../redux/modules/changeStack';
import {MoveStepChange} from '../../../redux/model/MoveStep';
import uuidv4 from "uuid/v4";


export const mapStateToProps = (state, {id}) => {
    let step = state.steps[id];
    let layout = state.stepsLayout[id];
    let type = id === 'start' || id === 'end' ? id : step.type;
    let availableTransitionTypes = availableTransitionsByStepType[type];
    let existingTransitionTypes = getTransitionTypesByStartStepId(state, id);
    let stepIdProperty = getStepPropertyByName(state, id, 'step_id');
    let snapToGrid = getSnapToGrid(state);
    let stepAvailability = getStepAvailabilityById(state, id);
    return {
        className: id === 'start' || id === 'end' ? 'start-end' : step.type,
        text: id === 'start' || id === 'end' ? id : getStepText(state, id),
        position: layout.position,
        size: layout.size,
        selected: getSelectedStepId(state) === id,
        focused: getSelectedStepId(state) === id && getFocusedArea(state) === DRAWING_AREA,
        intersectedAnchors: id === 'start' || id === 'end' ? [] : step.intersectedAnchors,
        highlighted: id === 'start' || id === 'end' ? false : (step.highlighted || isStepHighlighted(state, id)),
        selectable: !(id === 'start' || id === 'end'),
        transitionCanStart: !(_.difference(availableTransitionTypes, existingTransitionTypes).length === 0),
        connectionCanStart: !_.isNil(stepIdProperty) && _.isNil(stepIdProperty.value),
        connectionAnchorIsVisible: isLeftTopAngleVisible(state, id),
        modifyingTransition: modifyingTransition(state),
        modifyingConnection: modifyingConnection(state),
        pickUpFile: type === 'filepickup',
        zoomScale: getScale(state),
        svgPosition: getSvgPosition(state),
        snapToGrid: snapToGrid,
        stepAvailability: (stepAvailability) ? ' enableStep' : ' disableStep',
        anchorStartEndAvailability: id !== 'end',
        isExistDependentTransition: getStepDependenceFromTransitions(state, id),
        isIntersected: isExistImposedTransition(state)
    };
};

const mapDispatchToProps = (dispatch, {id}) => {
    return {
        onStepPositionChangeEnd: (startPosition, position) => {
            dispatch(applyChange(new MoveStepChange(id, startPosition, position)));
        },
        onStepPositionChange: (position) => {
            dispatch(updateStepsLayoutPosition(id, position));
        },
        onStepSelect: () => {
            dispatch(selectStep(id));
            dispatch(selectTransition(null));
            dispatch(selectDrawingArea(null));
        },
        onStepHighlight: () => {
            dispatch(highlightElement(id, 'step'));
        },
        onStepHighlightReset: () => {
            dispatch(highlightElement(null));
        },
        onTransitionCreationStart: (position) => {
            if (position === 'left-top-angle') {
                dispatch(setCreatingConnectionType());
            } else {
                dispatch(setCreatingTransitionType());
            }
            dispatch(startTempTransition(id, position));
        },
        onTransitionCreationEnd: (position) => {
            dispatch(endTransitionCreation(id, position));
        },
        onAnchorMouseOver: (position) => {
            dispatch(changeTransitionPosition(position === 'left-top-angle' ? 'left' : position));
        },
        onAnchorMouseOut: () => {
            dispatch(changeTransitionPosition(null));
        },
        onMoveStep: (direction) => {
            dispatch(moveStep(id, direction));
        },
        onRemoveStep: () => {
            if (id !== 'start' && id !== 'end') {
                dispatch(removeStep(id));
                dispatch(selectStep(null));
                dispatch(selectDrawingArea(true));
                dispatch(setFocusOnGlobalArea(DRAWING_AREA));
            }
        },
        onTransitionCreationStartByShortcut: (position) => {
            dispatch(startTempTransitionByShortcut(position, id));
        },
        onAnotherElementSelect: (direction) => {
            dispatch(selectAnotherElement(direction));
        },
        onCreateTempTransitionByTouch: (coordinates) => {
            dispatch(updateTempTransitionEndPoint(coordinates))
        },
        onDeleteTempTransition: () => {
            dispatch(removeTempTransition());
            dispatch(selectDrawingArea(null));
            dispatch(selectStep(null));
        },
        onAddTransition: (stepId, position) => {
            dispatch(endTransitionCreation(stepId, position))
        },
        onTransitionOver: (position) => {
            dispatch(checkIsTransitionImposed(position));
            dispatch(checkIsAnchorsImposed(id))
        },
        onDropStep: () => {
            let newTransitionId = uuidv4();
            dispatch(selectDrawingArea(true));
            dispatch(selectStep(id));
            dispatch(dropExistingStepIntoTransition(id, newTransitionId));
        },
        onSizeChange: (size) => {
            dispatch(updateStepsLayoutSize(id, size));
        }
    };
};


export default connect(mapStateToProps, mapDispatchToProps)(Step);