import {connect} from 'react-redux';
import ExternalObjectSelect from './ExternalObjectSelect';
import {
    createExternalObject,
    duplicateExternalObject,
    editExternalObject
} from '../../../../redux/modules/availableObjects';

const mapDispatchToProps = (dispatch, {type, value, onSelect, id}) => ({
    onCreate: () => dispatch(createExternalObject(type)),
    onDuplicate: () => dispatch(duplicateExternalObject(type, value)),
    onEdit: () => dispatch(editExternalObject(type, value)),
    onSetSelection: () => onSelect(id)
});

export default connect(null, mapDispatchToProps)(ExternalObjectSelect);