import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Step from './step/Step.container';
import Transition from './transition/Transition.container';
import TempTransition from './transition/TempTransition.container';
import TempConnection from './transition/TempConnection.container';
import FilePickupConnectionLine from './transition/FilePickupConnectionLine.container';

import './DrawingArea.css';
import spinner from './spinner.svg';
import {HEADER_OFFSET} from "../App";

class DrawingArea extends Component {
    constructor(props) {
        super(props);

        this.renderSteps = this.renderSteps.bind(this);
        this.renderTransitions = this.renderTransitions.bind(this);
        this.renderFilePickupConnection = this.renderFilePickupConnection.bind(this);
    }

    setDrawingArea = (drawingArea) => {
        this.drawingArea = drawingArea;
    };

    render() {
        if (!this.props.fetching) {
            window.onresize=this.browserResize;
            let viewBox = '' + this.props.viewportPosition.x + ' ' + this.props.viewportPosition.y + ' '
                + this.props.viewportSize.width + ' ' + this.props.viewportSize.height;
            
            return (
                <div ref={this.setDrawingArea}
                     tabIndex="0"
                     className="svgContainer"
                     onMouseMove={this.handleTempTransitionUpdate}
                     onMouseDown={this.handleDrawingAreaSelect}
                     onKeyDown={this.handleKeyDown}>
                    <svg
                        role='figure'
                        aria-label="canvas"
                        viewBox={viewBox}
                        onMouseDown={this.handleMouseDown}
                        onMouseUp={this.handleMouseUp}
                        onTouchStart= {this.handleTouchStart}
                        onTouchEnd= {this.handleTouchEnd}>
                        {this.renderTransitions()}
                        {this.renderFilePickupConnection()}
                        {this.props.modifyingTransition && this.props.transitionCreationInProgress ?
                            <TempTransition/> : null}
                        {this.props.modifyingConnection && this.props.transitionCreationInProgress ?
                            <TempConnection/> : null}
                        {this.renderSteps()}
                    </svg>
                </div>
            );
        }
        else {
            return (
                <div className="loading">
                    <img src={spinner} alt="Loading"/>
                </div>
            );
        }
    }

    renderSteps() {
        return this.props.steps.map(stepId => (
            <Step id={stepId} key={stepId}
                  isLockedProcessPlan={this.props.isLockedProcessPlan}
                  validationTooltips={this.props.stepsValidationTooltips[stepId]}/>));
    }

    renderTransitions() {
        return this.props.transitions.map(transitionId => (<Transition
            id={transitionId}
            types={this.props.transitionsTypes[transitionId]}
            icons={this.props.transitionsIcons[transitionId]}
            isLockedProcessPlan={this.props.isLockedProcessPlan}
            validationTooltips={this.props.transitionsValidationTooltips[transitionId]}
            key={transitionId}/>));
    }

    renderFilePickupConnection() {
        return this.props.filePickupConnections.map(connection => (
            <FilePickupConnectionLine connection={connection} key={`connection${connection.startStepId}`}/>));
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.drawingArea.focus();
        }
    }


    handleTempTransitionUpdate = (e) => {
        if (this.props.transitionCreationStarted) {
            this.props.onUpdateTempTransition({
                x: e.pageX ,
                y: e.pageY - HEADER_OFFSET
            });
        }
    };

    handleTouchStart = (e) => {
        this.haveChangedPosition = false;

        this.computedPosition = {
            x: e.targetTouches[0].pageX,
            y: e.targetTouches[0].pageY,
        };
        document.addEventListener('touchmove', this.handleTouchMove);
        document.addEventListener('touchend', this.handleTouchEnd);
    };

    handleTouchMove = (e) => {
        // Handle move viewport
        let xDiff = this.computedPosition.x - e.targetTouches[0].pageX;
        let yDiff = this.computedPosition.y - e.targetTouches[0].pageY;
        if (xDiff !== 0 || yDiff !== 0) {
            this.haveChangedPosition = true;
        }

        this.computedPosition.x = e.targetTouches[0].pageX;
        this.computedPosition.y = e.targetTouches[0].pageY;

        let position = {
            x: this.props.viewportPosition.x + (xDiff * this.props.scale),
            y: this.props.viewportPosition.y + (yDiff * this.props.scale)
        };

        this.props.onMoveViewport(position);
    };

    handleTouchEnd = () => {
        document.removeEventListener('touchmove', this.handleTouchMove);
        document.removeEventListener('touchend', this.handleTouchStart);
        this.computedPosition = {};
    };

    handleMouseDown = (e) => {
        this.haveChangedPosition = false;

        this.computedPosition = {
            x: e.pageX,
            y: e.pageY,
        };

        document.addEventListener('mousemove', this.handleMouseMove);
        document.addEventListener('mouseup', this.handleMouseUp);
    };

    handleMouseMove = (e) => {
        // Handle move viewport
        let xDiff = this.computedPosition.x - e.pageX;
        let yDiff = this.computedPosition.y - e.pageY;
        if (xDiff !== 0 || yDiff !== 0) {
            this.haveChangedPosition = true;
        }

        this.computedPosition.x = e.pageX;
        this.computedPosition.y = e.pageY;

        let position = {
            x: this.props.viewportPosition.x + (xDiff * this.props.scale),
            y: this.props.viewportPosition.y + (yDiff * this.props.scale),
        };

        this.props.onMoveViewport(position);
    };


    handleMouseUp = () => {
        document.removeEventListener('mousemove', this.handleMouseMove);
        document.removeEventListener('mouseup', this.handleMouseUp);
        this.computedPosition = {};
    };

    browserResize=() =>{
        let el= document.getElementById('mainSvgField');
        let size = {
            width: el.offsetWidth,
            height: el.offsetHeight,
        };
        this.props.onBrowserChangeViewport(size);
        this.drawingArea.focus();
    };

    handleDrawingAreaSelect = () => {
        this.drawingArea.focus();
        this.props.onDrawingAreaSelect();
    };

    handleKeyDown = (e) => {
        e.preventDefault();
        switch (e.key) {
            case 'ArrowUp':
                this.props.onArrowKeyDown();
                break;
            case 'ArrowDown':
                this.props.onArrowKeyDown();
                break;
            case 'Escape':
                this.props.onResetSelection();
                break;
            case 'Tab':
                this.props.onMoveToAnotherGroup();
                break;
            default:
                break;
        }
    };
}

const PositionShape = PropTypes.shape({
    x: PropTypes.number.isRequired,
    y: PropTypes.number.isRequired
});

const SizeShape = PropTypes.shape({
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired
});

DrawingArea.propTypes = {
    steps: PropTypes.arrayOf(PropTypes.string),
    transitions: PropTypes.arrayOf(PropTypes.string),
    transitionTypes: PropTypes.arrayOf(PropTypes.string),
    transitionIcons: PropTypes.arrayOf(PropTypes.string),
    filePickupConnections: PropTypes.arrayOf(PropTypes.object),
    fetching: PropTypes.bool,
    modifyingTransition: PropTypes.bool,
    modifyingConnection: PropTypes.bool,
    modifyingTransitionByMouse: PropTypes.bool,
    transitionCreationInProgress: PropTypes.bool,
    transitionCreationStarted: PropTypes.bool,
    scale: PropTypes.number,
    viewportPosition: PositionShape.isRequired,
    viewportSize: SizeShape.isRequired,
    selected: PropTypes.bool.isRequired,
    focused: PropTypes.bool.isRequired,
    onUpdateTempTransition: PropTypes.func,
    onDrawingAreaSelect: PropTypes.func,
    onMoveViewport: PropTypes.func,
    onBrowserChangeViewport: PropTypes.func,
    onMoveToAnotherGroup: PropTypes.func,
    onResetSelection: PropTypes.func,
    onArrowKeyDown: PropTypes.func
};

export default DrawingArea;