import _ from 'lodash';

class Api {
    subscribe() {}
    loadObjects() {
        throw new Error('loadObjects not implemented');
    }
    loadProcessPlan() {
        throw new Error('loadProcessPlan not implemented');
    }
    saveProcessPlan() {
        throw new Promise(resolve => {resolve()});
    }
    loadSettings() {
        throw new Error('loadSettings not implemented');
    }
    updateSettings(settings) {
        console.log('Settings update requested', settings);
        return new Promise(resolve => resolve());
    }
    createExternalObject(type) {
        console.log(`createExternalObject: ${type}`);
        return new Promise(resolve => resolve());
    }
    updateExternalObject(type, id) {
        console.log(`updateExternalObject: ${type}, ${id}`);
        return new Promise(resolve => resolve());
    }
    duplicateExternalObject(type, id) {
        console.log(`duplicateExternalObject: ${type}, ${id}`);
        return new Promise(resolve => resolve());
    }
    setPassword(password) {
        console.log(`setPassword: ${password}`);
        return new Promise(resolve => resolve());
    }
    checkPassword(password) {
        console.log(`checkPassword: ${password}`);
        return new Promise(resolve => resolve());
    }
}

class FetchApi extends Api {
    loadObjects() {
        return fetch('./samples/availableObjects.json').then(response => response.json());
    }
    loadProcessPlan(sampleName = 'default') {
        return fetch('./samples/processPlans/' + sampleName + '.json').then(response => response.json());
    }
    loadSettings() {
        return fetch('./samples/settings.json').then(response => response.json());
    }
    createExternalObject(type) {
        console.log(`createExternalObject: ${type} => <file_name:> should be selected`, );
        return Promise.resolve('V8ae60b5a7524b68eda9fc1dbd2bffbad');
    }
    duplicateExternalObject(type, id) {
        console.log(`duplicateExternalObject: ${type}, ${id} => <font_name:Hugo> should be selected`);
        return Promise.resolve('Vd125ff9fe4a7dcf7c763c49c425e4902');
    }
    setPassword(password) {
        return Promise.resolve(true);
    }
    checkPassword(password) {
        return Promise.resolve(true);
    }
}

class PdfToolboxNativeApi extends Api {
    constructor() {
        super();

        this.backEventsHandlers = {};

        window.sendExtensionMessage({
            request: {
                method: 'registerEventCallback'
            },
            persistent: true,
            onSuccess: (data) => {
                let callback = this.backEventsHandlers[data.eventName];
                if (_.isFunction(callback)) {
                    callback(data);
                }
            }
        });
    }

    _sendMessage(method, data) {
        return new Promise((resolve, reject) => {
            window.sendExtensionMessage({
                request: {
                    method,
                    data
                },
                onSuccess: (data) => {
                    try {
                        let value;
                        if (data != null && data.value != null && data.contentType != null) {
                            switch (data.contentType) {
                                case 'json':
                                    value = JSON.parse(data.value);
                                    break;
                                case 'string':
                                case 'bool':
                                    value = data.value;
                                    break;
                                default:
                                    reject(new Error(`Received unknown contentType ${data.contentType}, ignoring`));
                                    break;
                            }
                        }
                        resolve(value);
                    } catch (e) {
                        reject(e);
                    }
                },
                onFailure: (code, message) => {
                    console.log("Error(" + code + "): " + message);
                    reject(new Error(message));
                }
            });
        });
    }

    subscribe(eventName, callback) {
        this.backEventsHandlers[eventName] = callback;
    }

    loadObjects() {
        return this._sendMessage('loadObjects');
    }

    loadProcessPlan(sampleName) {
        return this._sendMessage('loadDefault');
    }

    saveProcessPlan(data) {
        return this._sendMessage('saveProcessPlan', data);
    }

    loadSettings() {
        return this._sendMessage('loadSettings');
    }

    updateSettings(settings) {
        return this._sendMessage('updateSettings', JSON.stringify(settings));
    }

    createExternalObject(type) {
        return this._sendMessage('createExternalObject', type);
    }

    updateExternalObject(type, id) {
        return this._sendMessage('updateExternalObject', {type, id});
    }

    duplicateExternalObject(type, id) {
        return this._sendMessage('duplicateExternalObject', {type, id});
    }

    setPassword(password) {
        return this._sendMessage('setPassword', password);
    }

    checkPassword(password) {
        return this._sendMessage('checkPassword', password);
    }
}

export const nativeApiAvailable = _.isFunction(window.sendExtensionMessage);

const api = nativeApiAvailable ? new PdfToolboxNativeApi() : new FetchApi();

export default api;
