import {connect} from 'react-redux';
import {createExternalObject, getVariables} from "../../../../../redux/modules/availableObjects";
import Variables from "./Variables";
import {getTranslate} from "react-localize-redux/lib/index";

const mapStateToProps = (state) => {
    return {
        variables: getVariables(state),
        translate: getTranslate(state.localize)
    };
};

const mapDispatchToProps = (dispatch, {onVariableCreated}) => {
    return {
        onCreate: () => dispatch(createExternalObject('variable', onVariableCreated)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Variables)