import {handleActions} from 'redux-actions';
import {createSelector} from 'reselect';
import _ from 'lodash';
import {
    getAvailableTransitionReports,
    getAvailableTransitionTypes,
    getSelectedTransition,
    getTransitionById,
    getTransitionIds,
    getTransitions,
    getTransitionsByStartStepId,
    getTransitionStartStepId,
    reportsLabels,
} from "./transitions";
import {getVariableDisplayText} from "./availableObjects";
import {ChangeTransitionProperty} from "../model/ChangeTransitionProperty";
import {applyChange} from "./changeStack";
import {getStepAvailabilityById} from './stepProperties';
import {getTranslate} from 'react-localize-redux';

const NO_REPORTS_CHOSEN_FOR_CHECKED_REPORTS_OPTIONS = 'NG_PPE_No_reports_chosen_for_checked_reports_option_long';
const NO_TYPES_CHOSEN = 'NG_PPE_No_type_chosen_long';

export const SELECTABLE_PROPERTIES_NUMBER_BY_TRANSITION_PROPERTY_NAME = {
    'checkbox': 1,
    'checkable-multiselect': 2
};

export const propertyEditControls = {
    types: (state, transition) => ({
        type: 'checkbox-group',
        label: 'Transition types',
        options: getAvailableTransitionTypes(state, transition.id),
        selectedOptions: getSelectedOnOtherTransitionsTypes(state, transition)
    }),
    certify: () => ({
        group: 'Post processing',
        type: 'checkbox',
        label: 'Certify PDF'
    }),
    create_report: (state, transition) => ({
        group: 'Post processing',
        type: 'checkable-multiselect',
        label: 'Create report',
        options: [
            {
                label: 'Base Types',
                options: getAvailableTransitionReports(state, transition.id)
            },
            /*{
                label: 'Variables',
                options: getVariables(state).map(
                    variable => ({
                        label: getVariableDisplayText(variable),
                        value: variable.id,
                    })
                )
            }*/
        ]
    })
};

//- Actions

export const updateTransitionTypes = (transitionId, types) => (dispatch, getState) => {
    let startStepId = getTransitionStartStepId(getTransitions(getState()), transitionId);
    let typesProperties = getTransitionPropertiesByNameAndStartStepId(getState(), 'types', startStepId);

    _.forEach(typesProperties, property => {
        if (property.transitionId !== transitionId) {
            // Uncheck types that are now assigned to new transition from previous
            let value = _.difference(property.value, types);
            if (!_.isEqual(value, property.value)) {
                dispatch(applyChange(new ChangeTransitionProperty(property.transitionId, 'types', value)));
            }
        }
    });
    dispatch(applyChange(new ChangeTransitionProperty(transitionId, 'types', types)));
};

//- State
const initialState = [];

//- Reducer
export default handleActions({
    PROCESS_PLAN_LOAD: (state, action) => {
        return action.payload.transitionProperties;
    },
    CHANGE_APPLY: (state, {payload: change}) => {
        switch (change.description) {
            case 'Remove transition':
                return change.applyRemoveTransitionProperty(state);
            case 'Add transition':
                return change.applyAddTransitionProperties(state);
            case 'Change transition property':
                return change.applyTransitionPropertyChange(state);
            case 'Remove step':
                return change.applyRemoveTransitionConnectedProperties(state);
            case 'Insert new step into transition':
                return change.applyAddTransitionPropertiesForInsertStep(state);
            case 'Create transitions for insert step':
                return change.applyAddTransitionPropertiesForInsertStep(state);
            case 'Generate auto layout':
                return change.applyLayoutAutoGenerationForTransitionProperties(state);
            default:
                return state;
        }
    },
    CHANGE_REVERT: (state, {payload: change}) => {
        switch (change.description) {
            case 'Remove transition':
                return change.revertRemoveTransitionProperty(state);
            case 'Add transition':
                return change.revertAddTransitionProperties(state);
            case 'Change transition property':
                return change.revertTransitionPropertyChange(state);
            case 'Remove step':
                return change.revertRemoveTransitionConnectedProperties(state);
            case 'Generate auto layout':
                return change.revertLayoutAutoGenerationForTransitionProperties(state);
            case 'Insert new step into transition':
                return change.revertAddTransitionPropertiesForInsertStep(state);
            case 'Create transitions for insert step':
                return change.revertAddTransitionPropertiesForInsertStep(state);
            default:
                return state;
        }
    }
}, initialState);

//- Selectors
export const getAllTransitionProperties = state => state.transitionProperties;

export const getTransitionProperties = (state, transitionId) => {
    if (transitionId === 'out') {
        return '';
    }

    return _.chain(getAllTransitionProperties(state))
        .filter(property => property.transitionId === transitionId)
        .map(property => ({
            transitionId: transitionId,
            name: property.name,
            value: property.value
        }))
        .value();
};

export const getSelectedTransitionProps = createSelector(
    state => state,
    getSelectedTransition,
    (state, selectedTransition) => _.isNil(selectedTransition) ? [] : getTransitionProperties(state, selectedTransition.id)
);

export const getSelectedTransitionPropsWithControlDescriptors = createSelector(
    state => state,
    getSelectedTransition,
    getSelectedTransitionProps,
    (state, transition, selectedTransitionProps) =>
        _.chain(selectedTransitionProps)
            .map(property => ({
                objectId: property.transitionId,
                name: property.name,
                value: property.value,
                disabled: (
                    propertyEditControls[property.name](state, transition).group === "Post processing" &&
                    isPostProcessDisabled(state, transition.id)
                ),
                controlDescriptor: propertyEditControls[property.name](state, transition)
            }))
            .groupBy(property => property.controlDescriptor.group)
            .map((properties, groupName) => ({
                name: groupName === 'undefined' ? undefined : groupName,
                properties
            }))
            .value()
);

export const getTransitionPropertiesByNameAndTransitionId = (state, name, transitionId) => {
    return _.find(getTransitionProperties(state, transitionId), property => property.name === name);
};

export const getTransitionPropertiesByNameAndStartStepId = (state, name, startStepId) => {
    let stepTransitionsIds = _.map(getTransitionsByStartStepId(state, startStepId), transition => transition.id);
    return _.filter(
        getAllTransitionProperties(state),
        property => _.includes(stepTransitionsIds, property.transitionId) && property.name === name
    );
};

export const getTransitionTypesByStartStepId = (state, startStepId) => {
    let transitionIds = Object.keys(getTransitionsByStartStepId(state, startStepId));
    return _.chain(state.transitionProperties)
        .filter(property => _.includes(transitionIds, property.transitionId) && property.name === 'types')
        .map(property => property.value)
        .flatten()
        .value();
};

export const getAllTransitionTypes = createSelector(
    getAllTransitionProperties,
    allTransitionProperties => _.chain(allTransitionProperties)
        .filter(property => property.name === 'types')
        .map(property => [property.transitionId, property.value])
        .fromPairs()
        .value()
);

export const getTransitionIcons = (state, id) => {
    return _.chain(getTransitionProperties(state, id))
        .filter(property => (property.name === 'create_report' && property.value.checked && property.value.values.length !== 0) || (property.name === 'certify' && property.value))
        .map(property => {
            let icon = {};
            if (property.name === 'create_report') {
                icon.name = `create_report${property.value.values.length !== 1 ? 's' : ''}`;
                icon.innerText = '' + (property.value.values.length !== 1 ? property.value.values.length : '');
                icon.caption = '';
                if (property.value.values.length === 1) {
                    let reportType = property.value.values[0].type;
                    icon.caption = _.isString(reportType) ? reportsLabels[reportType] : getVariableDisplayText(reportType);
                }
            } else {
                icon.name = property.name;
                icon.innerText = '';
                icon.caption = '';
            }
            return icon;
        })
        .value();
};

export const getAllTransitionIcons = createSelector(
    state => state,
    getTransitionIds,
    (state, transitionIds) => _.chain(transitionIds)
        .map(id => [id, getTransitionIcons(state, id)])
        .fromPairs()
        .value()
);

export const getTransitionPropertiesValidationTooltips = (state, id) => {
    let translate = getTranslate(state.localize);
    let validationTooltips = [];
    let allTransitionProperties = getTransitionProperties(state, id);
    let createReportProperty = _.find(allTransitionProperties, property => property.name === 'create_report');
    if (!_.isNil(createReportProperty) && createReportProperty.value.checked && createReportProperty.value.values.length === 0) {
        validationTooltips.push(translate(NO_REPORTS_CHOSEN_FOR_CHECKED_REPORTS_OPTIONS));
    }

    let typesProperty = _.find(allTransitionProperties, property => property.name === 'types');
    if (!_.isNil(typesProperty) && typesProperty.value.length === 0) {
        validationTooltips.push(translate(NO_TYPES_CHOSEN));
    }

    return validationTooltips;
};

export const getAllTransitionsPropertiesValidationTooltips = createSelector(
    state => state,
    getTransitionIds,
    (state, allTransitionIds) => _.chain(allTransitionIds)
        .map(id => [id, getTransitionPropertiesValidationTooltips(state, id)])
        .fromPairs()
        .value()
);

export const getSelectedOnOtherTransitionsTypes = (state, transition) => {
    return _.chain(getTransitionsByStartStepId(state, transition.startStepId))
        .filter(transitionByStartStepId => transitionByStartStepId.id !== transition.id)
        .map(transitionByStartStepId => transitionByStartStepId.id)
        .map(transitionId => getTransitionPropertiesByNameAndTransitionId(state, 'types', transitionId).value)
        .flatten()
        .value();
};

export const isTransitionDisabled = (state, id) => {
    let transition = getTransitionById(state, id);
    let stepAvailability = getStepAvailabilityById(state, transition.startStepId);
    let transitionEventTypes = getTransitionPropertiesByNameAndTransitionId(state, 'types', id);
    if (_.isNil(transitionEventTypes)) {
        return false;
    }

    if (stepAvailability === true) {
        // Step enabled, disable transition if it has only 'is_disabled' type or none
        return _.without(transitionEventTypes.value, 'if_disabled').length === 0;
    } else if (stepAvailability === false) {
        // Step disabled, disable transition if it doesn't have 'if_disabled' type
        return !_.includes(transitionEventTypes.value, 'if_disabled');
    }
    return false;
};

export const isPostProcessDisabled = (state, id) => {
    let transitionEventTypes = getTransitionPropertiesByNameAndTransitionId(state, 'types', id);
    return _.isNil(transitionEventTypes) ? false : _.without(transitionEventTypes.value, 'if_disabled').length === 0;
};

export const getOverviewReportValues = (state, name, id) => {
    let reportValues = getTransitionPropertiesByNameAndTransitionId(state, name, id).value.values;
    return _.find(reportValues, reportValue => reportValue.type === 'overview_report')
};