import {createSelector} from 'reselect';
import _ from 'lodash';

import {getAllFilePickupStepIds, getSelectedStep} from './steps';
import {getStepProperties, propertyEditControls} from './stepProperties';

export const getAllFilePickupConnections = state =>
    _.chain(getAllFilePickupStepIds(state))
        .map(stepId => getStepProperties(state, stepId))
        .flatten()
        .filter(property => property.name === 'step_id')
        .map(property => ({startStepId: property.stepId, endStepId: property.value}))
        .filter(connection => !_.isNil(connection.endStepId))
        .value();

export const getSelectedStepProps = createSelector(
    state => state,
    getSelectedStep,
    (state, selectedStep) => _.isNil(selectedStep) ? [] : getStepProperties(state, selectedStep.id)
);

export const getSelectedStepPropsWithControlDescriptors = createSelector(
    state => state,
    getSelectedStep,
    getSelectedStepProps,
    (state, step, selectedStepProps) => ([{
        properties: _.map(selectedStepProps, property => ({
            objectId: property.stepId,
            name: property.name,
            value: property.value,
            type: step.type,
            controlDescriptor: propertyEditControls[property.name](state, step)
        }))
    }])
);
