import {Change} from './Change';
import _ from 'lodash';

export class ChangeTransitionPosition extends Change {
    constructor(tempTransition, transition, transitionLayout) {
        super('Change transition position');
        this.tempTransition = tempTransition;

        this.baseTransition = transition;
        this.transitionLayout = transitionLayout;
    }

    applyTransitionPositionChange(transitions) {
        if (!_.isUndefined(this.tempTransition.start) && !_.isUndefined(this.tempTransition.end)) {
            return {
                ...transitions, [this.tempTransition.id]: {
                    id: this.tempTransition.id,
                    startStepId: this.tempTransition.start.stepId,
                    endStepId: this.tempTransition.end.stepId,
                    selected: false,
                    highlighted: false,
                    intersected: false
                }
            }
        } else {
            return transitions
        }
    }

    revertTransitionPositionChange(transitions) {
        return {
            ...transitions, [this.tempTransition.id]: {...this.baseTransition}
        }
    }

    applyTransitionLayoutPositionChange(transitions) {
        if (_.isNil(this.tempTransition)) {
            return transitions;
        }
        if (!_.isUndefined(this.tempTransition.start) && !_.isUndefined(this.tempTransition.end)) {
            return {
                ...transitions, [this.tempTransition.id]: {
                    start: {position: this.tempTransition.start.position},
                    end: {position: this.tempTransition.end.position},
                }
            }
        } else {
            return transitions
        }
    }

    revertTransitionLayoutPositionChange(transitions) {
        return {
            ...transitions, [this.tempTransition.id]: {...this.transitionLayout}
        };
    }

}