import {createAction, handleActions} from 'redux-actions';
import {createSelector} from 'reselect';
import _ from 'lodash';
import {getSelectedStep, getStepTypeById, highlightStep} from "./steps";
import React from 'react';
import {Translate} from 'react-localize-redux';
import {translatedReportLabels} from '../../dictionary'
import * as MainPoints from "./transitionsLayout";
import {calculateTransitionPoint} from "./transitionsLayout";

export const selectTransition = createAction("TRANSITION_SELECT", id => id);
export const highlightTransition = createAction("TRANSITION_HIGHLIGHT", id => id);
export const checkIsTransitionIntersectedByStep = createAction("TRANSITIONS_INTERSECT", (position, stepsLayout, transitionsLayout) => ({
    position,
    stepsLayout,
    transitionsLayout
}));
export const checkIsTransitionImposed = (position) => (dispatch, getState) => {
    dispatch(checkIsTransitionIntersectedByStep(position, getState().stepsLayout, getState().transitionsLayout, getState()))
};


export const highlightElement = (id, elementType) => (dispatch, getState) => {
    if (_.isNil(id)) {
        dispatch(highlightTransition(null));
        dispatch(highlightStep(null));
    }

    switch (elementType) {
        case 'transition':
            let transition = getTransitionById(getState(), id);
            dispatch(highlightStep(transition.startStepId));
            dispatch(highlightTransition(id));
            dispatch(highlightStep(transition.endStepId));
            break;
        case 'step':
            let allTransitions = getTransitionsByStartStepId(getState(), id);
            _.mapValues(allTransitions, transition => dispatch(highlightTransition(transition.id)));
            dispatch(highlightStep(id));
            break;
        default:
            break;
    }
};

export const reportsLabels = {
    'txt_report': 'Text',
    'xml_report': 'XML',
    'overview_report': 'PDF overview',
    'mask_report': 'PDF mask',
    'comment_report': 'Comment',
    'layer_report': 'Layer',
    'compare_report': 'Compare',
    'inventory_report': 'Inventory'
};

export const typeLabels = {
    'on_success': 'success',
    'on_failure': 'failure',
    'on_error': 'error',
    'on_warning': 'warning',
    'on_hit': 'hit',
    'on_info': 'info',
    'else': 'else',
    'if_disabled': 'disabled'
};

const availableReportsByStartStepType = {
    'processplan': [],
    'profile': ['txt_report', 'xml_report', 'overview_report', 'mask_report', 'comment_report', 'layer_report', 'compare_report', 'inventory_report'],
    'check': ['txt_report', 'xml_report', 'overview_report', 'mask_report', 'comment_report', 'layer_report', 'compare_report', 'inventory_report'],
    'fixup': ['txt_report', 'xml_report', 'overview_report', 'mask_report', 'comment_report', 'layer_report', 'compare_report', 'inventory_report'],
    'action': ['compare_report', 'inventory_report'],
    'variable': ['compare_report', 'inventory_report'],
    'filepickup': [],
    'pdfcopy': [],
    'quickcheck': [],
    'addfiles': [],
    'rename': []
};

const availableTransitionTypesByStartStepType = {
    "processplan": ["on_error", "else", "if_disabled"],
    "profile": ["on_error", "on_warning", "on_info", "on_success", "if_disabled"],
    "check": ["on_hit", "on_success", "if_disabled"],
    "fixup": ["on_success", "on_failure", "if_disabled"],
    "action": ["on_success", "on_failure", "if_disabled"],
    "variable": ["on_success", "on_failure", "if_disabled"],
    "filepickup": ["on_success", "on_failure", "if_disabled"],
    "pdfcopy": ["on_success", "on_failure", "if_disabled"],
    "quickcheck": ["on_success", "on_failure", "if_disabled"],
    "addfiles": ["on_success", "on_failure", "if_disabled"],
    "rename": ["on_success", "on_failure", "if_disabled"],
};

//- State
const initialState = {};

//- Reducers
export default handleActions({
    PROCESS_PLAN_LOAD: (state, action) => {
        return action.payload.transitions;
    },
    TRANSITION_SELECT: (state, action) => {
        if (_.isNil(action.payload)) {
            return _.mapValues(state, step => ({...step, selected: false}));
        }
        return _.mapValues(state, (transition, id) => {
            if (id === action.payload) {
                return {...transition, selected: true};
            } else {
                return transition.selected ? {...transition, selected: false} : transition;
            }
        });
    },
    CHANGE_APPLY: (state, {payload: change}) => {
        switch (change.description) {
            case 'Add transition':
                return change.applyAddTransition(state);
            case 'Change transition position':
                return change.applyTransitionPositionChange(state);
            case 'Remove step':
                return change.applyRemoveTransitionConnected(state);
            case 'Remove transition':
                return change.applyRemoveTransition(state);
            case 'Insert new step into transition':
                return change.applyAddTransitionForInsertStep(state);
            case 'Create transitions for insert step':
                return change.applyAddTransitionForInsertStep(state);
            case 'Generate auto layout':
                return change.applyLayoutAutoGenerationForTransitions(state);
            default:
                return state;
        }
    },
    CHANGE_REVERT: (state, {payload: change}) => {
        switch (change.description) {
            case 'Add transition':
                return change.revertAddTransition(state);
            case 'Change transition position':
                return change.revertTransitionPositionChange(state);
            case 'Remove step':
                return change.revertRemoveTransitionConected(state);
            case 'Remove transition':
                return change.revertRemoveTransition(state);
            case 'Insert new step into transition':
                return change.revertAddTransitionForInsertStep(state);
            case 'Create transitions for insert step':
                return change.revertAddTransitionForInsertStep(state);
            case 'Generate auto layout':
                return change.revertLayoutAutoGenerationForTransitions(state);
            default:
                return state;
        }
    },
    TRANSITION_HIGHLIGHT: (state, action) => {
        if (_.isNil(action.payload)) {
            return _.mapValues(state, transition => ({...transition, highlighted: false}))
        }

        return _.mapValues(state, transition => transition.id === action.payload ? {
            ...transition,
            highlighted: true
        } : transition);
    },
    TRANSITIONS_INTERSECT: (state, action) => {
        let position = action.payload.position;
        let stepsLayout = action.payload.stepsLayout;
        let transitionsLayout = action.payload.transitionsLayout;
        let transitions = state;
        let intersectedTransition = null;
        let yStart, yEnd, xStart, xEnd;

        _.forEach(transitions, (transition) => {
            let points = calculateNativeTransitionPoints(stepsLayout[transition.startStepId], stepsLayout[transition.endStepId], transitionsLayout[transition.id]);
            _.forEach(points, (arrow, index) => {
                if (index === points.length - 1) {
                    yStart = points[index].y < points[index - 1].y ? points[index].y : points[index - 1].y;
                    yEnd = points[index].y < points[index - 1].y ? points[index - 1].y : points[index].y;
                    xStart = points[index].x < points[index - 1].x ? points[index].x : points[index - 1].x;
                    xEnd = points[index].x < points[index - 1].x ? points[index - 1].x : points[index].x;
                } else {
                    yStart = points[index].y < points[index + 1].y ? points[index].y : points[index + 1].y;
                    yEnd = points[index].y < points[index + 1].y ? points[index + 1].y : points[index].y;
                    xStart = points[index].x < points[index + 1].x ? points[index].x : points[index + 1].x;
                    xEnd = points[index].x < points[index + 1].x ? points[index + 1].x : points[index].x;
                }
                if ((Math.abs(yEnd - yStart) <= 5) && yStart - 2 <= position.y && position.y <= yStart + 2 &&
                    xStart - 2 <= position.x && position.x <= xEnd + 2) {
                    intersectedTransition = transition;
                } else if ((Math.abs(xEnd - xStart) <= 5) && xStart - 2 <= position.x && position.x <= xStart + 2 &&
                    yStart - 2 <= position.y && position.y <= yEnd + 2) {
                    intersectedTransition = transition;
                }
            });
        });
        if (_.isNull(intersectedTransition)) {
            _.map(transitions, transition => transition.intersected = false);
            return transitions
        } else {
            return {
                ...transitions, [intersectedTransition.id]: {
                    id: intersectedTransition.id,
                    startStepId: intersectedTransition.startStepId,
                    endStepId: intersectedTransition.endStepId,
                    selected: false,
                    highlighted: false,
                    intersected: true
                }
            }
        }
    }
}, initialState);

//- Utils

export const calculateNativeTransitionPoints = (startStepLayout, endStepLayout, transitionLayout) => {
    if (_.isNil(startStepLayout)) {
        return null;
    }

    let points = [];

    // Calculate start and end points
    let startPoint = calculateTransitionPoint(startStepLayout, transitionLayout.start.position);
    let endPoint = calculateTransitionPoint(endStepLayout, transitionLayout.end.position);
    let mainPoints;
    points.push(startPoint);

    switch (transitionLayout.start.position) {
        case 'right':
            switch (transitionLayout.end.position) {
                case 'top':
                    mainPoints = MainPoints.createRightTopPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'left':
                    mainPoints = MainPoints.createRightLeftPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'right':
                    mainPoints = MainPoints.createRightRightPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'bottom':
                    mainPoints = MainPoints.createRightBottomPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                default:
                    break;
            }
            break;
        case 'bottom':
            switch (transitionLayout.end.position) {
                case 'top':
                    mainPoints = MainPoints.createBottomTopPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'bottom':
                    mainPoints = MainPoints.createBottomBottomPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'right':
                    mainPoints = MainPoints.createBottomRightPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'left':
                    mainPoints = MainPoints.createBottomLeftPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                default:
                    break;
            }
            break;
        case 'left':
            switch (transitionLayout.end.position) {
                case 'top':
                    mainPoints = MainPoints.createLeftTopPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'bottom':
                    mainPoints = MainPoints.createLeftBottomPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'left':
                    mainPoints = MainPoints.createLeftLeftPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'right':
                    mainPoints = MainPoints.createLeftRightPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                default:
                    break;
            }
            break;
        case 'top':
            switch (transitionLayout.end.position) {
                case 'top':
                    mainPoints = MainPoints.createTopTopPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'left':
                    mainPoints = MainPoints.createTopLeftPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'bottom':
                    mainPoints = MainPoints.createTopBottomPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                case 'right':
                    mainPoints = MainPoints.createTopRightPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    break;
                default:
                    break;
            }
            break;
        default:
            break;
    }
    points.push(endPoint);
    return points;
};

//- Selectors
export const getTransitions = state => state.transitions;

export const getTransitionById = (state, id) =>
    _.find(getTransitions(state), transition => transition.id === id);

export const getTransitionIds = createSelector(
    getTransitions,
    transitions => Object.keys(transitions)
);

export const getSelectedTransition = createSelector(
    getTransitions,
    transitions => _.find(transitions, transition => transition.selected === true)
);

export const getSelectedTransitionId = createSelector(
    getSelectedTransition,
    selectedTransition => selectedTransition ? selectedTransition.id : null
);

export const getTransitionStartStepId = (transitions, transitionId) => {
    let transition = _.find(transitions, transition => transitionId === transition.id);
    return transition ? transition.startStepId : null;
};

export const getAvailableTransitionReports = (state, transitionId) => {
    let stepId = getTransitionStartStepId(getTransitions(state), transitionId);
    if (_.isNil(stepId)) {
        return null;
    }
    let availableReports = availableReportsByStartStepType[getStepTypeById(state, stepId)];
    return _.map(availableReports, report => ({
        label: <Translate id={translatedReportLabels[report]}/>,
        value: report
    }));
};

export const getAvailableTransitionTypes = (state, transitionId) => {
    let stepId = getTransitionStartStepId(getTransitions(state), transitionId);
    if (_.isNil(stepId)) {
        return null;
    }
    let availableTypes = availableTransitionTypesByStartStepType[getStepTypeById(state, stepId)];
    return _.map(availableTypes, report => ({label: typeLabels[report], value: report}));
};

export const getTransitionsByStartStepId = (state, startStepId) =>
    _.pickBy(getTransitions(state), transition => transition.startStepId === startStepId);

export const getTransitionsByEndStepId = (state, endStepId) =>
    _.pickBy(getTransitions(state), transition => transition.endStepId === endStepId);

export const isTransitionHighlighted = (state, id) => {
    let selectedStep = getSelectedStep(state);
    let transition = getTransitionById(state, id);
    return (!_.isNil(selectedStep) && !_.isNil(transition) && (selectedStep.id === transition.startStepId)) || (!_.isNil(transition) && transition.selected);
};

export const getStepDependenceFromTransitions = (state, id) => {
    let transitions = getTransitions(state);
    let isDependent = false;
    _.forEach(transitions, transition => {
        if (transition.startStepId === id || transition.endStepId === id) {
            isDependent = true
        }
    });
    return isDependent
};

export const getIdOfIntersectedTransition = (state) => {
    let intersectedTransition = _.find(getTransitions(state), transition => transition.intersected === true);
    if (!_.isUndefined(intersectedTransition)) {
        return intersectedTransition.id
    }
};

export const isExistImposedTransition = (state) => {
    return !_.isUndefined(_.find(getTransitions(state), transition => transition.intersected === true));
};