import React from 'react';
import PropTypes from 'prop-types';
import {translatedTransitionRadioButtonType} from "../../../../dictionary";
import './RadioButton.css';

class RadioButton extends React.Component {
    setRadioButton = (radioButton) => {
        this.radioButton = radioButton;
    };

    render() {
        let className = "radio-button-container" + (this.props.className ? " " + this.props.className : "")
            + (this.props.focused ? " focused " : " ")
            + (this.props.selectedOnOtherTransitions ? ' selected-on-other-transition' : ' ')
            + (this.props.disabled ? ' radio-button-container-disabled' : '');
        let labelClassName = this.props.disabled ? ' label-for-radio-button-container-disabled' : ' label-for-radio-button-container';
        let icon = this.props.value ? "radio_button_checked" : "radio_button_unchecked";
        return (
            <div ref={this.setRadioButton}
                 className={className}
                 aria-labelledby={`${this.props.groupLabelId || ''} radioButton-label-${this.props.label.replace(' ', '-')}`}
                 role='checkbox'
                 aria-checked={this.props.value}
                 tabIndex="0"
                 onKeyDown={this.handleKeyDown}
                 onClick={this.handleClick}>
                <i key={icon} className="material-icons">{icon}</i>
                <label id={`radioButton-label-${this.props.label.replace(' ', '-')}`} className={labelClassName}>
                    {this.props.translate(translatedTransitionRadioButtonType[this.props.label])}
                </label>
            </div>
        );
    }

    componentDidMount() {
        if (this.props.focused) {
            this.radioButton.focus();
        }
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.radioButton.focus();
        }
        if (prevProps.disabled !== this.props.disabled && this.props.disabled){
            this.props.onChange(false);
        }
    }

    handleClick = () => {
        if (!this.props.disabled) {
            this.props.onChange(!this.props.value);
        }
        this.radioButton.focus();
    };

    handleKeyDown = (e) => {
        if (!this.props.disabled) {
            if (e.which === 13 || e.keyCode === 13) { // enter
                this.props.onChange(!this.props.value);
            }
        }
    };
}

RadioButton.propTypes = {
    label: PropTypes.string.isRequired,
    value: PropTypes.bool.isRequired,
    className: PropTypes.string,
    focused: PropTypes.bool,
    groupLabelId: PropTypes.string,
    selectedOnOtherTransitions: PropTypes.bool,
    translate: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired
};

export default RadioButton;
