import {createAction, handleActions} from 'redux-actions';
import _ from 'lodash';

//- Actions
export const applyChange = createAction('CHANGE_APPLY', change => change);
export const revertChange = createAction('CHANGE_REVERT', change => change);

export const undo = () => (dispatch, getState) => {
    let undoStack = getUndoStack(getState());
    if(undoStack.length > 0) {
        dispatch(revertChange(undoStack[undoStack.length - 1]));
    }
};

export const redo = () => (dispatch, getState) => {
    let redoStack = getRedoStack(getState());
    if(redoStack.length > 0) {
        dispatch(applyChange(redoStack[redoStack.length - 1]));
    }
};

//- State
const initialState = {
    undoStack: [],
    redoStack: []
};

//- Reducer
export default handleActions({
    CHANGE_APPLY: (changeStack, {payload: change}) => {
        change.isRedo = true;
        let undoStack = [...changeStack.undoStack, change];
        let redoStack = [];
        if (change.isRedo) {
            redoStack = _.slice(changeStack.redoStack, 0, changeStack.redoStack.length - 1);
        }
        return {undoStack, redoStack};
    },
    CHANGE_REVERT: (changeStack, {payload: change}) => {
        let undoStack = _.slice(changeStack.undoStack, 0, changeStack.undoStack.length - 1);
        let redoStack = [...changeStack.redoStack, change];
        return {undoStack, redoStack};
    }
}, initialState);

//- Selectors
export const getUndoStack = state => state.changeStack.undoStack;
export const getRedoStack = state => state.changeStack.redoStack;