import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './ModalPdfOverview.css';
import CheckBox from '../checkBox/CheckBox';
import RadioButton from '../radioButton/RadioButton';
import Select from 'react-select';
import _ from 'lodash';

import {Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';

class ModalPdfOverview extends Component {
    constructor(props) {
        super(props);

        this.state = {
            highlightingProblems: this.props.details === 'mask_report' || this.props.details === 'layer_report'
        };
    }

    render() {
        return (
            <Modal className="process-plan-json-modal" isOpen={this.props.modalIsOpen}
                   toggle={this.toggleModal}
                   fade={this.props.modalIsOpenCount === 0}
                   role="dialog"
            >
                <ModalHeader toggle={this.toggleModal}/>
                <ModalBody className='modal-pdf-overview'>
                    <Select id={'modal-select'}
                            options={this.props.templateOptions}
                            value={_.isUndefined(this.props.template)
                                ? _.find(this.props.templateOptions, option => _.isEqual(option.value, ''))
                                : _.find(this.props.templateOptions, option => _.isEqual(option.value, this.props.template))}
                            ref={this.setSelect}
                            isDisabled={false}
                            onChange={this.handleTemplateOptionsChange}
                            onSelect={this.handleSelect}
                    />
                    <CheckBox id={'modal-CheckBox'}
                              value={this.state.highlightingProblems}
                              label={'Problems highlighting'}
                              groupLabelId={'Modal'}
                              focused={false}
                              disabled={false}
                              translate={this.props.translate}
                              onChange={this.highlightingCheckedChange}
                              onSelect={this.handleSelect}
                    />
                    <div className='details'>
                        <RadioButton
                            value={(this.props.details === 'mask_report' && this.state.highlightingProblems) || this.props.details === ''}
                            label={'Transparent masks'}
                            groupLabelId={'Modal'}
                            focused={false}
                            disabled={!this.state.highlightingProblems}
                            translate={this.props.translate}
                            onChange={this.maskCheckedChange}/>
                        <RadioButton value={this.props.details === 'layer_report' && this.state.highlightingProblems}
                                     label={'Layers'}
                                     groupLabelId={'Modal'}
                                     focused={false}
                                     disabled={!this.state.highlightingProblems}
                                     translate={this.props.translate}
                                     onChange={this.layerCheckedChange}/>
                    </div>
                </ModalBody>
                <ModalFooter/>
            </Modal>
        );
    }

    handleTemplateOptionsChange = (selectedOption) => {
        this.props.onPropertyChange(
            this.props.transitionId,
            {
                type: "overview_report",
                details: this.props.details,
                template: _.isUndefined(selectedOption) ? _.find(this.props.templateOptions, option => _.isEqual(option.value, '')).value : selectedOption.value
            });
        this.props.onSetModalCount();
    };

    highlightingCheckedChange = () => {
        if (this.state.highlightingProblems) {
            this.props.onPropertyChange(
                this.props.transitionId,
                {
                    type: "overview_report",
                    details: '',
                    template: this.props.template
                });
        } else {
            this.props.onPropertyChange(
                this.props.transitionId,
                {
                    type: "overview_report",
                    details: 'mask_report',
                    template: this.props.template
                });
        }
        this.props.onSetModalCount();
        this.setState({
            highlightingProblems: !this.state.highlightingProblems
        });
    };

    maskCheckedChange = () => {
        if (this.state.highlightingProblems && this.props.details !== 'mask_report') {
            this.props.onSetModalCount();
            this.props.onPropertyChange(
                this.props.transitionId,
                {
                    type: "overview_report",
                    details: 'mask_report',
                    template: this.props.template
                });
        }
    };

    layerCheckedChange = () => {
        if (this.state.highlightingProblems && this.props.details !== 'layer_report') {
            this.props.onSetModalCount();
            this.props.onPropertyChange(
                this.props.transitionId,
                {
                    type: "overview_report",
                    details: 'layer_report',
                    template: this.props.template
                });
        }
    };

    toggleModal = () => {
        this.props.onSetModal(!this.props.modalIsOpen);
    };

    handleSelect = (selectedType) => {
        this.setState({...this.state, selectedPropertyIndex: _.findIndex(this.properties, property => property === selectedType)});
    };
}

ModalPdfOverview.propTypes = {
    translate: PropTypes.func.isRequired,
    templateOptions: PropTypes.array.isRequired,
    transitionId: PropTypes.string.isRequired,
    details: PropTypes.string.isRequired,
    template: PropTypes.string,
    modalIsOpen: PropTypes.bool.isRequired,
    modalIsOpenCount: PropTypes.number,
    onPropertyChange: PropTypes.func.isRequired,
    onSetModal: PropTypes.func.isRequired,
    onSelectModal: PropTypes.func.isRequired,
    onCount: PropTypes.func.isRequired,
};

export default ModalPdfOverview;
