import {combineReducers} from 'redux';
import {combineEpics} from 'redux-observable';
import processPlan, {processplanSettingsLoadEpic} from './processPlan';
import availableObjects, {handleExternalObjectsUpdateEpic} from './availableObjects';
import steps from './steps';
import transitions from './transitions';
import transitionsLayout from './transitionsLayout';
import stepsLayout from './stepsLayout';
import stepProperties from './stepProperties'
import transitionProperties from './transitionProperties';
import tempTransition from './tempTransition';
import drawingArea from './drawingArea';
import changeStack from './changeStack';
import connectionsLayout from './connectionsLayout';
import transitionActionType from './transitionActionType';
import transitionCopy from './transitionCopy';
import {localizeReducer} from "react-localize-redux";

export default combineReducers({
    localize: localizeReducer,
    processPlan,
    steps,
    transitions,
    stepsLayout,
    transitionsLayout,
    availableObjects,
    stepProperties,
    transitionProperties,
    tempTransition,
    drawingArea,
    changeStack,
    connectionsLayout,
    transitionActionType,
    transitionCopy
});

export const rootEpic = combineEpics(
    processplanSettingsLoadEpic,
    handleExternalObjectsUpdateEpic
);