import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './StepButton.css';
import {getIcon, RECT_OFFSET} from '../../../drawingArea/step/Step';
import {HEADER_OFFSET} from "../../../App";
import _ from 'lodash';
import {translatedStepTypes} from "../../../../dictionary"

const START_SCREEN_POSITION = 0;

class StepButton extends Component {
    setStepButton = (stepButton) => {
        this.stepButton = stepButton;
    };

    render() {
        let className = 'step-button ' + this.props.type + (this.props.focused ? ' selected' : ' ') + (this.props.isLockedProcessPlan ? ' step-button-disabled ' : '');

        return (
            <div ref={this.setStepButton}
                 role='button'
                 tabIndex="0"
                 className={className}
                 draggable={!this.props.isLockedProcessPlan}
                 onDragStart={this.onDragStart}
                 onTouchStart={this.onDragTouchStart}
                 onTouchEnd={this.onDragEnd}>
                <svg x="0" y="0" width="30" height="30" viewBox="0 0 30 30">
                    {getIcon(this.props.type)}
                </svg>
                <div className='step-button-name'>
                    {this.props.translate(translatedStepTypes[this.props.type])}
                </div>
            </div>
        );
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.stepButton.focus();
        }
    }

    onDragStart = (ev) => {
        ev.dataTransfer.setData('text', JSON.stringify({
            offsetX: ev.nativeEvent.offsetX,
            offsetY: ev.nativeEvent.offsetY,
            stepType: this.props.type
        }));
    };
    onDragTouchStart = (ev) => {
        let rect = ev.target.getBoundingClientRect();
        this.offset = {
            x: ev.targetTouches[0].pageX - rect.left,
            y: ev.targetTouches[0].pageY - rect.top
        };
        document.addEventListener('touchmove', this.onDragTouchMove);
    };

    onDragTouchMove = (ev) => {
        this.touchPosition = {
            x: ev.targetTouches[0].pageX,
            y: ev.targetTouches[0].pageY
        };
    };

    onDragEnd = () => {
        if (!_.isUndefined(this.touchPosition)) {
            if (this.touchPosition.x < this.props.originalViewportSize.width &&
                this.touchPosition.x > START_SCREEN_POSITION &&
                this.touchPosition.y - HEADER_OFFSET < this.props.originalViewportSize.height &&
                this.touchPosition.y - HEADER_OFFSET > START_SCREEN_POSITION) {
                let position = {
                    x: this.touchPosition.x - this.offset.x - RECT_OFFSET,
                    y: this.touchPosition.y - this.offset.y - RECT_OFFSET - HEADER_OFFSET
                };
                this.props.onMobileDrop(this.props.type, position);
            }
        }
        document.removeEventListener('touchmove', this.onDragTouchMove);
    };
}

const PositionShape = PropTypes.shape({
    x: PropTypes.number.isRequired,
    y: PropTypes.number.isRequired
});

const SizeShape = PropTypes.shape({
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired
});

StepButton.propTypes = {
    type: PropTypes.string.isRequired,
    focused: PropTypes.bool.isRequired,
    originalViewportPosition: PositionShape.isRequired,
    originalViewportSize: SizeShape.isRequired,
    translate: PropTypes.func.isRequired,
    onMobileDrop: PropTypes.func
};

export default StepButton;

