import {connect} from 'react-redux'
import Transition, {CREATE_REPORT_TYPE_OFFSET} from './Transition'
import _ from 'lodash';
import * as MainPoints from "../../../redux/modules/transitionsLayout";
import {calculateTransitionPoint, getArrowLineType} from "../../../redux/modules/transitionsLayout";
import {
    highlightElement, isTransitionHighlighted, selectTransition, typeLabels
} from "../../../redux/modules/transitions";
import {translatedTransitionTypes} from "../../../dictionary"
import {dropStepIntoTransition, selectStep} from "../../../redux/modules/steps";
import {
    getScale, getSvgPosition, isTransitionsInfoShown, moveToAnotherTransitionFromSameStep, moveToAnotherTransitionToSameStep,
    selectAnotherElement, selectDrawingArea
} from "../../../redux/modules/drawingArea";
import {
    setUpdatingTransitionEndPointType, setUpdatingTransitionStartPointType
} from "../../../redux/modules/transitionActionType";
import {RECT_OFFSET} from "../step/Step";
import {DRAWING_AREA, getFocusedArea, setFocusOnGlobalArea} from "../../../redux/modules/processPlan";
import {applyChange} from "../../../redux/modules/changeStack";
import {RemoveTransitionChange} from "../../../redux/model/RemoveTransition";
import {startTransitionPointUpdate} from '../../../redux/modules/tempTransition';
import {isTransitionDisabled} from '../../../redux/modules/transitionProperties';
import {getTranslate} from 'react-localize-redux';
import uuidv4 from "uuid/v4";

export const ARROW_OFFSET = 10;
const TYPE_FONT_WIDTH = 7.5;
const LINE_HEIGHT = 17;
export const ICON_SIZE = 24;
const MIN_ICON_TEXT_LENGTH_TO_SET_OFFSET = 6;

export const calculateTransitionPoints = (startStepLayout, endStepLayout, transitionLayout) => {
    if (_.isNil(startStepLayout)) {
        return null;
    }

    let points = [];

    // Calculate start and end points
    let startPoint = calculateTransitionPoint(startStepLayout, transitionLayout.start.position);
    let endPoint = calculateTransitionPoint(endStepLayout, transitionLayout.end.position);
    let mainPoints;
    points.push(startPoint);

    switch (transitionLayout.start.position) {
        case 'right':
            switch (transitionLayout.end.position) {
                case 'top':
                    mainPoints = MainPoints.createRightTopPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x,
                        y: endPoint.y - ARROW_OFFSET,
                        position: endPoint.position
                    };
                    break;
                case 'left':
                    mainPoints = MainPoints.createRightLeftPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x - ARROW_OFFSET,
                        y: endPoint.y,
                        position: endPoint.position
                    };
                    break;
                case 'right':
                    mainPoints = MainPoints.createRightRightPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x + ARROW_OFFSET,
                        y: endPoint.y,
                        position: endPoint.position
                    };
                    break;
                case 'bottom':
                    mainPoints = MainPoints.createRightBottomPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x,
                        y: endPoint.y + ARROW_OFFSET,
                        position: endPoint.position
                    };
                    break;
                default:
                    break;
            }
            break;
        case 'bottom':
            switch (transitionLayout.end.position) {
                case 'top':
                    mainPoints = MainPoints.createBottomTopPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x,
                        y: endPoint.y - ARROW_OFFSET,
                        position: endPoint.position
                    };
                    break;
                case 'bottom':
                    mainPoints = MainPoints.createBottomBottomPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x,
                        y: endPoint.y + ARROW_OFFSET,
                        position: endPoint.position
                    };
                    break;
                case 'right':
                    mainPoints = MainPoints.createBottomRightPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x + ARROW_OFFSET,
                        y: endPoint.y,
                        position: endPoint.position
                    };
                    break;
                case 'left':
                    mainPoints = MainPoints.createBottomLeftPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x - ARROW_OFFSET,
                        y: endPoint.y,
                        position: endPoint.position
                    };
                    break;
                default:
                    break;
            }
            break;
        case 'left':
            switch (transitionLayout.end.position) {
                case 'top':
                    mainPoints = MainPoints.createLeftTopPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x,
                        y: endPoint.y - ARROW_OFFSET,
                        position: endPoint.position
                    };
                    break;
                case 'bottom':
                    mainPoints = MainPoints.createLeftBottomPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x,
                        y: endPoint.y + ARROW_OFFSET,
                        position: endPoint.position
                    };
                    break;
                case 'left':
                    mainPoints = MainPoints.createLeftLeftPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x - ARROW_OFFSET,
                        y: endPoint.y,
                        position: endPoint.position
                    };
                    break;
                case 'right':
                    mainPoints = MainPoints.createLeftRightPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x + ARROW_OFFSET,
                        y: endPoint.y,
                        position: endPoint.position
                    };
                    break;
                default:
                    break;
            }
            break;
        case 'top':
            switch (transitionLayout.end.position) {
                case 'top':
                    mainPoints = MainPoints.createTopTopPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x,
                        y: endPoint.y - ARROW_OFFSET,
                        position: endPoint.position
                    };
                    break;
                case 'left':
                    mainPoints = MainPoints.createTopLeftPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x - ARROW_OFFSET,
                        y: endPoint.y,
                        position: endPoint.position
                    };
                    break;
                case 'bottom':
                    mainPoints = MainPoints.createTopBottomPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x,
                        y: endPoint.y + ARROW_OFFSET,
                        position: endPoint.position
                    };
                    break;
                case 'right':
                    mainPoints = MainPoints.createTopRightPoints(startStepLayout, endStepLayout, startPoint, endPoint);
                    points = _.concat(points, mainPoints);
                    endPoint = {
                        x: endPoint.x + ARROW_OFFSET,
                        y: endPoint.y,
                        position: endPoint.position
                    };
                    break;
                default:
                    break;
            }
            break;
        default:
            break;
    }
    points.push(endPoint);
    return points;
};

const calculateTypesPoints = (transitionPoints, types, position) => {
    let typesPoints = [];

    let maxLength = Math.max(..._.map(types, type => type.length));

    let sign;
    let offset;

    if (position === 'left' || position === 'right') {
        if (position === 'left') {
            offset = -TYPE_FONT_WIDTH * maxLength;
        } else {
            offset = TYPE_FONT_WIDTH
        }

        if (transitionPoints.length === 2 || transitionPoints[0].y < transitionPoints[2].y) {
            for (let i = 0; i < types.length; i++) {
                typesPoints.push({
                    x: transitionPoints[0].x + offset,
                    y: transitionPoints[0].y - LINE_HEIGHT / 2 - i * LINE_HEIGHT
                });
            }
        }
        else {
            for (let i = 0; i < types.length; i++) {
                typesPoints.push({
                    x: transitionPoints[0].x + offset,
                    y: transitionPoints[0].y + LINE_HEIGHT + i * LINE_HEIGHT
                });
            }
        }
    } else if (position === 'top' || position === 'bottom') {
        if (position === 'top') {
            sign = -1;
        } else {
            sign = 1;
        }

        if (transitionPoints.length === 2 || transitionPoints[0].x > transitionPoints[2].x) {
            for (let i = 0; i < types.length; i++) {
                typesPoints.push({
                    x: transitionPoints[0].x + TYPE_FONT_WIDTH,
                    y: transitionPoints[0].y + sign * LINE_HEIGHT * (i + 1)
                });
            }
        }
        else {
            for (let i = 0; i < types.length; i++) {
                typesPoints.push({
                    x: transitionPoints[0].x - TYPE_FONT_WIDTH * (maxLength + 0.5),
                    y: transitionPoints[0].y + sign * LINE_HEIGHT * (i + 1)
                });
            }
        }
    }

    return typesPoints;
};

const calculateIconsPoints = (transitionPoints, icons, types, position) => {
    let points = [];

    let iconSizeX = ICON_SIZE;
    let createReportTextOffsetX;
    let createReportTextOffsetY;
    let maxLength = types.length !== 0 ? Math.max(..._.map(types, type => type.length)) : 0;

    let step;
    let sign;
    let offset;

    if (position === 'right' || position === 'left') {
        if (position === 'right') {
            step = 0;
            sign = 1;
            offset = 0;
        } else {
            step = 1;
            sign = -1;
            offset = iconSizeX;
        }

        if (Math.abs(transitionPoints[0].x - transitionPoints[1].x) > (iconSizeX + 2 * CREATE_REPORT_TYPE_OFFSET) * icons.length + RECT_OFFSET * (icons.length + 1)) {
            if (transitionPoints.length === 2 || (transitionPoints.length > 2 && transitionPoints[2].y > transitionPoints[0].y)) {
                for (let i = 0; i < icons.length; i++) {
                    createReportTextOffsetX = icons[i].name.startsWith('create_report') && icons[i].caption.length > MIN_ICON_TEXT_LENGTH_TO_SET_OFFSET ? CREATE_REPORT_TYPE_OFFSET : 0;
                    points.push({
                        x: transitionPoints[0].x + sign * (iconSizeX + createReportTextOffsetX + RECT_OFFSET) * (i + step) + sign * RECT_OFFSET,
                        y: transitionPoints[0].y + RECT_OFFSET
                    });
                }
            } else {
                for (let i = 0; i < icons.length; i++) {
                    createReportTextOffsetX = icons[i].name.startsWith('create_report') && icons[i].caption.length > MIN_ICON_TEXT_LENGTH_TO_SET_OFFSET ? CREATE_REPORT_TYPE_OFFSET : 0;
                    createReportTextOffsetY = icons[i].name.startsWith('create_report') && icons[i].caption.length > 0 ? CREATE_REPORT_TYPE_OFFSET : 0;
                    points.push({
                        x: transitionPoints[0].x + sign * (iconSizeX + RECT_OFFSET) * (i + step) + RECT_OFFSET + 2 * sign * createReportTextOffsetX,
                        y: transitionPoints[0].y - iconSizeX - RECT_OFFSET - createReportTextOffsetY
                    });
                }
            }
        } else {
            if (transitionPoints.length === 2 || (transitionPoints.length > 2 && transitionPoints[2].y > transitionPoints[0].y)) {
                for (let i = 0; i < icons.length; i++) {
                    createReportTextOffsetX = icons[i].name.startsWith('create_report') && icons[i].caption.length > MIN_ICON_TEXT_LENGTH_TO_SET_OFFSET ? CREATE_REPORT_TYPE_OFFSET : 0;
                    createReportTextOffsetY = icons[i].name.startsWith('create_report') && icons[i].caption.length > 0 ? CREATE_REPORT_TYPE_OFFSET : 0;
                    points.push({
                        x: transitionPoints[0].x + sign * 2 * RECT_OFFSET + sign * TYPE_FONT_WIDTH * maxLength + sign * (offset + createReportTextOffsetX),
                        y: transitionPoints[0].y - (i + 1) * (iconSizeX + RECT_OFFSET + createReportTextOffsetY)
                    });
                }
            } else {
                for (let i = 0; i < icons.length; i++) {
                    createReportTextOffsetX = icons[i].name.startsWith('create_report') && icons[i].caption.length > MIN_ICON_TEXT_LENGTH_TO_SET_OFFSET ? CREATE_REPORT_TYPE_OFFSET : 0;
                    points.push({
                        x: transitionPoints[0].x + sign * 2 * RECT_OFFSET + sign * TYPE_FONT_WIDTH * maxLength + sign * (offset + createReportTextOffsetX),
                        y: transitionPoints[0].y + i * (iconSizeX + RECT_OFFSET) + RECT_OFFSET
                    });
                }
            }
        }
    } else if (position === 'bottom' || position === 'top') {
        if (position === 'bottom') {
            sign = 1;
            offset = 0;
        } else {
            sign = -1;
            offset = iconSizeX
        }

        if (Math.abs(transitionPoints[0].y - transitionPoints[1].y) > iconSizeX + 2 * RECT_OFFSET) {
            if (transitionPoints.length === 2 || (transitionPoints.length > 2 && transitionPoints[0].x > transitionPoints[2].x)) {
                for (let i = 0; i < icons.length; i++) {
                    createReportTextOffsetX = icons[i].name.startsWith('create_report') && icons[i].caption.length > MIN_ICON_TEXT_LENGTH_TO_SET_OFFSET ? CREATE_REPORT_TYPE_OFFSET : 0;
                    createReportTextOffsetY = icons[i].name.startsWith('create_report') && icons[i].innerText.length === 0 && position === 'top' ? CREATE_REPORT_TYPE_OFFSET : 0;
                    points.push({
                        x: transitionPoints[0].x - (i + 1) * (iconSizeX + RECT_OFFSET) - createReportTextOffsetX - RECT_OFFSET,
                        y: transitionPoints[0].y + sign * RECT_OFFSET + sign * (offset + createReportTextOffsetY)
                    })
                }
            } else {
                for (let i = 0; i < icons.length; i++) {
                    createReportTextOffsetX = icons[i].name.startsWith('create_report') && icons[i].caption.length > MIN_ICON_TEXT_LENGTH_TO_SET_OFFSET ? CREATE_REPORT_TYPE_OFFSET : 0;
                    createReportTextOffsetY = icons[i].name.startsWith('create_report') && icons[i].innerText.length === 0 && position === 'top' ? CREATE_REPORT_TYPE_OFFSET : 0;
                    points.push({
                        x: transitionPoints[0].x + RECT_OFFSET + i * (iconSizeX + RECT_OFFSET) + 2 * createReportTextOffsetX,
                        y: transitionPoints[0].y + sign * RECT_OFFSET + sign * (offset + createReportTextOffsetY)
                    });
                }
            }
        } else {
            if (transitionPoints.length === 2 || (transitionPoints.length > 2 && transitionPoints[0].x > transitionPoints[2].x)) {
                for (let i = 0; i < icons.length; i++) {
                    createReportTextOffsetX = icons[i].name.startsWith('create_report') && icons[i].caption.length > MIN_ICON_TEXT_LENGTH_TO_SET_OFFSET ? CREATE_REPORT_TYPE_OFFSET : 0;
                    createReportTextOffsetY = icons[i].name.startsWith('create_report') && icons[i].innerText.length === 0 && position === 'top' ? CREATE_REPORT_TYPE_OFFSET : 0;
                    points.push({
                        x: transitionPoints[0].x + i * (iconSizeX + RECT_OFFSET) + RECT_OFFSET + TYPE_FONT_WIDTH * maxLength + 2 * createReportTextOffsetX,
                        y: transitionPoints[0].y + sign * RECT_OFFSET + sign * (offset + createReportTextOffsetY)
                    });
                }
            } else {
                for (let i = 0; i < icons.length; i++) {
                    createReportTextOffsetX = icons[i].name.startsWith('create_report') && icons[i].caption.length > MIN_ICON_TEXT_LENGTH_TO_SET_OFFSET ? CREATE_REPORT_TYPE_OFFSET : 0;
                    createReportTextOffsetY = icons[i].name.startsWith('create_report') && icons[i].innerText.length === 0 && position === 'top' ? CREATE_REPORT_TYPE_OFFSET : 0;
                    points.push({
                        x: transitionPoints[0].x - (i + 1) * (iconSizeX + RECT_OFFSET) - TYPE_FONT_WIDTH * maxLength - 2 * RECT_OFFSET - createReportTextOffsetX,
                        y: transitionPoints[0].y + sign * RECT_OFFSET + sign * (offset + createReportTextOffsetY)
                    });
                }
            }
        }
    }

    return points;
};

export const mapStateToProps = (state, {id, types, icons}) => {
    let transition = state.transitions[id];
    let transitionLayout = state.transitionsLayout[id];
    let typesToDisplay = _.chain(types).filter(type => type !== 'out').map(type => typeLabels[type]).value();
    let points = calculateTransitionPoints(state.stepsLayout[transition.startStepId], state.stepsLayout[transition.endStepId], transitionLayout);
    let translatedTypes = [];
    let translate = getTranslate(state.localize);
    if (typesToDisplay.length) {
        translatedTypes = typesToDisplay.map(type => translate(translatedTransitionTypes[type]));
    }
    //console.log('icons',icons);
    return {
        points: points,
        className: getArrowLineType(transition.startStepId, transition.endStepId),
        start: calculateTransitionPoint(state.stepsLayout[transition.startStepId], transitionLayout.start.position),
        end: calculateTransitionPoint(state.stepsLayout[transition.endStepId], transitionLayout.end.position),
        arrowType: transitionLayout.end.position,
        selected: transition.selected,
        intersected: transition.intersected,
        focused: transition.selected && !_.isNil(getFocusedArea(state)) && getFocusedArea(state) === DRAWING_AREA,
        scale: getScale(state),
        svgPosition: getSvgPosition(state),
        highlighted: transition.highlighted || isTransitionHighlighted(state, id),
        disabled: isTransitionDisabled(state, id),
        types: typesToDisplay,
        typesPoints: calculateTypesPoints(points, translatedTypes, transitionLayout.start.position),
        icons: icons,
        iconsPoints: calculateIconsPoints(points, icons, translatedTypes, transitionLayout.start.position),
        showTransitionInfo: isTransitionsInfoShown(state),
        translate
    }
};

const mapDispatchToProps = (dispatch, {id}) => {
    return {
        onTransitionSelect: () => {
            dispatch(selectTransition(id));
            dispatch(selectStep(null));
            dispatch(selectDrawingArea(null));
        },
        onStartTransitionPointUpdate: (coordinates, pointType) => {
            if (pointType === 'start') {
                dispatch(setUpdatingTransitionStartPointType());
            } else {
                dispatch(setUpdatingTransitionEndPointType());
            }
            dispatch(startTransitionPointUpdate(coordinates, id, pointType));
        },
        onTransitionHighlight: () => {
            dispatch(highlightElement(id, 'transition'));
        },
        onTransitionHighlightReset: () => {
            dispatch(highlightElement(null));
        },
        onRemoveTransition: () => {
            dispatch(applyChange(new RemoveTransitionChange(id)));
            dispatch(selectTransition(null));
            dispatch(selectDrawingArea(true));
            dispatch(setFocusOnGlobalArea(DRAWING_AREA));
        },
        onArrowKeyDown: (direction, ctrlKey) => {
            if (direction === 'right' || direction === 'left') {
                if (ctrlKey) {
                    dispatch(moveToAnotherTransitionToSameStep(direction));
                } else {
                    dispatch(moveToAnotherTransitionFromSameStep(direction));
                }
            } else {
                dispatch(selectAnotherElement(direction));
            }
        },
        onInsertStep: (type, position) => {
            let stepId = uuidv4();
            let newTransitionId = uuidv4();
            dispatch(dropStepIntoTransition(stepId, type, position, id, newTransitionId));
            dispatch(selectDrawingArea(true));
            dispatch(selectStep(stepId));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Transition)