import React, {Component} from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import ProcessPlanDetails from "./processPlanDetails/ProcessPlanDetails.container";
import VariableInput from "../sidePanel/propPanel/variableInput/VariableInput.container";
import api from "../../api";
import './HeaderPanel.css';

import {
    Button,
    Dropdown,
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
    Modal,
    ModalBody,
    ModalFooter,
    ModalHeader,
    Popover,
    PopoverBody
} from 'reactstrap';
import CodeMirror from 'react-codemirror';
import 'codemirror/mode/javascript/javascript';
import 'codemirror/lib/codemirror.css';
import 'codemirror/addon/fold/foldgutter.css';
import 'codemirror/addon/fold/foldgutter.js';
import 'codemirror/addon/fold/brace-fold.js';
import error from '../drawingArea/error_icon.svg';
const DUPLICATE_PROCESS_PLAN_NAME = 'NG_PPE_NameExistWarning_long';
const EMPTY_PROCESS_PLAN_NAME = 'NG_PPE_NameEmptyWarning_long';

class HeaderPanel extends Component {
    constructor(props) {
        super(props);

        this.state = {
            exportModalIsOpened: false,
            lockMenuIsOpened: false,
            lockWithPasswordModalIsOpened: false,
            passwordConfirmationPopoverOpen: false,
            unlockWithPasswordModalIsOpened: false,
            popoverOpen: false,
        };
    }

    setPassword = password => {
        this.password = password;
    };

    setPasswordLock = passwordLock => {
        this.passwordLock = passwordLock;
    };

    setPasswordConfirmation = passwordConfirmation => {
        this.passwordConfirmation = passwordConfirmation;
    };

    render() {
        if (!this.props.fetching) {
            return (
                <div className='header'
                     onMouseUp={this.headerMouseUp}>
                    <VariableInput
                        type="text"
                        aria-live="polite"
                        aria-label={this.props.translate("NG_PPE_Process_plan_name_long")}
                        label="variable-processPlan-name"
                        className="variable-processPlan-name"
                        value={this.props.name}
                        isLockedProcessPlan={this.props.isLockedProcessPlan}
                        onDrop={(ev) => ev.preventDefault()}
                        onChange={this.handleNameChange}
                        multiLine={false}
                    />
                    {this.renderValidationTooltip()}
                    {this.renderExportButton()}
                    {this.renderLockMenu()}
                    <span role="button"
                          title={this.props.translate("NG_PPE_More_details_long")}
                          aria-label={this.props.translate("NG_PPE_More_details_long")}
                          aria-expanded={!this.props.isHidden}
                          aria-controls="process-plan-details"
                          onClick={this.moreDetails}>
                        <i className="material-icons more_vert">more_vert</i>
                    </span>
                    {!this.props.isHidden && <ProcessPlanDetails isLockedProcessPlan={this.props.isLockedProcessPlan}/>}
                    {this.renderExportModal()}
                    {this.renderModalLockWithPassword()}
                    {this.renderModalUnlockWithPassword()}
                </div>
            );
        }
        else {
            return null;
        }
    }

    renderValidationTooltip = () => {
        if (this.props.validationTooltip) {
            let tooltipText = (this.props.name.value === '' ) ? this.props.translate(EMPTY_PROCESS_PLAN_NAME) : this.props.translate(DUPLICATE_PROCESS_PLAN_NAME);
            return (
                <span
                    className={'header-tooltip-toggle'}
                    data-tooltip={tooltipText}
                    role="tooltip"
                    tabIndex="0"
                    aria-label={tooltipText}>
                    <img
                        src={error}
                        width={25}
                        className={'header-error-icon'}
                        alt={tooltipText}
                    />
                </span>
            );
        }
    };

    renderExportButton = () => {
        if (!this.props.isExportAvailable) {
            return null;
        }
        return (
            <span role="button"
                  aria-label="Export process plan"
                  aria-controls="process-plan-export"
                  onClick={this.handleExportButtonClick}>
                <i className="material-icons save">vertical_align_bottom</i>
            </span>
        );
    };

    renderLockMenu = () => {
        let icon = this.props.locked ? 'lock' : 'lock_open';
        let title;
        if (this.props.locked) {
            title = this.props.hasPassword ?
                this.props.translate("NG_PPE_PasswordProtected_long") :
                this.props.translate("NG_PPE_Locked_long");
        } else {
            title = this.props.translate("NG_PPE_Unlocked_long");
        }
        return (
            <Dropdown isOpen={this.state.lockMenuIsOpened} toggle={this.changeLockType}>
                <DropdownToggle>
                    <i title={title} key={icon} className="material-icons lock">{icon}</i>
                </DropdownToggle>
                <DropdownMenu>
                    <DropdownItem
                        onClick={this.changeSimpleLockType}>{this.props.translate("NG_PPE_LockSimple_long")}</DropdownItem>
                    <DropdownItem
                        onClick={this.changePasswordProtectedLockTypeMenu}>{this.props.translate("NG_PPE_LockPassword_long")}</DropdownItem>
                </DropdownMenu>
            </Dropdown>
        )
    };

    renderExportModal = () => {
        return (
            <Modal className="process-plan-json-modal" isOpen={this.state.exportModalIsOpened}
                   toggle={this.toggleModal}>
                <ModalHeader toggle={this.toggleModal}>Process plan converted to json</ModalHeader>
                <ModalBody>
                    <CodeMirror className='code-mirror'
                                value={this.props.modalText}
                                options={{
                                    mode: 'application/json',
                                    lineNumbers: true,
                                    readOnly: true,
                                    foldGutter: true,
                                    gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter"]
                                }}
                                autoFocus={true}/>
                </ModalBody>
                <ModalFooter>
                    <Button color="primary" onClick={this.toggleModal}>Ok</Button>
                </ModalFooter>
            </Modal>
        )
    };

    renderModalLockWithPassword = () => {
        return (
            <Modal className="process-plan-json-modal" isOpen={this.state.lockWithPasswordModalIsOpened}
                   toggle={this.togglePasswordProtectedLockModal}>
                <ModalBody>
                    <div className="locking-body-password ">
                        <label id="password-enter-label" htmlFor="password-enter">
                            {this.props.translate("NG_PPE_PasswordEnter_long")}
                        </label>
                        <input type="password"
                               ref={this.setPasswordLock}
                               aria-labelledby="password-enter"
                               aria-live="off"
                               className="password"
                               onDrop={(ev) => ev.preventDefault()}
                        />
                    </div>
                    <div className="locking-body-password ">
                        <label id="password-confirmation-enter-label" htmlFor="password-confirmation-enter">
                            {this.props.translate("NG_PPE_PasswordConfirm_long")}
                        </label>
                        <input type="password"
                               ref={this.setPasswordConfirmation}
                               aria-labelledby="password-confirmation-enter"
                               aria-live="off"
                               className="password"
                               onDrop={(ev) => ev.preventDefault()}
                        />
                        {(!_.isUndefined(this.passwordConfirmation) && !_.isNil(this.passwordConfirmation) && !_.isUndefined(this.passwordLock) && !_.isNil(this.passwordLock)) ?
                            this.renderConfirmPopover(this.passwordConfirmation) : ''}
                    </div>
                </ModalBody>
                <ModalFooter>
                    <Button color="primary"
                            onClick={this.changePasswordProtectedLockType}>{this.props.translate("NG_PPE_ButtonOK_long")}</Button>
                    <Button color="primary"
                            onClick={this.togglePasswordProtectedLockModal}>{this.props.translate("NG_PPE_ButtonCancel_long")}</Button>
                </ModalFooter>
            </Modal>
        )
    };

    renderConfirmPopover = () => {
        return (
            <Popover placement="bottom" isOpen={this.state.passwordConfirmationPopoverOpen}
                     target={this.passwordConfirmation} toggle={this.toggle}>
                <PopoverBody>{this.props.translate("NG_PPE_PasswordInvalid_long")}</PopoverBody>
            </Popover>
        )
    };

    renderModalUnlockWithPassword = () => {
        if (!this.props.isLockedProcessPlan || !this.props.hasPassword) {
            return null;
        }

        return (
            <Modal className="process-plan-json-modal" isOpen={this.state.unlockWithPasswordModalIsOpened}
                   toggle={this.togglePasswordProtectedUnlockModal}>
                <ModalBody>
                    <div className="locking-body-password ">
                        <label id="password-enter-label" htmlFor="password-enter">
                            {this.props.translate("NG_PPE_PasswordEnter_long")}
                        </label>
                        <input type="password"
                               ref={this.setPassword}
                               aria-labelledby="password-enter"
                               aria-live="off"
                               className="password"
                               onDrop={(ev) => ev.preventDefault()}
                        />
                        {(!_.isUndefined(this.password) && !_.isNil(this.password)) ? this.renderPopover(this.password) : ''}
                    </div>
                </ModalBody>
                <ModalFooter>
                    <Button color="primary"
                            onClick={this.changePasswordProtectedUnlockType}>{this.props.translate("NG_PPE_ButtonOK_long")}</Button>
                    <Button color="primary"
                            onClick={this.togglePasswordProtectedUnlockModal}>{this.props.translate("NG_PPE_ButtonCancel_long")}</Button>
                </ModalFooter>
            </Modal>
        )
    };

    renderPopover = () => {
        return (
            <Popover placement="bottom" isOpen={this.state.popoverOpen} target={this.password} toggle={this.toggle}>
                <PopoverBody>{this.props.translate("NG_PPE_PasswordInvalid_long")}</PopoverBody>
            </Popover>
        )
    };

    handleNameChange = (name) => {
        this.props.onChangeProcessPlanName(name);
    };

    changeLockType = () => {
        this.props.isLockedProcessPlan
            ? this.props.hasPassword
            ? this.setState({unlockWithPasswordModalIsOpened: !this.state.unlockWithPasswordModalIsOpened})
            : this.changeSimpleUnlockType()
            : this.setState({lockMenuIsOpened: !this.state.lockMenuIsOpened});
    };

    moreDetails = (ev) => {
        ev.stopPropagation();
        this.props.isHideProcessPlanInfo(!this.props.isHidden);
    };

    toggleModal = () => {
        this.setState({exportModalIsOpened: !this.state.exportModalIsOpened});
    };

    togglePasswordProtectedLockModal = () => {
        this.setState({lockWithPasswordModalIsOpened: !this.state.lockWithPasswordModalIsOpened});
    };

    togglePasswordProtectedUnlockModal = () => {
        this.setState({unlockWithPasswordModalIsOpened: !this.state.unlockWithPasswordModalIsOpened});
        this.password.value = this.password.value ? "" : this.password.value = "";
    };

    changeSimpleUnlockType = () => {
        this.props.onChangeLockType(!this.props.locked);
    };

    changeSimpleLockType = () => {
        this.props.onChangeLockType(!this.props.locked);
        this.setState({lockMenuIsOpened: !this.state.lockMenuIsOpened});
        this.props.onChangePassword(false);
    };

    changePasswordProtectedLockTypeMenu = () => {
        this.setState({lockMenuIsOpened: false});
        this.setState({lockWithPasswordModalIsOpened: true});
    };

    changePasswordProtectedUnlockType = () => {
        api.checkPassword(this.password.value).then(isValidPassword => {
            if (isValidPassword) {
                this.props.onChangeLockType(!this.props.locked);
                this.setState({unlockWithPasswordModalIsOpened: !this.state.unlockWithPasswordModalIsOpened});
            } else {
                this.password.value = "";
                this.setState({popoverOpen: true});
            }
        })
    };

    changePasswordProtectedLockType = () => {
        if (this.passwordLock.value === this.passwordConfirmation.value) {
            api.setPassword(this.passwordLock.value);
            this.props.onChangePassword(true);
            this.setState({
                ...this.state,
                passwordConfirmationPopoverOpen: false,
                lockWithPasswordModalIsOpened: !this.state.lockWithPasswordModalIsOpened
            });
            this.props.onChangeLockType(!this.props.locked);
        } else {
            this.setState({passwordConfirmationPopoverOpen: true});
        }
        this.passwordLock.value = "";
        this.passwordConfirmation.value = "";
    };

    handleExportButtonClick = (e) => {
        e.stopPropagation();
        if (!this.props.isLockedProcessPlan) {
            this.closeFullscreen();
            this.props.onProcessPlanExport();
            this.toggleModal();
        }
    };

    closeFullscreen = () => {
        if (document.fullscreenElement && document.exitFullscreen) {
            document.exitFullscreen();
        } else if (document.mozFullScreenElement && document.mozCancelFullScreen) { /* Firefox */
            document.mozCancelFullScreen();
        } else if (document.webkitFullscreenElement && document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
            document.webkitExitFullscreen();
        } else if (document.msFullscreenElement && document.msExitFullscreen) { /* IE/Edge */
            document.msExitFullscreen();
        }
    };

    headerMouseUp = () => {
        if (this.props.isExistTempTransition) {
            this.props.destroyTempTransition();
        }
    };
}

HeaderPanel.propTypes = {
    name: PropTypes.object,
    locked: PropTypes.bool,
    hasPassword: PropTypes.bool,
    fetching: PropTypes.bool,
    isHidden: PropTypes.bool,
    isExportAvailable: PropTypes.bool,
    isExistTempTransition: PropTypes.bool.isRequired,
    validationTooltip: PropTypes.bool,
    translate: PropTypes.func.isRequired,
    onChangeProcessPlanName: PropTypes.func,
    onChangeLockType: PropTypes.func,
    onChangePassword: PropTypes.func,
    isHideProcessPlanInfo: PropTypes.func,
    destroyTempTransition: PropTypes.func
};

export default HeaderPanel;
