import {connect} from 'react-redux';
import ModalPdfOverview from "./ModalPdfOverview";
import {applyChange} from "../../../../redux/modules/changeStack";
import {ChangeTransitionProperty} from "../../../../redux/model/ChangeTransitionProperty";

const mapStateToProps = (state) => ({

});

const mapDispatchToProps = (dispatch, {onSelectModal, onCount, onSelect, id}) => ({
    onPropertyChange: (transitionId, value) => {
        dispatch(applyChange(new ChangeTransitionProperty(transitionId, 'pdf_overview_details', value)));
    },
    onSetModal: (modalIsOpened) => onSelectModal(modalIsOpened),
    onSetModalCount: () => onCount(),
    onSetSelection: () => onSelect(id)
});

export default connect(mapStateToProps,mapDispatchToProps)(ModalPdfOverview);