import React from 'react';
import PropTypes from 'prop-types';
import Variables from '../variableInput/variables/Variables.container';
import './VariableCheckBox.css';
import _ from 'lodash';
import {getVariableDisplayText} from '../../../../redux/modules/availableObjects';
import {translatedStepCheckboxLabels} from "../../../../dictionary"

class VariableCheckBox extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isActiveCheckBoxVariable: false
        };
    }
    setCheckBox = (checkBox) => {
        this.checkBox = checkBox;
    };

    render() {
        let className = "check-box-container" + (this.props.className ? " " + this.props.className : "")
            + (this.props.focused ? " focused " : " ")
            + (this.props.selectedOnOtherTransitions ? ' selected-on-other-transition' : ' ')
            + (this.props.disabled ? ' check-box-container-disabled' : '');
        let labelClassName = this.props.disabled ? ' label-for-check-box-container-disabled' : ' label-for-check-box-container';
        let icon;
        let isVariable = _.isPlainObject(this.props.value);
        if (this.props.selectedOnOtherTransitions) {
            icon = this.props.selectedOnOtherTransitions ? "indeterminate_check_box" : "check_box_outline_blank";
        } else {
            icon = isVariable ? "indeterminate_check_box" : (this.props.value ? "check_box" : "check_box_outline_blank");
        }
        let activeTooltip, tooltipText;
        if (isVariable) {
            activeTooltip = 'tooltip-toggle';
            tooltipText = getVariableDisplayText(this.props.value);
        }
        return (
            <div ref={this.setCheckBox}
                 className={className}
                 aria-labelledby={`${this.props.groupLabelId || ''} checkbox-label-${this.props.label.replace(' ', '-')}`}
                 role='checkbox'
                 aria-checked={this.props.value}
                 tabIndex="0"
                 onKeyDown={this.handleKeyDown}
                 onClick={this.handleClick}>
                <span
                    className={activeTooltip}
                    data-tooltip={tooltipText}
                    role="tooltip"
                    tabIndex="0"
                    aria-label={isVariable ? tooltipText : this.props.value}>
                    <i key={icon} className="material-icons">{icon}</i>
                </span>
                <span
                    onClick={this.handleClickCheckboxVariable}>
                    <i className="material-icons arrow_drop_down">arrow_drop_down</i>
                    <Variables className={`${this.props.label}-variables-group`}
                               id={`${this.props.label}-variables-group`}
                               isActive={this.state.isActiveCheckBoxVariable}
                               focused={this.state.isActiveCheckBoxVariable}
                               onSelect={this.handleVariableSelect}
                               onExitVariable={this.handleExitVariable}
                               onVariableCreated={this.handleVariableSelect}
                    />
                </span>
                <label id={`checkbox-label-${this.props.label.replace(' ', '-')}`} className={labelClassName}>
                    {this.props.translate(translatedStepCheckboxLabels[this.props.label])}
                </label>
            </div>
        );
    }

    componentDidMount() {
        if (this.props.focused) {
            this.checkBox.focus();
        }
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.checkBox.focus();
        }
        if(!prevProps.selected && this.props.selected){
            this.checkBox.focus();
        }
        if(prevProps.selected && !this.props.selected){
            this.checkBox.blur();
            this.setState({...this.state, isActiveCheckBoxVariable: false});
        }
    }

    handleClick = () => {
        if (!this.props.disabled) {
            this.props.onChange(!this.props.value);
            this.checkBox.focus();
            this.props.onSetSelection();
        }
    };

    handleKeyDown = (e) => {
        if (!this.props.disabled) {
            if (e.which === 13 || e.keyCode === 13) { // enter
                this.props.onChange(!this.props.value);
            }
        }
        switch (e.which) {
            case 32: //Space
                this.handleClickCheckboxVariable(e);
                break;
            default:
                break;
        }
    };

    handleClickCheckboxVariable = (e) => {
        e.stopPropagation();
        if (!this.props.disabled) {
            this.setState({isActiveCheckBoxVariable: !this.state.isActiveCheckBoxVariable});
            this.props.onSetSelection();
        }
    };

    handleVariableSelect = (newVariable) => {
        this.props.onChange(newVariable.id);
    };

    handleExitVariable = () => {
        this.setState({
            isActiveCheckBoxVariable: false
        });
        this.checkBox.focus();
    };
}

VariableCheckBox.propTypes = {
    id: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
    value: PropTypes.oneOfType([PropTypes.bool, PropTypes.object]),
    className: PropTypes.string,
    selected: PropTypes.bool.isRequired,
    focused: PropTypes.bool,
    groupLabelId: PropTypes.string,
    selectedOnOtherTransitions: PropTypes.bool,
    translate: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
    onSetSelection: PropTypes.func.isRequired
};

export default VariableCheckBox;


