import {connect} from 'react-redux'
import {getTranslate} from 'react-localize-redux';

import Transition from './Transition'
import {calculateTransitionPoint} from "../../../redux/modules/transitionsLayout";
import {getStepLayoutById} from "../../../redux/modules/stepsLayout";
import {calculateTransitionPoints} from "./Transition.container";
import {RECT_OFFSET} from "../step/Step";
import {startFilePickUpConnectionEndPointUpdate} from "../../../redux/modules/tempTransition";
import {setUpdatingConnectionType} from "../../../redux/modules/transitionActionType";
import {getScale, getSvgPosition, selectDrawingArea} from "../../../redux/modules/drawingArea";
import {selectTransition} from "../../../redux/modules/transitions";
import {selectStep} from "../../../redux/modules/steps";
import {RemoveConnection} from "../../../redux/model/RemoveConnection";
import {applyChange} from "../../../redux/modules/changeStack";
import {DRAWING_AREA, setFocusOnGlobalArea} from "../../../redux/modules/processPlan";

export const mapStateToProps = (state, {connection}) => {
    let startStepLayout = {
        position: {
            x: getStepLayoutById(state, connection.startStepId).position.x - RECT_OFFSET,
            y: getStepLayoutById(state, connection.startStepId).position.y
        }, size: {height: 0, width: 0}
    };

    let endStepLayout = {
        position: {
            x: getStepLayoutById(state, connection.endStepId).position.x - RECT_OFFSET,
            y: getStepLayoutById(state, connection.endStepId).position.y
        }, size: {height: 0, width: 0}
    };

    let startPosition = startStepLayout.position.x >= endStepLayout.position.x + endStepLayout.size.width/2 + RECT_OFFSET ? 'left': 'top';
    let endPosition = 'left';

    let tempTransitionLayout = {
        start: {position: startPosition},
        end: {position: endPosition}
    };

    return {
        points: calculateTransitionPoints(startStepLayout, endStepLayout, tempTransitionLayout),
        className: 'connection ',
        start: calculateTransitionPoint(startStepLayout, startPosition),
        end: calculateTransitionPoint(endStepLayout, endPosition),
        arrowType: null,
        selected: false,
        focused: false,
        highlighted: false,
        scale: getScale(state),
        svgPosition: getSvgPosition(state),
        showTransitionInfo: false,
        translate: getTranslate(state.localize)
    }
};


const mapDispatchToProps = (dispatch, {connection}) => {
    return {
        onStartTransitionPointUpdate: (coordinates, pointType) => {
            if(pointType === 'end'){
                dispatch(setUpdatingConnectionType());
                dispatch(startFilePickUpConnectionEndPointUpdate(coordinates, connection));
            }
        },
        onTransitionSelect: () => {
            // We do not select pickup connection, so just reset selection
            dispatch(selectTransition(null));
            dispatch(selectStep(null));
        },
        onRemoveTransition: () => {
            dispatch(applyChange(new RemoveConnection(connection.startStepId)));
            dispatch(selectDrawingArea(true));
            dispatch(setFocusOnGlobalArea(DRAWING_AREA));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Transition)