import {connect} from 'react-redux';
import StepPanel from "./StepPanel";
import {STEP_TYPES} from "../../../api/processPlanConversion";
import { selectStep} from "../../../redux/modules/steps";
import uuidv4 from "uuid/v4";
import {DRAWING_AREA, getFocusedArea, getAddFilesAvailability, setFocusOnGlobalArea, SIDE_PANEL} from "../../../redux/modules/processPlan";
import {applyChange} from "../../../redux/modules/changeStack";
import {AddStepChange} from "../../../redux/model/AddStep";

const mapStateToProps = (state) => {
    return {
        focused: getFocusedArea(state) === SIDE_PANEL,
        addFilesAvailability: getAddFilesAvailability(state)
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        onStepAdd: (index) => {
            let type = Object.values(STEP_TYPES)[index];
            if (type) {
                let id = uuidv4();
                dispatch(applyChange(new AddStepChange(id, type)));
                dispatch(selectStep(id));
                dispatch(setFocusOnGlobalArea(DRAWING_AREA));
            }
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(StepPanel)