import {Change} from './Change';
import {availableTransitionsByStepType} from "../../api/processPlanConversion";
import _ from 'lodash';
import uuidv4 from "uuid/v4";

export class RemoveStepChange extends Change {
    constructor(stepId, allTransitionProperties, allTransitionLayout, steps) {
        super('Remove step');
        this.stepId = stepId;

        this.step = null;
        this.stepLayout = null;

        this.deletedTransition = null;
        this.tempConectedLayout = null;

        this.transitionConnectedLayout = null;
        this.transitionProperties = null;
        this.transitionConnectedProperties = null;

        this.allTransitionProperties = allTransitionProperties;
        this.originProperties = allTransitionProperties;
        this.allTransitionLayout = allTransitionLayout;
        this.allSteps = steps;

        this.newTransitions = [];
        this.newTransitionsLayout = [];
        this.newTransitionsProperties = [];

        this.outgoingTransitionsProperties = [];
        this.incomingTransitionsProperties = [];

        this.outgoingTransitions = [];
        this.incomingTransitions = [];
    }

    applyRemoveStep(steps) {
        this.step = steps[this.stepId];
        return _.omitBy(steps, step => step.id === this.stepId);
    }

    revertRemoveStep(steps) {
        return {
            ...steps,
            [this.stepId]:
            this.step
        }
    };

    applyRemoveStepLayout(steps) {
        this.stepLayout = steps[this.stepId];
        return _.omitBy(steps, step => step === this.stepLayout);
    }

    revertRemoveStepLayout(steps) {
        return {
            ...steps,
            [this.stepId]:
            this.stepLayout
        }
    };

    applyRemoveStepConnectionLayout(steps) {
        if (!_.isEmpty(steps)) {
            this.tempConectedLayout = _.filter(steps, connectionLayout => connectionLayout.startStepId === this.stepId);
            return _.filter(steps, connectionLayout => connectionLayout.startStepId !== this.stepId);
        } else
            return steps;
    }

    revertRemoveStepConnectionLayout(steps) {
        if (!_.isEmpty(steps)) {
            return [...steps, this.tempConectedLayout];
        } else
            return steps;
    }

    applyRemoveStepProperty(stepProperties) {
        this.stepProperties = _.filter(stepProperties, property => property.value !== this.stepId);
        return _.filter(this.stepProperties, property => property.stepId !== this.stepId);
    }

    revertRemoveStepProperty(stepProperties) {
        return this.stepProperties;
    }

    applyRemoveTransitionConnected(transitions) {
        this.newTransitionsProperties = [];
        this.outgoingTransitionsProperties = [];
        this.incomingTransitionsProperties = [];
        let outgoingTransitionTypes = [];

        _.mapValues(transitions, transition => {
            if (transition.startStepId === this.stepId) {
                this.outgoingTransitions.push(transition);
                let outgoingTransitionsProperty = _.filter(this.allTransitionProperties, transitionProperty => transitionProperty.transitionId === transition.id);
                this.outgoingTransitionsProperties.push(...outgoingTransitionsProperty);
                outgoingTransitionTypes.push(..._.find(this.allTransitionProperties, transitionProperty => transitionProperty.transitionId === transition.id && transitionProperty.name === "types").value);
                this.deletedTransition = _.assign(this.deletedTransition, {[transition.id]: transition});
            } else if (transition.endStepId === this.stepId) {
                this.incomingTransitions.push(transition);
                let incomingTransitionsProperty = _.filter(this.allTransitionProperties, transitionProperty => transitionProperty.transitionId === transition.id);
                this.incomingTransitionsProperties.push(...incomingTransitionsProperty);
                this.deletedTransition = _.assign(this.deletedTransition, {[transition.id]: transition});
            }
        });

        _.map(this.incomingTransitionsProperties, (incomeProperty) => {
            let incomingValue = _.find(this.allTransitionProperties, transitionProperty => (transitionProperty.transitionId === incomeProperty.transitionId && transitionProperty.name === "types")).value;
            _.map(this.outgoingTransitionsProperties, (outgoingProperty) => {
                let outgoingValue = _.find(this.allTransitionProperties, transitionProperty => (transitionProperty.transitionId === outgoingProperty.transitionId && transitionProperty.name === "types")).value;
                let intersectedValues = _.intersection(incomingValue, outgoingValue);

                let incomingTransition = _.find(transitions, transition => transition.id === incomeProperty.transitionId);
                let outgoingTransition = _.find(transitions, transition => transition.id === outgoingProperty.transitionId);
                let originTransitions = _.filter(transitions, transition => transition.startStepId === incomingTransition.startStepId && transition.endStepId === outgoingTransition.endStepId);

                let isPossibleToTransferTypes = false;
                if (originTransitions.length > 0) {
                    _.forEach(originTransitions, (originTransition) => {
                        let isPossibleToTransferCertifyValue = false;
                        let isPossibleToTransferReportValues = false;

                        let originCertifyProperties = _.find(this.allTransitionProperties, transitionProperty =>
                            (transitionProperty.transitionId === originTransition.id && transitionProperty.name === "certify"));
                        let originReportProperties = _.find(this.allTransitionProperties, transitionProperty =>
                            (transitionProperty.transitionId === originTransition.id && transitionProperty.name === "create_report"));
                        let originTypesProperties = _.find(this.allTransitionProperties, transitionProperty =>
                            (transitionProperty.transitionId === originTransition.id && transitionProperty.name === "types")).value;

                        let incomingCertifyProperties = _.find(this.allTransitionProperties, transitionProperty =>
                            (transitionProperty.transitionId === incomingTransition.id && transitionProperty.name === "certify"));
                        let incomingReportProperties = _.find(this.allTransitionProperties, transitionProperty =>
                            (transitionProperty.transitionId === incomingTransition.id && transitionProperty.name === "create_report"));

                        isPossibleToTransferCertifyValue = _.isUndefined(originCertifyProperties) ? true
                            : (originCertifyProperties.value === incomingCertifyProperties.value);

                        if (_.isUndefined(originReportProperties)) {
                            isPossibleToTransferReportValues = true;
                        } else {
                            if (originReportProperties.value.checked === incomingReportProperties.value.checked) {
                                isPossibleToTransferReportValues = originReportProperties.value.checked === false ? true
                                    : _.difference(originReportProperties.value.values, incomingReportProperties.value.values).length === 0;
                            }
                        }
                        isPossibleToTransferTypes = (isPossibleToTransferCertifyValue === true && isPossibleToTransferReportValues === true);
                        if (isPossibleToTransferTypes) {
                            let deletedStepType = this.allSteps[this.stepId].type;
                            let differenceInTypes = _.difference(incomingValue, availableTransitionsByStepType[deletedStepType]);

                            this.allTransitionProperties = _.map(this.allTransitionProperties, property => {
                                if (property.transitionId === originTransition.id && property.name === "types" && !_.includes([...originTypesProperties], ...intersectedValues) && !_.includes([...originTypesProperties], ...differenceInTypes)) {
                                    let transferringValues = differenceInTypes.length > 0 ? [...originTypesProperties, ...intersectedValues, ...differenceInTypes] : [...originTypesProperties, ...intersectedValues];
                                    return {
                                        ...property,
                                        value: transferringValues
                                    }
                                }
                                return property
                            });
                            incomingValue = _.difference(incomingValue, differenceInTypes);
                        }
                    });
                }


                if (!isPossibleToTransferTypes) {
                    let startStepId = _.find(transitions, transition => transition.id === incomeProperty.transitionId).startStepId;
                    let endStepId = _.find(transitions, transition => transition.id === outgoingProperty.transitionId).endStepId;

                    let newModifiedTransition = _.find(this.newTransitions, newTransition =>
                        (newTransition.startStepId === startStepId && newTransition.endStepId === endStepId));
                    let abilityToTransferCertify;
                    let abilityToTransferReports;
                    if(!_.isUndefined(newModifiedTransition)){
                        let incomingCertify = _.find(this.allTransitionProperties, property => (property.transitionId === incomeProperty.transitionId &&
                            property.name === 'certify'));

                        let incomingReports = _.find(this.allTransitionProperties, property => (property.transitionId === incomeProperty.transitionId &&
                            property.name === 'create_report'));

                        let modifiedCertify = _.find(this.newTransitionsProperties, property => (property.transitionId === newModifiedTransition.id &&
                            property.name === 'certify'));

                        let modifiedReports = _.find(this.newTransitionsProperties, property => (property.transitionId === newModifiedTransition.id &&
                            property.name === 'create_report'));

                        abilityToTransferCertify = ((_.isUndefined(incomingCertify) && _.isUndefined(modifiedCertify)) ||
                            (!_.isUndefined(incomingCertify) && !_.isUndefined(modifiedCertify) && incomingCertify.value === modifiedCertify.value));

                        abilityToTransferReports = ((_.isUndefined(incomingReports) && _.isUndefined(modifiedReports)) ||
                            (!_.isUndefined(incomingReports) && !_.isUndefined(modifiedReports) && incomingReports.value.checked === modifiedReports.value.checked &&
                            _.isEqual(incomingReports.value.values, modifiedReports.value.values)));
                    }else{
                        abilityToTransferCertify = false;
                        abilityToTransferReports = false;
                    }

                    if (_.size(this.newTransitions) > 0 && !_.isUndefined(newModifiedTransition) && abilityToTransferCertify &&
                        abilityToTransferReports
                    ) {
                        if (this.allTransitionLayout[incomeProperty.transitionId].start.position === this.allTransitionLayout[outgoingProperty.transitionId].start.position &&
                            this.allTransitionLayout[incomeProperty.transitionId].end.position === this.allTransitionLayout[outgoingProperty.transitionId].end.position) {

                            this.newTransitionsLayout = {
                                ...this.newTransitionsLayout,
                                [newModifiedTransition.id]: {
                                    start: this.allTransitionLayout[incomeProperty.transitionId].start,
                                    end: this.allTransitionLayout[outgoingProperty.transitionId].end
                                }
                            };
                        }
                        if (incomeProperty.name === outgoingProperty.name && outgoingProperty.name === 'types') {
                            this.newTransitionsProperties = _.map(this.newTransitionsProperties, newTransitionsProperty => {
                                if (newTransitionsProperty.transitionId === newModifiedTransition.id && newTransitionsProperty.name === 'types' &&
                                    _.difference(intersectedValues, newTransitionsProperty.value).length > 0) {
                                    return {
                                        transitionId: newTransitionsProperty.transitionId,
                                        name: "types",
                                        value: _.concat(newTransitionsProperty.value, _.difference(intersectedValues, newTransitionsProperty.value))
                                    }
                                } else {
                                    return newTransitionsProperty
                                }
                            });
                        }
                    } else if (startStepId === "start") {
                        let outId = "out";
                        this.newTransitions = {
                            ...this.newTransitions,
                            [outId]: {
                                id: outId,
                                startStepId: "start",
                                endStepId: endStepId,
                                selected: false,
                                highlighted: false,
                                intersected: false
                            }
                        };

                        this.newTransitionsLayout = {
                            ...this.newTransitionsLayout,
                            [outId]: {
                                start: this.allTransitionLayout[incomeProperty.transitionId].start,
                                end: this.allTransitionLayout[outgoingProperty.transitionId].end
                            }
                        };

                        this.newTransitionsProperties.push({
                            transitionId: outId,
                            name: "types",
                            value: intersectedValues
                        });
                    } else {

                        let startStepType = this.allSteps[startStepId].type;
                        let deletedStepType = this.allSteps[this.stepId].type;

                        if (startStepType !== deletedStepType && outgoingTransitionTypes.length > 0) {
                            let differenceBetweenTypes = _.difference(incomingValue, outgoingTransitionTypes);
                            if (differenceBetweenTypes.length > 0) {
                                intersectedValues.push(...differenceBetweenTypes);
                                outgoingTransitionTypes = [];
                            }
                        }

                        if (intersectedValues.length > 0 && startStepId !== endStepId) {

                            let currentNewTransition = _.find(this.newTransitions, newTransition => (newTransition.startStepId === startStepId && newTransition.endStepId === endStepId));
                            let currentTransitionId;
                            if (_.isUndefined(currentNewTransition) || !abilityToTransferCertify ||
                                !abilityToTransferReports) {
                                currentTransitionId = uuidv4();
                                this.newTransitions = {
                                    ...this.newTransitions,
                                    [currentTransitionId]: {
                                        id: currentTransitionId,
                                        startStepId: startStepId,
                                        endStepId: endStepId,
                                        selected: false,
                                        highlighted: false,
                                        intersected: false,
                                    }
                                };

                                this.newTransitionsLayout = {
                                    ...this.newTransitionsLayout,
                                    [currentTransitionId]: {
                                        start: this.allTransitionLayout[incomeProperty.transitionId].start,
                                        end: this.allTransitionLayout[outgoingProperty.transitionId].end
                                    }
                                };
                            } else {
                                currentTransitionId = currentNewTransition.id;
                            }

                            this.newTransitionsProperties.push({
                                transitionId: currentTransitionId,
                                name: "types",
                                value: intersectedValues
                            });

                            if (!_.isUndefined(_.find(this.allTransitionProperties, property => (property.transitionId === incomeProperty.transitionId &&
                                    property.name === 'certify')))) {
                                this.newTransitionsProperties.push({
                                    transitionId: currentTransitionId,
                                    name: 'certify',
                                    value: _.find(this.allTransitionProperties, property => (property.transitionId === incomeProperty.transitionId &&
                                        property.name === 'certify')).value
                                });
                            }
                            if (!_.isUndefined(_.find(this.allTransitionProperties, property => (property.transitionId === incomeProperty.transitionId &&
                                    property.name === 'create_report')))) {
                                let newIncomeProperty = _.find(this.allTransitionProperties, property => (property.transitionId === incomeProperty.transitionId &&
                                    property.name === 'create_report'));
                                let certifyValue = newIncomeProperty.value.checked ? newIncomeProperty.value.values : [];
                                this.newTransitionsProperties.push({
                                    transitionId: currentTransitionId,
                                    name: 'create_report',
                                    value: {checked: newIncomeProperty.value.checked, values: certifyValue}
                                });
                            }
                        }
                    }
                }
            })
        });
        let currentTransition = _.omitBy(transitions, transition => transition.startStepId === this.stepId || transition.endStepId === this.stepId);
        return {...currentTransition, ...this.newTransitions};
    }

    revertRemoveTransitionConected(transitions) {
        transitions = _.pickBy(transitions, (transition, key) => _.isUndefined(this.newTransitions[key]));
        _.assign(transitions, this.deletedTransition);
        return {...transitions};
    }

    applyRemoveTransitionConnectedProperties(transitionProperties) {
        let tempTransitionProperties = this.allTransitionProperties;
        _.mapValues(this.allTransitionProperties, transitionProperty => {
            _.mapValues(this.deletedTransition, (value, key) => {
                if (key === transitionProperty.transitionId) {
                    this.transitionConnectedProperties = _.isNull(this.transitionConnectedProperties) ? {...transitionProperty} : _.concat(this.transitionConnectedProperties, {...transitionProperty});
                    tempTransitionProperties = _.filter(tempTransitionProperties, transitionProperty => key !== transitionProperty.transitionId);
                }
            })
        });
        return [...tempTransitionProperties, ...this.newTransitionsProperties];
    }

    revertRemoveTransitionConnectedProperties(transitionProperties) {
        return this.originProperties
    }

    applyRemoveTransitionConnectionLayout(transitionsLayout) {
        let tempTransitionsLayout = transitionsLayout;
        _.mapValues(tempTransitionsLayout, (value, key) => {
            let tempKey = key;
            let tempValue = value;
            _.mapValues(this.deletedTransition, (value, key) => {
                if (key === tempKey) {
                    this.transitionConnectedLayout = _.assign(this.transitionConnectedLayout, {[key]: {...tempValue}});
                    tempTransitionsLayout = _.omitBy(tempTransitionsLayout, (value, key) => key === tempKey);
                }
            })
        });
        return _.merge(tempTransitionsLayout, this.newTransitionsLayout);
    }

    revertRemoveTransitionConnectionLayout(transitionsLayout) {
        let delTransitionConnectionLayout = this.transitionConnectedLayout;
        transitionsLayout = _.pickBy(transitionsLayout, (transitionLayout, key) => _.isUndefined(this.newTransitionsLayout[key]));
        return _.assign(transitionsLayout, {...delTransitionConnectionLayout});
    }
}