import React, {Component} from 'react';
import PropTypes from 'prop-types';

import './Input.css'

class Input extends Component {
    setInput = (input) => {
        this.input = input;
    };

    render() {
        let className = 'input ' + (this.props.className || ' ');
        return (
            <div className={className}
                 tabIndex="0"
                 onClick={this.handleClick}>
                <label id={`input-label-${this.props.label}`}>{this.props.label}</label>
                <input ref={this.setInput}
                       aria-live="polite"
                       aria-labelledby={`input-label-${this.props.label}`}
                       value={this.props.value}
                       disabled={this.props.disabled ? 'disabled' : ''}
                       onChange={this.handleChange}/>
            </div>
        );
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.input.focus();
        }
    };

    handleChange = (event) => {
        if (!this.props.disabled){
            this.props.onChange(event.target.value);
        }
    };

    handleClick = () => {
        this.input.focus();
        this.props.onSetSelection();
    };
}

Input.propTypes = {
    id: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
    value: PropTypes.string,
    className: PropTypes.string,
    focused: PropTypes.bool,
    onChange: PropTypes.func.isRequired,
    onSetSelection: PropTypes.func.isRequired
};

export default Input;