import {Change} from './Change';

export class ChangeProcessPlanComment extends Change {
    constructor(comment) {
        super('Change process plan comment');
        this.comment = comment;
        this.prevComment = null;
    }

    applyProcessPlanCommentChange(processPlan) {
        this.prevComment = processPlan.comment;
        return {...processPlan, comment: this.comment};
    }
    revertProcessPlanCommentChange(processPlan) {
        return {...processPlan, comment: this.prevComment};
    }
}