import {connect} from 'react-redux';
import _ from 'lodash';

import App from './App';
import {dropStep, getSelectedStep, selectStep, removeStep} from "../redux/modules/steps";
import {getSelectedTransition, getSelectedTransitionId, selectTransition} from "../redux/modules/transitions";
import {continueTransitionCreation, endTransitionCreation, getTempTransition} from "../redux/modules/tempTransition";
import {
    DRAWING_AREA, getProcessPlanAccess, isNeedToUpdateLayout, setFocusOnGlobalArea, showProcessPlanInfo, updateLayout
} from "../redux/modules/processPlan";
import {
    changeScreenSize, getSelectedStepId, getSnapToGrid, getTranslations, isTransitionsInfoShown, selectDrawingArea,
    toggleSnapToGrid, toggleTransitionInfo, updateOriginalViewportSize, updateViewportSize
} from "../redux/modules/drawingArea";
import {applyChange, getRedoStack, getUndoStack, redo, undo} from "../redux/modules/changeStack";
import uuidv4 from "uuid/v4";
import {RemoveTransitionChange} from "../redux/model/RemoveTransition";
import {GenerateLayout} from "../redux/model/AutoGenerateLayout";

export const mapStateToProps = (state) => {
    return ({
        selectedObjectType: !_.isNil(getSelectedStepId(state)) ? 'step' : (!_.isNil(getSelectedTransitionId(state)) ? 'transition' : null),
        selectedStep: getSelectedStep(state),
        selectedTransition: getSelectedTransition(state),
        showTransitionInfo: isTransitionsInfoShown(state),
        snapToGrid: getSnapToGrid(state),
        translations: getTranslations(state),
        isAvailableUndo: getUndoStack(state).length > 0,
        isAvailableRedo: getRedoStack(state).length > 0,
        isLockedProcessPlan: getProcessPlanAccess(state),
        isExistTempTransition: !_.isEmpty(getTempTransition(state))
    });
};

const mapDispatchToProps = (dispatch, {isLockedProcessPlan}) => {
    return {
        onDrop: (type, position) => {
            let id = uuidv4();
            dispatch(dropStep(id, type, position));
            dispatch(selectDrawingArea(true));
            dispatch(selectStep(id));
        },
        onMainRender: (viewPortSize) => {
            dispatch(updateViewportSize(viewPortSize));
            dispatch(updateOriginalViewportSize(viewPortSize));
        },
        onMouseMove: (coordinates) => dispatch(continueTransitionCreation(coordinates)),
        onMouseUp: () => dispatch(endTransitionCreation(null)),
        onClick: () => dispatch(showProcessPlanInfo(true)),
        onTransitionInfoButtonClick: () => dispatch(toggleTransitionInfo()),
        onFocusOnGlobalArea: globalArea => dispatch(setFocusOnGlobalArea(globalArea)),
        onSelectDrawingArea: () => dispatch(selectDrawingArea(true)),
        onSnapToGridButtonClick: () => {
            if (!isLockedProcessPlan) dispatch(toggleSnapToGrid())
        },
        onUndo: () => {
            if (!isLockedProcessPlan) dispatch(undo())
        },
        onRedo: () => {
            if (!isLockedProcessPlan) dispatch(redo())
        },
        onDeleteStep: (id) => {
            if (id !== 'start' && id !== 'end') {
                dispatch(removeStep(id));
                dispatch(selectStep(null));

                dispatch(selectDrawingArea(true));
                dispatch(setFocusOnGlobalArea(DRAWING_AREA));
            }
        },
        onDeleteTransition: (id) => {
            dispatch(applyChange(new RemoveTransitionChange(id)));
            dispatch(selectTransition(null));
            dispatch(selectDrawingArea(true));
            dispatch(setFocusOnGlobalArea(DRAWING_AREA));
        },
        onUpdateLayout: () => {
            if (dispatch(isNeedToUpdateLayout())) {
                dispatch(applyChange(new GenerateLayout(dispatch(updateLayout()))))
            }
        },
        onUpdateScreenSize: () => {
            dispatch(changeScreenSize())
        },
        destroyTempTransition: () => {
            dispatch(endTransitionCreation(null))
        }
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(App);