import {connect} from 'react-redux';
import _ from 'lodash';
import {getVariableById} from '../../../../redux/modules/availableObjects';
import VariableCheckBox from './VariableCheckBox';

const mapStateToProps = (state, {value, onSelect , id}) => ({
    value: _.isString(value) ? getVariableById(state, value) : value,
    onSetSelection: () => onSelect(id)
});

export default connect(mapStateToProps)(VariableCheckBox);