import {Change} from './Change';

export class ChangeProcessPlanDialog extends Change {
    constructor(dialog) {
        super('Change process plan dialog');
        this.dialog = dialog;
        this.prevDialog = null;
    }

    applyProcessPlanDialogChange(processPlan) {
        this.prevDialog = processPlan.dialog;
        return {...processPlan, dialog: this.dialog};
    }
    revertProcessPlanDialogChange(processPlan) {
        return {...processPlan, dialog: this.prevDialog};
    }
}
