import {handleActions, createAction} from 'redux-actions';
import {createSelector} from 'reselect'

export const CREATING_TRANSITION = 'creating-transition';
export const UPDATING_TRANSITION_END_POINT = 'updating-transition-end-point';
export const UPDATING_TRANSITION_START_POINT = 'updating-transition-start-point';
export const CREATING_CONNECTION = 'creating-connection';
export const UPDATING_CONNECTION = 'updating-connection';
export const CREATING_TRANSITION_BY_SHORTCUTS = 'updating-transition-by-shortcuts';

//- Actions
export const setCreatingTransitionByShortcutsType = createAction("CREATING_TRANSITION_BY_SHORTCUTS_TYPE_SET");
export const setCreatingTransitionType = createAction("CREATING_TRANSITION_TYPE_SET");
export const setUpdatingTransitionEndPointType = createAction("UPDATING_TRANSITION_END_POINT_TYPE_SET");
export const setUpdatingTransitionStartPointType = createAction("UPDATING_TRANSITION_START_POINT_TYPE_SET");
export const setCreatingConnectionType = createAction("CREATING_CONNECTION_TYPE_SET");
export const setUpdatingConnectionType = createAction("UPDATING_CONNECTION_TYPE_SET");
export const resetActionType = createAction("ACTION_TYPE_RESET");

//- State
const initialState = {
    actionType: null
};

//- Reducers
export default handleActions({
    CREATING_TRANSITION_TYPE_SET: () => {
        return {actionType: CREATING_TRANSITION};
    },
    UPDATING_TRANSITION_END_POINT_TYPE_SET: () => {
        return {actionType: UPDATING_TRANSITION_END_POINT};
    },
    UPDATING_TRANSITION_START_POINT_TYPE_SET: () => {
        return {actionType: UPDATING_TRANSITION_START_POINT};
    },
    CREATING_CONNECTION_TYPE_SET: () => {
        return {actionType: CREATING_CONNECTION};
    },
    UPDATING_CONNECTION_TYPE_SET: () => {
        return {actionType: UPDATING_CONNECTION};
    },
    ACTION_TYPE_RESET: () => {
        return {actionType: null};
    },
    CREATING_TRANSITION_BY_SHORTCUTS_TYPE_SET: () => {
        return {actionType: CREATING_TRANSITION_BY_SHORTCUTS};
    }
}, initialState);

//- Selectors
export const getActionType = state => state.transitionActionType.actionType;

export const modifyingConnection = createSelector(
    getActionType,
    actionType => actionType === CREATING_CONNECTION || actionType === UPDATING_CONNECTION
);

export const modifyingTransition = createSelector(
    getActionType,
    actionType => actionType === CREATING_TRANSITION || actionType === UPDATING_TRANSITION_START_POINT || actionType === UPDATING_TRANSITION_END_POINT || actionType === CREATING_TRANSITION_BY_SHORTCUTS
);

export const modifyingTransitionByMouse = createSelector(
    getActionType,
    actionType => actionType === CREATING_TRANSITION || actionType === UPDATING_TRANSITION_START_POINT || actionType === UPDATING_TRANSITION_END_POINT
);