import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './ProcessPlanDetails.css';
import {Creatable } from 'react-select';
import VariableInput from "../../sidePanel/propPanel/variableInput/VariableInput.container";
import _ from 'lodash';
import {Translate} from 'react-localize-redux';

class ProcessPlanDetails extends Component {
    constructor(props) {
        super(props);

        let selectedGroupOption = undefined;
        let groupOptions = props.groups;

        if (props.group) {
            selectedGroupOption = props.groups.find(group => group.value === props.group);
            if (!selectedGroupOption) {
                selectedGroupOption = {label: props.group, value: _.snakeCase(props.group)};
                groupOptions.push(selectedGroupOption);
            }
        }

        let selectedDialogOption = undefined;
        let dialogOptions = props.dialogs;

        if (props.dialog) {
            selectedDialogOption = props.dialogs.find(dialog => dialog.value === props.dialog);
            if (!selectedDialogOption) {
                selectedDialogOption = {label: props.dialog, value: _.snakeCase(props.dialog)};
                dialogOptions.push(selectedDialogOption);
            }
        }

        this.state = {
            selectedGroupOption: selectedGroupOption,
            groupOptions: groupOptions,
            selectedDialogOption: selectedDialogOption,
            dialogOptions: dialogOptions
        };
    }
    render() {
        return (
                <div className='process-plan-details' id='process-plan-details' onClick={this.handleClick}>
                    <div>
                        <label id="process-plan-details-group-label" >
                            <Translate id="NG_PPE_Group_long"/>
                        </label>
                        <Creatable
                            className={this.props.isLockedProcessPlan ? "disabled-process-plan-details" : ""}
                            value={this.state.selectedGroupOption}
                            onChange={this.handleGroupChange}
                            options={this.state.groupOptions}
                            isValidNewOption={this.isValidNewGroupOption}
                            isDisabled = {this.props.isLockedProcessPlan ? "isDisabled" : ""}
                            styles={{ menu: provided => ({ ...provided, zIndex: 100 })}}
                        />
                    </div>
                    <div>
                        <label id="process-plan-details-dialog-label" >
                            <Translate id="NG_PPE_Dialog_template_long"/>
                        </label>
                        <Creatable
                            className={this.props.isLockedProcessPlan ? "disabled-process-plan-details" : ""}
                            value={this.state.selectedDialogOption}
                            onChange={this.handleDialogChange}
                            options={this.state.dialogOptions}
                            isValidNewOption={this.isValidNewDialogOption}
                            isDisabled = {this.props.isLockedProcessPlan ? "isDisabled" : ""}
                            styles={{ menu: provided => ({ ...provided, zIndex: 100 })}}
                        />

                    </div>
                    <div>
                        <label id="process-plan-details-author-label" htmlFor="process-plan-details-author">
                            <Translate id="NG_PPE_Author_long"/>
                        </label>
                        <input id="process-plan-details-author"
                               className={this.props.isLockedProcessPlan ? "disabled-process-plan-details" : ""}
                               aria-labelledby="process-plan-details-author-label"
                               type="text"
                               value={this.props.author}
                               onDrop={(ev) => ev.preventDefault()}
                               onChange={this.handleAuthorChange}
                               disabled = {this.props.isLockedProcessPlan ? "disabled" : ""}/>
                    </div>
                    <div>
                        <label id="process-plan-details-email-label" htmlFor="process-plan-details-email">
                            <Translate id="NG_PPE_Email_long"/>
                        </label>
                        <input id="process-plan-details-email"
                               className={this.props.isLockedProcessPlan ? "disabled-process-plan-details" : ""}
                               aria-labelledby="process-plan-details-email-label"
                               type="text"
                               value={this.props.email}
                               disabled = {this.props.isLockedProcessPlan ? "disabled" : ""}
                               onDrop={(ev) => ev.preventDefault()}
                               onChange={this.handleEmailChange} />
                    </div>
                    <div>
                        <VariableInput
                            className={"variable-comment" +(this.props.isLockedProcessPlan ? " disabled-process-plan-details" : "")}
                            aria-labelledby="process-plan-details-comment"
                            label={this.props.translate("NG_PPE_Comment_long")}
                            value={this.props.comment}
                            isLockedProcessPlan={this.props.isLockedProcessPlan}
                            onDrop={(ev) => ev.preventDefault()}
                            onChange={this.handleCommentChange}
                            multiLine={true}
                        />
                    </div>
                </div>
        );
    }
    handleClick =(ev) => {
        // Avoid panel hiding
        ev.stopPropagation();
    };
    handleAuthorChange = (ev) => {
        this.props.onChangeAuthor(ev.target.value);
    };
    handleEmailChange = (ev) => {
        this.props.onChangeEmail(ev.target.value);
    };
    handleCommentChange = (comment) => {
        this.props.onChangeComment(comment);
    };
    handleGroupChange = (option, { action }) => {
        switch(action) {
            case 'select-option':
                this.setState({ selectedGroupOption: option });
                break;
            case 'create-option':
                const { groupOptions } = this.state;
                groupOptions.push(option);
                this.setState({ selectedGroupOption: option, groupOptions });
                break;
            default:
                break;
        }
        this.props.onChangeGroup(option.value);
    };
    handleDialogChange = (option, { action }) => {
        switch(action) {
            case 'select-option':
                this.setState({ selectedDialogOption: option });
                break;
            case 'create-option':
                const { dialogOptions } = this.state;
                dialogOptions.push(option);
                this.setState({ selectedDialogOption: option, dialogOptions });
                break;
            default:
                break;
        }
        this.props.onChangeDialog(option.value);
    };
    isValidNewGroupOption = (inputValue, selectValue, selectGroupOptions) => {
        return !(inputValue.trim().length === 0 || selectGroupOptions.find(option => option.name === inputValue));
    };
    isValidNewDialogOption = (inputValue, selectValue, selectDialogOptions) => {
        return !(inputValue.trim().length === 0 || selectDialogOptions.find(option => option.name === inputValue));
    };
}

ProcessPlanDetails.propTypes = {
    group: PropTypes.string,
    groups: PropTypes.array,
    dialog: PropTypes.string,
    dialogs: PropTypes.array,
    author: PropTypes.string,
    email: PropTypes.string,
    comment: PropTypes.object,
    translate: PropTypes.func.isRequired,
    onChangeAuthor: PropTypes.func,
    onChangeEmail: PropTypes.func,
    onChangePassword: PropTypes.func,
    onChangeComment: PropTypes.func,
    onChangeGroup: PropTypes.func,
    onChangeDialog: PropTypes.func
};

export default ProcessPlanDetails;
