import {Change} from './Change';
import _ from 'lodash';

export class GenerateLayout extends Change {
    constructor(plans) {
        super('Generate auto layout');
        this.processPlan = plans.newProcessPlan;
        this.prevProcessPlan = plans.currentProcessPlan;
    }

    applyLayoutAutoGenerationForSteps(){
        return this.processPlan.steps;
    }
    revertLayoutAutoGenerationForSteps() {
        return this.prevProcessPlan.steps;
    }

    applyLayoutAutoGenerationForStepsLayout(){
        return this.processPlan.stepsLayout;
    }
    revertLayoutAutoGenerationForStepsLayout() {
        return this.prevProcessPlan.stepsLayout;
    }

    applyProcessPlanLayoutAutoGenerationStepProperties(){
        return this.processPlan.stepProperties;
    }
    revertProcessPlanLayoutAutoGenerationStepProperties() {
        return this.prevProcessPlan.stepProperties;
    }

    applyLayoutAutoGenerationForTransitions(){
        return this.processPlan.transitions;
    }
    revertLayoutAutoGenerationForTransitions() {
        return this.prevProcessPlan.transitions;
    }

    applyLayoutAutoGenerationForTransitionsLayout(){
        return this.processPlan.transitionsLayout;
    }
    revertAutoGenerationForTransitionsLayout() {
        return this.prevProcessPlan.transitionsLayout;
    }

    applyLayoutAutoGenerationForTransitionProperties(){
        return this.processPlan.transitionProperties;
    }
    revertLayoutAutoGenerationForTransitionProperties() {
        return this.prevProcessPlan.transitionProperties;
    }

    applyLayoutAutoGenerationForConnections(){
        return _.chain(this.processPlan.stepProperties)
            .filter(property => property.name === 'step_id')
            .map(property => ({startStepId: property.stepId, endStepId: property.value}))
            .map(connection => ({
                ...connection,
                endPosition: null}))
            .value();
    }
    revertLayoutAutoGenerationForConnections() {
        return this.prevProcessPlan.connectionsLayout;
    }
}