import {Change} from './Change';
import _ from 'lodash';
import {availableTransitionsByStepType, postProcessingOptionsByStartStepType} from "../../api/processPlanConversion";

export class AddTransitionForInsertStep extends Change {
    constructor(stepId, stepType, baseTransitionId, newTransitionId) {
        super('Create transitions for insert step');
        this.stepId = stepId;
        this.stepType = stepType;
        this.baseTransitionId = baseTransitionId;
        this.newTransitionId = newTransitionId;
    }

    applyAddTransitionForInsertStep(transitions) {
        this.prevTransition = _.find(transitions, transition => transition.id === this.baseTransitionId);
        return {
            ...transitions,
            [this.baseTransitionId]: {
                id: this.baseTransitionId,
                startStepId: this.prevTransition.startStepId,
                endStepId: this.stepId,
                selected: false,
                highlighted: false,
                intersected: false
            },
            [this.newTransitionId]: {
                id: this.newTransitionId,
                startStepId: this.stepId,
                endStepId: this.prevTransition.endStepId,
                selected: false,
                highlighted: false,
                intersected: false
            }
        }
    }

    revertAddTransitionForInsertStep(transitions) {
        transitions = _.omit(transitions, [this.newTransitionId]);
        return {
            ...transitions,
            [this.baseTransitionId]: {
                id: this.baseTransitionId,
                startStepId: this.prevTransition.startStepId,
                endStepId: this.prevTransition.endStepId,
                selected: false,
                highlighted: false,
                intersected: false
            }
        }
    }

    applyAddTransitionLayoutForInsertStep(transitionsLayout) {
        let prevTransitionLayout = _.find(transitionsLayout, (layout, transitionId) => transitionId === this.baseTransitionId);
        return {
            ...transitionsLayout, [this.newTransitionId]: {
                start: {position: prevTransitionLayout.start.position},
                end: {position: prevTransitionLayout.end.position},
            }
        }
    }

    revertAddTransitionLayoutForInsertStep(transitionsLayout) {
        return _.omit(transitionsLayout, [this.newTransitionId])
    }

    applyAddTransitionPropertiesForInsertStep(transitionProperties) {
        let properties = [...transitionProperties];
        properties.push({
            transitionId: this.newTransitionId,
            name: 'types',
            value: availableTransitionsByStepType[this.stepType]
        });

        _.forEach(postProcessingOptionsByStartStepType[this.stepType], option => {
            switch (option) {
                case 'certify':
                    properties.push({transitionId: this.newTransitionId, name: 'certify', value: false});
                    break;
                case 'create_report':
                    properties.push({
                        transitionId: this.newTransitionId,
                        name: 'create_report',
                        value: {checked: false, values: []}
                    });
                    break;
                default:
                    break;
            }
        });
        return properties
    }

    revertAddTransitionPropertiesForInsertStep(transitionProperties) {
        return _.filter(transitionProperties, transitionProperty => transitionProperty.transitionId !== this.newTransitionId)
    }

    applyInsertStepForAnchors (steps){
        return _.mapValues(steps, step => {
            return {...step, intersectedAnchors : []}
        })
    }

    revertInsertStepForAnchors (steps){
        return steps
    }
}