import React, {Component} from 'react';
import PropTypes from 'prop-types';
import VariableItem from './variableItem/VariableItem.container';
import {FixedSizeList} from 'react-window';
import AutoSizer from "react-virtualized-auto-sizer";

import './Variables.css'

class Variables extends Component {
    constructor(props) {
        super(props);
        this.state = {selectedVariableItemIndex: -1};
    }

    setVariablesList = (variable) => {
        this.variablesList = variable;
    };

    setFixedVariablesList = (fixedVariableList) => {
        this.fixedSizeList = fixedVariableList
    };
    
    render() {
        if (!this.props.isActive) {
            return null;
        }
        const Row = ({ index, style }) => (
            <div style={style}>
                <VariableItem
                    key={`${this.props.variables[index].id}-Item`}
                    style={style}
                    variable={this.props.variables[index]}
                    position={this.props.position}
                    inputValue={this.props.inputValue}
                    inputType={this.props.inputType}
                    focused={this.props.variables[this.state.selectedVariableItemIndex] === this.props.variables[index] && this.props.focused}
                    translate={this.props.translate}
                    onSelect={this.props.onSelect}
                    onVariableUpdated={this.props.onSelect}
                    onVariableDuplicated={this.props.onSelect}
                />
            </div>
        );
        return (
            <ul ref={this.setVariablesList}
                className="variables"
                tabIndex="0"
                onKeyDown={this.handleKeyDown}>
                <li className = {`variable-item  ${(this.state.selectedVariableItemIndex === -1 ? 'selected-variable-item' : '')}`}
                    onClick={this.props.onCreate}>
                    <div className="variable-value">{this.props.translate("NG_PPE_VariableCreate_long")}</div>
                </li>
                <AutoSizer>
                    {({ height, width }) => (
                    <FixedSizeList
                        height={height -30}
                        width={width}
                        itemSize={30}
                        itemCount={this.props.variables.length}
                        ref={this.setFixedVariablesList}
                    >
                        {Row}
                    </FixedSizeList>
                    )}
                </AutoSizer>
            </ul>
        );
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.setState({...this.state, selectedVariableItemIndex: -1});
            this.variablesList.focus();
        }
    }

    handleKeyDown = (e) => {
        e.stopPropagation();
        switch (e.key) {
            case 'ArrowUp':
                e.preventDefault();
                if (this.state.selectedVariableItemIndex >= 0) {
                    this.setState({...this.state, selectedVariableItemIndex: this.state.selectedVariableItemIndex - 1});
                    this.fixedSizeList.scrollToItem(this.state.selectedVariableItemIndex -1);
                } else {
                    this.props.onExitVariable(e);
                }
                break;
            case 'ArrowDown':
                e.preventDefault();
                if (this.state.selectedVariableItemIndex < this.props.variables.length - 1) {
                    this.setState({...this.state, selectedVariableItemIndex: this.state.selectedVariableItemIndex + 1});
                    this.fixedSizeList.scrollToItem(this.state.selectedVariableItemIndex + 1);
                }
                break;
            case 'Enter':
                e.preventDefault();
                this.state.selectedVariableItemIndex === -1 ? this.props.onCreate() : this.props.onSelect(this.props.variables[this.state.selectedVariableItemIndex]);
                this.props.onExitVariable(e);
                break;
            case 'Escape':
                this.props.onExitVariable(e);
                break;
            default:
                break;
        }
    };
}

Variables.propTypes = {
    variables: PropTypes.array,
    isActive: PropTypes.bool,
    position: PropTypes.number,
    inputValue: PropTypes.string,
    inputType: PropTypes.string,
    focused: PropTypes.bool,
    translate: PropTypes.func.isRequired,
    onSelect: PropTypes.func.isRequired,
    onExitVariable: PropTypes.func.isRequired,
    onCreate: PropTypes.func.isRequired
};

export default Variables;
