import {connect} from 'react-redux';
import CheckableMultiSelect from './CheckableMultiSelect';
import {getReportTemplatesOptions} from '../../../../redux/modules/availableObjects';

const mapStateToProps = (state) => ({
    templateOptions: getReportTemplatesOptions(state)
});

const mapDispatchToProps = (dispatch, {onSelect, id}) => ({
    onSetSelection: () => onSelect(id)
});

export default connect(mapStateToProps, mapDispatchToProps)(CheckableMultiSelect);