import {Change} from './Change';

export class ChangeProcessPlanName extends Change {
    constructor(name) {
        super('Change process plan name');
        this.name = name;
        this.prevName = null;
    }

    applyProcessPlanNameChange(processPlan) {
        this.prevName = processPlan.name;
        return {...processPlan, name: this.name};
    }
    revertProcessPlanNameChange(processPlan) {
        return {...processPlan, name: this.prevName};
    }
}