import React, {Component} from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import './Select.css';
import {transltedGroupStepProperties} from "../../../../dictionary"
import WindowedSelect from 'react-windowed-select';

class Select extends Component {
    setSelect = (select) => {
        this.select = select;
    };

    render() {
        let className = 'select-container ' + (this.props.className || ' ');
        let selectedOption = _.find(this.props.options, ['value', this.props.value]);
        return (
            <div className={className}
                 tabIndex="0"
                 onKeyDown={this.handleKeyDown}
                 onClick={this.handleClick}>
                <label>{this.props.translate(transltedGroupStepProperties[this.props.label])} </label>
                <WindowedSelect value={selectedOption}
                                options={this.props.options}
                                clearable={false}
                                isDisabled={this.props.disabled}
                                onChange={this.handleChange}
                                onOpen={this.handleSelectOpen}
                                onClose={this.handleSelectClose}
                                ref={this.setSelect}
                                components={this.getComponents()}
                />
            </div>
        );
    }

    getComponents = () => ({
        Option: props => {
            const {
                innerProps: {innerRef, ...innerProps}
            } = props;
            return (
                <div
                    className={`singe-option ${props.isFocused ? "focused-option" : ""} ${props.isSelected ? "selected-option" : ""}`}
                    {...innerProps}
                    ref={innerRef}
                    title={props.label}
                >
                    {props.label}
                </div>
            );
        }
    });

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.select.focus();
        }
        if(!prevProps.selected && this.props.selected){
            this.variableInput.focus();
        }

        if(prevProps.selected && !this.props.selected){
            this.variableInput.blur();
            this.setState({...this.state, isActiveVariable: false});
        }
    }

    handleChange = selectedOption => {
        selectedOption ? this.props.onChange(selectedOption.value) : this.props.onChange(null);
    };

    handleKeyDown = (e) => {
        if ((e.key === 'ArrowUp' || e.key === 'ArrowDown') && this.openedSelect) {
            e.stopPropagation();
        }
    };

    handleSelectOpen = () => {
        this.openedSelect = true;
    };

    handleSelectClose = () => {
        this.openedSelect = false;
    };

    handleClick = () =>{
        if(!_.isUndefined(this.props.id)){
            this.props.onSetSelection()
        }
    };
}

const OptionShape = PropTypes.shape({
    label: PropTypes.string.isRequired,
    value: PropTypes.string.isRequired
});

Select.propTypes = {
    id: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
    value: PropTypes.string,
    options: PropTypes.arrayOf(OptionShape),
    className: PropTypes.string,
    focused: PropTypes.bool,
    translate: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
    onSetSelection: PropTypes.func.isRequired
};

export default Select;
