import React, {Component} from 'react';
import {STEP_TYPES} from "../../../api/processPlanConversion";
import SidePanel from '../SidePanel';
import StepButton from './stepButton/StepButton.container';
import './StepPanel.css';
import PropTypes from "prop-types";
import _ from 'lodash';

class StepPanel extends Component {
    constructor(props) {
        super(props);

        this.state = {selectedStepButtonIndex: 0};
    }

    setStepPanel = (stepPanel) => {
        this.stepPanel = stepPanel;
    };

    render() {
        return (
            <div className='step-panel' ref={this.setStepPanel} tabIndex="0" onKeyDown={this.handleKeyDown}>
                <SidePanel>
                    {this.renderStepButtons()}
                </SidePanel>
            </div>
        );
    }

    renderStepButtons = () => {
        return _.chain(Object.values(STEP_TYPES))
            .filter(stepType => (stepType !== 'addfiles' || (stepType === 'addfiles' && this.props.addFilesAvailability)))
            .map(stepType =>
                <StepButton
                    focused={Object.values(STEP_TYPES)[this.state.selectedStepButtonIndex] === stepType && this.props.focused}
                    type={stepType}
                    key={stepType}
                    isLockedProcessPlan={this.props.isLockedProcessPlan}/>
            )
            .value()
    };

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.setState({...this.state, selectedStepButtonIndex: 0});
        }
    }

    handleKeyDown = (e) => {
        switch (e.key) {
            case 'ArrowUp':
                if (this.state.selectedStepButtonIndex > 0) {
                    this.setState({...this.state, selectedStepButtonIndex: this.state.selectedStepButtonIndex - 1});
                }
                break;
            case 'ArrowDown':
                if (this.state.selectedStepButtonIndex < Object.values(STEP_TYPES).length - 1) {
                    this.setState({...this.state, selectedStepButtonIndex: this.state.selectedStepButtonIndex + 1});
                }
                break;
            case 'Enter':
                if (!this.props.isLockedProcessPlan) {
                    this.props.onStepAdd(this.state.selectedStepButtonIndex);
                }
                break;
            default:
                break;
        }
    };
}

StepPanel.propTypes = {
    focused: PropTypes.bool.isRequired,
    addFilesAvailability: PropTypes.bool.isRequired,
    onStepAdd: PropTypes.func.isRequired
};
export default StepPanel;
