import {connect} from 'react-redux';
import {getAllStepIds, getAllStepsValidationTooltips, selectStep} from '../../redux/modules/steps';
import { getSelectedTransitionId, getTransitionIds,
    selectTransition
} from '../../redux/modules/transitions';
import {DRAWING_AREA, getFocusedArea, isProcessPlanLoaded} from "../../redux/modules/processPlan";
import {isAvailableObjectsLoaded} from "../../redux/modules/availableObjects";
import {updateViewportPosition, updateViewportSize, updateOriginalViewportSize} from "../../redux/modules/drawingArea";

import {selectDrawingArea, moveToAnotherGroup, getSelectedStepId} from "../../redux/modules/drawingArea";

import DrawingArea from './DrawingArea';
import {
    continueTransitionCreation, transitionCreationInProgress,
    transitionCreationStarted
} from "../../redux/modules/tempTransition";

import {getAllFilePickupConnections} from '../../redux/modules/compositeSelectors';
import {
    modifyingConnection, modifyingTransition, modifyingTransitionByMouse
} from "../../redux/modules/transitionActionType";
import {
    getAllTransitionIcons, getAllTransitionsPropertiesValidationTooltips,
    getAllTransitionTypes
} from "../../redux/modules/transitionProperties";

import _ from 'lodash';

const mapStateToProps = (state) => {
    let drawingThisArea = state.drawingArea;

    return {
        steps: getAllStepIds(state),
        transitions: getTransitionIds(state),
        transitionsTypes: getAllTransitionTypes(state),
        transitionsIcons: getAllTransitionIcons(state),
        filePickupConnections: getAllFilePickupConnections(state),
        fetching: !(isProcessPlanLoaded(state) && isAvailableObjectsLoaded(state)),
        modifyingTransition: modifyingTransition(state),
        modifyingConnection: modifyingConnection(state),
        modifyingTransitionByMouse: modifyingTransitionByMouse(state),
        transitionCreationInProgress: transitionCreationInProgress(state),
        transitionCreationStarted: transitionCreationStarted(state),
        scale: drawingThisArea.scale,
        viewportSize: drawingThisArea.viewportSize,
        viewportPosition: drawingThisArea.viewportPosition,
        selected: drawingThisArea.selected,
        focused: getFocusedArea(state) === DRAWING_AREA && _.isNil(getSelectedStepId(state)) && _.isNil(getSelectedTransitionId(state)),
        transitionsValidationTooltips: getAllTransitionsPropertiesValidationTooltips(state),
        stepsValidationTooltips: getAllStepsValidationTooltips(state)
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        onUpdateTempTransition: (coordinates) => {
            dispatch(continueTransitionCreation(coordinates))
        },
        onDrawingAreaSelect: () => {
            dispatch(selectDrawingArea(true));
            dispatch(selectStep(null));
            dispatch(selectTransition(null));
        },
        onMoveViewport: (svgPosition) => {
            dispatch(updateViewportPosition(svgPosition));
        },
        onBrowserChangeViewport:(viewPortSize) =>{
            dispatch(updateViewportSize(viewPortSize));
            dispatch(updateOriginalViewportSize(viewPortSize));
        },
        onMoveToAnotherGroup: () => {
            dispatch(moveToAnotherGroup());
        },
        onResetSelection: () => {
            dispatch(selectTransition(null));
            dispatch(selectStep(null));
            dispatch(selectDrawingArea(true));
        },
        onArrowKeyDown: () => {
            dispatch(selectStep('start'));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(DrawingArea)