import {Change} from './Change';
import _ from 'lodash';

export class RemoveTransitionChange extends Change {
    constructor(transitionId) {
        super('Remove transition');
        this.transitionId = transitionId;

        this.transition = null;
        this.transitionsLayout = null;
        this.deletedtransitionProperties = null;
    }

    applyRemoveTransition(transitions) {
        this.transition = transitions[this.transitionId];
        return _.omitBy(transitions, transition => transition.id === this.transitionId);
    }
    applyRemoveTransitionLayout(transitions) {
        this.transitionsLayout = transitions[this.transitionId];
        return _.omitBy(transitions, transition => transition === this.transitionsLayout);
    }

    applyRemoveTransitionProperty(transitionProperties) {
        this.deletedtransitionProperties = _.filter(transitionProperties, property => property.transitionId === this.transitionId);
        return _.filter(transitionProperties, property => property.transitionId !== this.transitionId);
    }

    revertRemoveTransition(transitions) {
        return {...transitions,
            [this.transitionId]:
            this.transition
        }
    };
    revertRemoveTransitionLayout(transitions) {
        return {...transitions,
            [this.transitionId]:
            this.transitionsLayout
        }
    };
    revertRemoveTransitionProperty(transitionProperties) {
        return _.concat(transitionProperties, this.deletedtransitionProperties )
    };
}
