import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {renderToStaticMarkup} from 'react-dom/server';
import {withLocalize} from 'react-localize-redux';
import DrawingArea from './drawingArea/DrawingArea.container';
import ZoomToolbar from './zoomPanel/ZoomToolbar.container';
import StepPanel from "./sidePanel/stepPanel/StepPanel.container";
import TransitionPropPanel from "./sidePanel/propPanel/TransitionPropPanel.container";
import StepPropPanel from "./sidePanel/propPanel/StepPropPanel.container";
import {RECT_OFFSET} from "./drawingArea/step/Step";
import HeaderPanel from "./headerPanel/HeaderPanel.container";
import './App.css';
import _ from 'lodash';
import {DRAWING_AREA, SIDE_PANEL} from "../redux/modules/processPlan";

export const HEADER_OFFSET = 100;
export const SIDE_PANEL_OFFSET = 300;
export const COLOR_DISABLE = "#7e7e7e";

class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isGotTranslation: false,
        };
    }

    setApp = (app) => {
        this.app = app;
    };

    setMain = (main) => {
        this.main = main;
    };

    render() {
        return (
            <div className="app"
                 tabIndex="0"
                 ref={this.setApp}
                 onClick={this.handleFullScreen}
                 onKeyDown={this.handleKeyDown}
                 id="full-screen"
            >
                <HeaderPanel className="header" isLockedProcessPlan={this.props.isLockedProcessPlan}/>
                <div className="toolbar"
                     ref={this.setToolbar}
                     tabIndex="0"
                     onMouseUp={this.toolbarMouseUp}
                >
                    <div className="edit-toolbar">
                        <div
                            className={'undo-redo-icon' + (this.props.isLockedProcessPlan || !this.props.isAvailableUndo ? ' undo-redo-icon-disabled' : '')}
                            onClick={this.handleUndo}
                            title={this.props.translate("NG_PPE_Undo_long")}
                            role='button'
                            aria-label={this.props.translate("NG_PPE_Undo_long")}
                        >
                            <i className="material-icons">undo</i>
                        </div>
                        <div
                            className={'undo-redo-icon' + (this.props.isLockedProcessPlan || !this.props.isAvailableRedo ? ' undo-redo-icon-disabled' : '')}
                            onClick={this.handleRedo}
                            title={this.props.translate("NG_PPE_Redo_long")}
                            role='button'
                            aria-label={this.props.translate("NG_PPE_Redo_long")}
                        >
                            <i className="material-icons">redo</i>
                        </div>
                        {((this.props.selectedObjectType === 'step' && !_.isUndefined(this.props.selectedStep)) || this.props.selectedObjectType === 'transition') ?
                            <div
                                className={'delete-button' + (this.props.isLockedProcessPlan ? ' delete-button-disabled' : '')}
                                title={this.props.translate("NG_PPE_Delete_long")}
                                role='button'
                                aria-label={this.props.translate("NG_PPE_Delete_long") + this.props.selectedObjectType}
                                onClick={this.deleteElement}>
                                <i className="material-icons">delete_forever</i>
                            </div> : ''}
                    </div>
                    <ZoomToolbar className="zoom-toolbar"/>
                    <div className="visualization-buttons">
                        <div className="transition-info-button"
                             title={this.props.translate("NG_PPE_Show_transition_info_long") + (this.props.showTransitionInfo ? this.props.translate("NG_PPE_On_long") : this.props.translate("NG_PPE_Off_long"))}
                             role='button'
                             aria-label={this.props.translate("NG_PPE_Show_transition_info_long")}
                             aria-pressed={this.props.showTransitionInfo}
                             onClick={this.props.onTransitionInfoButtonClick}>
                            {this.props.showTransitionInfo ?
                                <i key="on" className="material-icons">visibility</i> :
                                <i key="off" className="material-icons">visibility_off</i>
                            }
                        </div>
                        <div
                            className={'snap-to-grid' + (this.props.isLockedProcessPlan ? ' snap-to-grid-disabled' : '')}
                            title={this.props.translate("NG_PPE_Snap_to_grid_long") + (this.props.snapToGrid ? this.props.translate("NG_PPE_On_long") : this.props.translate("NG_PPE_Off_long"))}
                            role='button'
                            aria-label={`${this.props.snapToGrid ? this.props.translate("NG_PPE_Reset_Snap_to_grid_long") : this.props.translate("NG_PPE_Set_Snap_to_grid_long")}`}
                            aria-pressed={this.props.snapToGrid}
                            onClick={this.props.onSnapToGridButtonClick}>
                            {this.props.snapToGrid ?
                                <i key="on" className="material-icons">grid_on</i> :
                                <i key="off" className="material-icons">grid_off</i>}
                        </div>
                        <div
                            className={'auto-generate-layout' + (this.props.isLockedProcessPlan ? ' auto-generate-layout-disabled' : '')}
                            title={this.props.translate("NG_PPE_Auto_generate_layout_long")}
                            role='button'
                            aria-label={this.props.translate("NG_PPE_Auto_generate_layout_long")}
                            onClick={this.onAutoGenerateLayout}>
                            <i className="material-icons">clear_all</i>
                        </div>
                    </div>
                </div>
                <div className="main"
                     id='mainSvgField'
                     ref={this.setMain}
                     onMouseUp={this.handleMouseUp}
                     onDrop={this.handleDrop}
                     onDragStart={(event) => event.preventDefault()}
                     onDragOver={(event) => event.preventDefault()}>
                    <DrawingArea isLockedProcessPlan={this.props.isLockedProcessPlan}/>
                </div>
                <div className="aside">
                    {this.renderSidePanel()}
                </div>
                <div className={"loading-overlay" + (this.state.isGotTranslation ? " hidden" : "")}/>
            </div>
        );
    }

    renderSidePanel() {
        switch (this.props.selectedObjectType) {
            case 'step':
                return <StepPropPanel isLockedProcessPlan={this.props.isLockedProcessPlan}/>;
            case 'transition':
                return <TransitionPropPanel isLockedProcessPlan={this.props.isLockedProcessPlan}/>;
            default:
                return <StepPanel isLockedProcessPlan={this.props.isLockedProcessPlan}/>;
        }
    }

    componentDidMount() {
        this.props.onMainRender(
            {
                width: window.innerWidth - SIDE_PANEL_OFFSET,
                height: window.innerHeight - HEADER_OFFSET
            }
        );
        window.addEventListener('resize', this.handleResize);
        this.app.focus();
    };

    componentDidUpdate(prevProps, prevState) {
        if (!prevState.isGotTranslation && !_.isEmpty(this.props.translations)) {
            this.setState({isGotTranslation: true});
            this.props.initialize({
                languages: [{name: "", code: ""}],
                translation: this.props.translations,
                options: {renderToStaticMarkup}
            });
        }
    };

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    getMobileOperatingSystem = () => {
        let userAgent = navigator.userAgent || navigator.vendor || window.opera;

        if (/windows phone/i.test(userAgent)) {
            return "Windows Phone";
        }
        if (/android/i.test(userAgent)) {
            return "Android";
        }
        if (/iPad|iPhone|iPod/.test(userAgent) && !window.MSStream) {
            return "iOS";
        }
        return "unknown";
    };

    handleFullScreen = () => {
        this.props.onClick();

        if (this.getMobileOperatingSystem() !== 'unknown') {

            let elem = document.getElementById("root");
            if (elem.webkitRequestFullscreen) {
                elem.webkitRequestFullscreen();
            } else if (elem.mozRequestFullScreen) { /* Firefox */
                elem.mozRequestFullScreen();
            } else if (elem.msRequestFullscreen) { /* IE/Edge */
                elem.msRequestFullscreen();
            }
        }
    };

    handleDrop = (e) => {
        if (!this.props.isLockedProcessPlan) {
            // Prevent default drop redirect in FF
            e.preventDefault();
            let data;
            try {
                data = JSON.parse(e.dataTransfer.getData('text'));
            } catch (ignore) {
                return;
            }
            let position = {
                x: e.clientX - data.offsetX - (RECT_OFFSET),
                y: e.clientY - data.offsetY - (RECT_OFFSET) - HEADER_OFFSET
            };
            this.props.onDrop(data.stepType, position);
        }
    };

    handleMouseUp = () => {
        this.props.onMouseUp();
    };

    handleKeyDown = (e) => {
        if ((e.altKey && e.which === 67) || (e.altKey && e.keyCode === 67)) { //alt+C
            e.preventDefault();
            this.props.onFocusOnGlobalArea(DRAWING_AREA);
            this.props.onSelectDrawingArea();
        } else if ((e.altKey && e.which === 80) || (e.altKey && e.keyCode === 80)) { //alt+P
            e.preventDefault();
            this.props.onFocusOnGlobalArea(SIDE_PANEL);
        } else if ((e.ctrlKey && e.which === 89) || (e.metaKey && e.shiftKey && e.keyCode === 90) || (e.metaKey && e.shiftKey && e.which === 90)) { //ctrl+Y //cmd+SHIFT+Z
            e.preventDefault();
            this.props.onRedo();
        } else if ((e.ctrlKey && e.which === 90) || (e.metaKey && e.keyCode === 90)) { //ctrl+Z //cmd+Z
            e.preventDefault();
            this.props.onUndo();
        }
    };

    deleteElement = () => {
        if (!this.props.isLockedProcessPlan) {
            (this.props.selectedObjectType === 'step') ? this.props.onDeleteStep(this.props.selectedStep.id) : this.props.onDeleteTransition(this.props.selectedTransition.id);
        }
    };

    onAutoGenerateLayout = () => {
        if (!this.props.isLockedProcessPlan) {
            this.props.onUpdateLayout();
        }
    };

    handleUndo = () => {
        if (!this.props.isLockedProcessPlan) {
            this.props.onUndo();
        }
    };

    handleRedo = () => {
        if (!this.props.isLockedProcessPlan) {
            this.props.onRedo();
        }
    };

    handleResize = () => {
        this.props.onMainRender(
            {
                width: window.innerWidth - SIDE_PANEL_OFFSET,
                height: window.innerHeight - HEADER_OFFSET
            }
        );
        this.props.onUpdateScreenSize();
    };

    toolbarMouseUp = () => {
        if (this.props.isExistTempTransition) {
            this.props.destroyTempTransition();
        }
    };
}

App.propTypes = {
    selectedObjectType: PropTypes.oneOf(['step', 'transition']),
    showTransitionInfo: PropTypes.bool,
    transitionCreationStarted: PropTypes.bool,
    selectedStep: PropTypes.object,
    selectedTransition: PropTypes.object,
    translations: PropTypes.object,
    isAvailableUndo: PropTypes.bool,
    isAvailableRedo: PropTypes.bool,
    isLockedProcessPlan: PropTypes.bool,
    isExistTempTransition: PropTypes.bool.isRequired,
    initialize: PropTypes.func.isRequired,
    translate: PropTypes.func.isRequired,
    onMainRender: PropTypes.func,
    onDrop: PropTypes.func,
    onMouseMove: PropTypes.func,
    onMouseUp: PropTypes.func,
    onClick: PropTypes.func,
    onTransitionInfoButtonClick: PropTypes.func,
    onFocusOnGlobalArea: PropTypes.func,
    onSelectDrawingArea: PropTypes.func,
    onUndo: PropTypes.func,
    onRedo: PropTypes.func,
    onDeleteStep: PropTypes.func,
    onDeleteTransition: PropTypes.func,
    onUpdateLayout: PropTypes.func,
    onUpdateScreenSize: PropTypes.func,
    destroyTempTransition: PropTypes.func
};

export default withLocalize(App);
