import {createAction, handleActions} from 'redux-actions';

import uuidv4 from 'uuid/v4';
import _ from 'lodash';
import {getStepLayoutById, getStepPositionById} from "./stepsLayout";
import {ARROW_OFFSET} from "../../components/drawingArea/transition/Transition.container";
import {getStepTypeById, selectStep} from "./steps";
import {
    CREATING_CONNECTION,
    CREATING_TRANSITION_BY_SHORTCUTS,
    getActionType,
    resetActionType,
    UPDATING_CONNECTION,
    UPDATING_TRANSITION_END_POINT,
    UPDATING_TRANSITION_START_POINT
} from "./transitionActionType";
import {copyTransition, getTransitionCopy} from "./transitionCopy";
import {getTransitionById, getTransitions, selectTransition} from "./transitions";
import {selectDrawingArea} from "./drawingArea";
import {ChangeTransitionPosition} from "../model/ChangeTransition";
import {applyChange} from "./changeStack";
import {AddTransitionChange} from "../model/AddTransition";
import {AddConnection} from "../model/AddConnection";
import {getTransitionLayoutById} from "./transitionsLayout";
import {ChangeConnectionPosition} from "../model/ChangeConnection";
import {RECT_OFFSET} from '../../components/drawingArea/step/Step';
import {getConnectionEndPosition} from './connectionsLayout';
import {createSelector} from "reselect";

export const startTransitionCreation = createAction("TRANSITION_CREATION_START", (type, stepId, position, x, y) => ({
    type,
    stepId,
    position,
    x,
    y
}));
export const updateScaledTempTransitionEndPoint = createAction("TEMP_TRANSITION_END_POINT_UPDATE", (coordinates, scale, translate) => ({
    coordinates,
    scale,
    translate
}));
export const updateScaledTempTransitionStartPoint = createAction("TEMP_TRANSITION_START_POINT_UPDATE", (coordinates, scale, translate) => ({
    coordinates,
    scale,
    translate
}));
export const changeTempTransitionEndPointPosition = createAction("TEMP_TRANSITION_END_POINT_POSITION_CHANGE", position => position);
export const changeTempTransitionStartPointPosition = createAction("TEMP_TRANSITION_START_POINT_POSITION_CHANGE", position => position);
export const removeTempTransition = createAction("TEMP_TRANSITION_REMOVE");
export const startTransitionUpdate = createAction("TRANSITION_UPDATE_START", tempTransition => tempTransition);

export const startTransitionPointUpdate = (coordinates, id, pointType) => (dispatch, getState) => {
    let transition = getTransitionById(getState(), id);
    let transitionLayout = getTransitionLayoutById(getState(), id);
    let tempTransition = {
        id: id,
        start: {
            x: coordinates.start.x,
            y: coordinates.start.y,
            position: pointType === 'start' ? null : transitionLayout.start.position,
            stepId: transition.startStepId
        },
        end: {
            x: coordinates.end.x,
            y: coordinates.end.y,
            position: pointType === 'end' ? null : transitionLayout.end.position,
            stepId: transition.endStepId
        }
    };

    let transitionCopy = {
        start: {
            position: transitionLayout.start.position,
            stepId: transition.startStepId
        },
        end: {
            position: transitionLayout.end.position,
            stepId: transition.endStepId
        }
    };

    dispatch(copyTransition(transitionCopy));
    dispatch(startTransitionUpdate(tempTransition));
};

export const startFilePickUpConnectionEndPointUpdate = (coordinates, connection) => (dispatch, getState) => {
    let {startStepId, endStepId} = connection;
    let endStepSize = getStepLayoutById(getState(), endStepId).size;
    let tempTransition = {
        start: {
            stepId: startStepId,
            x: coordinates.start.x,
            y: coordinates.start.y,
            position: coordinates.start.x >= coordinates.end.x + endStepSize.width / 2 + RECT_OFFSET ? 'left' : 'top'
        },
        end: {
            stepId: endStepId,
            x: coordinates.end.x,
            y: coordinates.end.y,
            position: null
        }
    };

    let transitionCopy = {
        start: {
            stepId: startStepId,
            position: null
        },
        end: {
            stepId: endStepId,
            position: getConnectionEndPosition(getState(), connection) || calculatePosition(coordinates.start.x, coordinates.start.y, coordinates.end.x, coordinates.end.y)
        }
    };

    dispatch(copyTransition(transitionCopy));
    dispatch(startTransitionUpdate(tempTransition));
};

export const updateTempTransitionEndPoint = (coordinates) => (dispatch, getState) => {
    dispatch(updateScaledTempTransitionEndPoint(coordinates, getState().drawingArea.scale, getState().drawingArea.viewportPosition));
};


export const updateTempTransitionStartPoint = (coordinates) => (dispatch, getState) =>
    dispatch(updateScaledTempTransitionStartPoint(coordinates, getState().drawingArea.scale, getState().drawingArea.viewportPosition));


export const endTransitionCreation = (stepId, position) => (dispatch, getState) => {
    if (!transitionCreationStarted(getState())) {
        dispatch(selectDrawingArea(true));
        dispatch(removeTempTransition());
        dispatch(resetActionType());
        return;
    }
    let restoreOldTransition = false;
    let restoreOldConnection = false;
    let actionType = getActionType(getState());
    let tempTransition = {...getTempTransition(getState())};
    let transitionCopy = {...getTransitionCopy(getState())};

    let transition = getTransitionById(getState(), tempTransition.id);
    let transitionLayout = getTransitionLayoutById(getState(), tempTransition.id);
    if (!_.isUndefined(tempTransition.start)) {
        if (position) {
            actionType === UPDATING_TRANSITION_START_POINT ? tempTransition.start = {
                ...tempTransition.start,
                stepId
            } : tempTransition.end = {...tempTransition.end, stepId};
            actionType === UPDATING_TRANSITION_START_POINT ? tempTransition.start = {
                ...tempTransition.start,
                position
            } : tempTransition.end = {...tempTransition.end, position};
            tempTransition.id = tempTransition.id || (tempTransition.start.stepId === 'start' ? 'out' : uuidv4());
            if (actionType === UPDATING_TRANSITION_END_POINT) { //updating transition end point
                if (tempTransition.start.stepId !== stepId && stepId !== 'start') {
                    dispatch(applyChange(new ChangeTransitionPosition(tempTransition, transition, transitionLayout)));
                } else {
                    restoreOldTransition = true;
                }
                if (actionType !== UPDATING_CONNECTION) {
                    dispatch(selectTransition(tempTransition.id));
                }
            } else if (actionType === UPDATING_TRANSITION_START_POINT) { //updating transition start point
                if (tempTransition.start.stepId === transitionCopy.start.stepId) {
                    dispatch(applyChange(new ChangeTransitionPosition(tempTransition, transition, transitionLayout)));
                    dispatch(selectTransition(tempTransition.id));
                } else {
                    restoreOldTransition = true;
                }
            } else if (tempTransition.start.stepId !== stepId && stepId !== 'start') { //creating connection or transition
                if (actionType === CREATING_CONNECTION) {
                    dispatch(applyChange(new AddConnection(tempTransition)));
                }
                if (actionType !== CREATING_CONNECTION && actionType !== UPDATING_CONNECTION) {
                    dispatch(applyChange(new AddTransitionChange(tempTransition)));
                    dispatch(selectTransition(tempTransition.id));
                }
            }
            if (actionType === UPDATING_CONNECTION) {//updating connection end point
                dispatch(applyChange(new ChangeConnectionPosition(tempTransition)));
            }
        } else if (!_.isNil(tempTransition)) {
            restoreOldTransition = actionType === UPDATING_TRANSITION_START_POINT || actionType === UPDATING_TRANSITION_END_POINT;
            restoreOldConnection = actionType === UPDATING_CONNECTION;
        }
        if (restoreOldConnection) {
            if (!_.isNull(tempTransition.end.position)) {
                restoreOldTransition = restoreOldConnection;
            }
        }
        if (restoreOldTransition) {
            tempTransition.end = {...tempTransition.end, ...transitionCopy.end};
            tempTransition.start = {...tempTransition.start, ...transitionCopy.start};
            dispatch(applyChange(new ChangeTransitionPosition(tempTransition, transition, transitionLayout)));

            if (actionType !== CREATING_CONNECTION && actionType !== UPDATING_CONNECTION) {
                dispatch(selectTransition(tempTransition.id));
            }
        }
    }
    dispatch(selectDrawingArea(null));
    dispatch(selectStep(null));
    dispatch(removeTempTransition());
    dispatch(resetActionType());
};

export const startTempTransition = (stepId, position) => (dispatch, getState) => {
    let stepPosition = getStepPositionById(getState(), stepId);
    if (stepId !== 'start' || (stepId === 'start' && _.isUndefined(_.find(getTransitions(getState()), transition => transition.startStepId === 'start')))) {
        dispatch(startTransitionCreation(getStepTypeById(getState(), stepId), stepId, position, stepPosition.x, stepPosition.y));
    }
};

export const continueTransitionCreation = (coordinates) => (dispatch, getState) => {
    let actionType = getActionType(getState());
    if (actionType === UPDATING_TRANSITION_START_POINT) {
        dispatch(updateTempTransitionStartPoint(coordinates));
    } else {
        dispatch(updateTempTransitionEndPoint(coordinates));
    }
};

export const changeTransitionPosition = (position) => (dispatch, getState) => {
    let actionType = getActionType(getState());
    if (actionType !== CREATING_TRANSITION_BY_SHORTCUTS) {
        if (actionType === UPDATING_TRANSITION_START_POINT) {
            dispatch(changeTempTransitionStartPointPosition(position));
        } else {
            dispatch(changeTempTransitionEndPointPosition(position));
        }
    }
};

//- State
const initialState = {};

//- Reducers
export default handleActions({
    TRANSITION_CREATION_START: (state, action) => {
        return {
            start: {
                ...action.payload
            }
        };
    },
    TEMP_TRANSITION_END_POINT_UPDATE: (state, action) => {
        let {coordinates, scale, translate} = action.payload;
        let scaledCoordinates = {
            x: coordinates.x * scale + translate.x,
            y: (coordinates.y) * scale + translate.y
        };
        return {
            ...state,
            end: {
                ...state.end,
                ...scaledCoordinates
            }
        };
    },
    TEMP_TRANSITION_START_POINT_UPDATE: (state, action) => {
        let {coordinates, scale, translate} = action.payload;
        let scaledCoordinates = {
            x: coordinates.x * scale + translate.x,
            y: (coordinates.y) * scale + translate.y
        };
        return {
            ...state,
            start: {
                ...state.start,
                ...scaledCoordinates
            }
        };
    },
    TEMP_TRANSITION_END_POINT_POSITION_CHANGE: (state, action) => {
        if (_.isNil(state.end)) {
            return state;
        }
        return {
            ...state,
            end: {
                ...state.end,
                position: action.payload
            }
        };
    },
    TEMP_TRANSITION_START_POINT_POSITION_CHANGE: (state, action) => {
        if (_.isNil(state.start)) {
            return state;
        }
        return {
            ...state,
            start: {
                ...state.start,
                position: action.payload
            }
        };
    },
    TEMP_TRANSITION_REMOVE: () => {
        return {};
    },
    TRANSITION_UPDATE_START: (state, action) => {
        return action.payload;
    }
}, initialState);

//- Selectors
export const getTempTransition = state => state.tempTransition;

export const transitionCreationStarted = createSelector(
    getTempTransition,
    tempTransition => !_.isNil(tempTransition.start)
);

export const transitionCreationInProgress = createSelector(
    getTempTransition,
    tempTransition => !_.isNil(tempTransition.end)
);

export const getTempTransitionEndPointPosition = createSelector(
    getTempTransition,
    tempTransition => tempTransition && tempTransition.end && tempTransition.end.position
);

//- Utils
export const calculatePosition = (x1, y1, x2, y2) => {
    if (y2 <= y1 + ARROW_OFFSET / 2 && y2 >= y1 - ARROW_OFFSET / 2) {
        if (x1 > x2) {
            return 'right';
        }
        else {
            return 'left';
        }
    }
    else {
        if (y2 < y1 - ARROW_OFFSET / 2) {
            return 'bottom';
        }
        else if (y2 > y1 + ARROW_OFFSET / 2) {
            return 'top';
        }
    }
};
