import {Change} from './Change';

export class ChangeProcessPlanEmail extends Change {
    constructor(email) {
        super('Change process plan email');
        this.email = email;
        this.prevEmail = null;
    }

    applyProcessPlanEmailChange(processPlan) {
        this.prevEmail = processPlan.email;
        return {...processPlan, email: this.email};
    }
    revertProcessPlanEmailChange(processPlan) {
        return {...processPlan, email: this.prevEmail};
    }
}