import {Change} from './Change';
import _ from 'lodash';

export class ChangeTransitionProperty extends Change {
    constructor(transitionId, name, value) {
        super('Change transition property');
        this.transitionId = transitionId;
        this.name = name;
        this.value = value;

        this.currentValue = null;
    }

    applyTransitionPropertyChange(transitionProperties) {
        return _.map(transitionProperties, property => {
            if (property.name === this.name && property.transitionId === this.transitionId) {
                this.currentValue = property.value;
                return {...property, value: this.value};
            } else if (this.name === 'pdf_overview_details' && property.name === 'create_report' && property.transitionId === this.transitionId) {
                this.currentValue = property.value;
                let changedValues = _.map({...property.value.values}, value => {
                    if (value.type === 'overview_report') {
                        return {...this.value}
                    } else {
                        return value
                    }
                });
                return {...property, value: {values: changedValues, checked: property.value.checked}};
            } else {
                return {...property};
            }
        });
    }

    revertTransitionPropertyChange(transitionProperties) {
        return _.map(transitionProperties, property => {
            if (property.name === this.name && property.transitionId === this.transitionId) {
                return {...property, value: this.currentValue};
            } else if (this.name === 'pdf_overview_details' && property.name === 'create_report' && property.transitionId === this.transitionId) {
                return {...property, value: this.currentValue};
            } else {
                return {...property};
            }
        });
    }
}