import {connect} from 'react-redux';
import {getTranslate} from 'react-localize-redux';
import ZoomToolbar from "./ZoomToolbar";
import {fitTheViewBox, incrementZoomScale,resetToTheBaseViewBox} from "../../redux/modules/drawingArea";


const mapStateToProps = (state) => {
    return {
        translate: getTranslate(state.localize)
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        onChangeZoom: (sign) => dispatch(incrementZoomScale(sign)),
        onFitTheScreen: () => dispatch(fitTheViewBox()),
        onReset:() => dispatch(resetToTheBaseViewBox())
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(ZoomToolbar);