import {connect} from 'react-redux';
import PropPanel from './PropPanel';
import {getTranslate} from 'react-localize-redux';
import _ from 'lodash';
import {
    getSelectedTransitionPropsWithControlDescriptors, SELECTABLE_PROPERTIES_NUMBER_BY_TRANSITION_PROPERTY_NAME,
    updateTransitionTypes
} from "../../../redux/modules/transitionProperties";
import {getFocusedArea, SIDE_PANEL} from "../../../redux/modules/processPlan";
import {getSelectedTransitionId} from "../../../redux/modules/transitions";
import {applyChange} from "../../../redux/modules/changeStack";
import {ChangeTransitionProperty} from "../../../redux/model/ChangeTransitionProperty";

export const mapStateToProps = (state) => {
    let propertyGroups = getSelectedTransitionPropsWithControlDescriptors(state);
    let selectablePropertiesNumber = _.chain(propertyGroups)
        .map(group => group.properties)
        .flatten()
        .map(property => property.controlDescriptor.type === 'checkbox-group' ? property.controlDescriptor.options.length :SELECTABLE_PROPERTIES_NUMBER_BY_TRANSITION_PROPERTY_NAME[property.controlDescriptor.type])
        .sum()
        .value();

    return ({
        objectId: getSelectedTransitionId(state),
        propertyGroups: getSelectedTransitionPropsWithControlDescriptors(state),
        selectablePropertiesNumber: selectablePropertiesNumber,
        selected: getFocusedArea(state) === SIDE_PANEL,
        translate: getTranslate(state.localize)
    })
};

const mapDispatchToProps = (dispatch) => {
    return {
        onPropertyChange: (transitionId, name, value) => {
            if (name === 'types') {
                dispatch(updateTransitionTypes(transitionId, value));
            } else {
                dispatch(applyChange(new ChangeTransitionProperty(transitionId, name, value)));
            }
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(PropPanel)