import {Change} from './Change';
import _ from 'lodash';
import {defaultPropertyValues, propertiesByStepType} from '../../api/processPlanConversion';
import {GRID_CELL_SIZE} from "../../components/drawingArea/step/Step";

export const DEFAULT_STEP_SIZE = {
    width: 200,
    height: 40
};

export const DEFAULT_STEP_POSITION = {
    x: 0,
    y: 0
};

export class AddStepChange extends Change {
    constructor(id, type, position, scale, translate, snapToGrid) {
        super('Add step');
        this.stepId = id;
        this.type = type;
        this.position = position;
        this.scale = scale;
        this.translate = translate;
        this.snapToGrid = snapToGrid;

        this.step = null;
        this.stepLayout = null;
    }
    applyAddStep(steps) {
        this.step= {
            id: this.stepId,
            type: this.type,
            selected: false,
            highlighted: false,
            intersectedAnchors: []
        };
        return {
            ...steps,
            [this.stepId]: this.step
        };
    }
    applyAddStepLayout(stepsLayout) {
        let scaledPosition;
        if (this.position) {
            (this.snapToGrid)?
                scaledPosition = {
                    x:  Math.round((this.position.x * this.scale + this.translate.x)/ GRID_CELL_SIZE) * GRID_CELL_SIZE,
                    y:  Math.round((this.position.y * this.scale + this.translate.y)/ GRID_CELL_SIZE) * GRID_CELL_SIZE
                } : scaledPosition = {
                    x: this.position.x * this.scale + this.translate.x,
                    y: this.position.y * this.scale + this.translate.y
                };
        }
        this.stepLayout= {
            position: scaledPosition || DEFAULT_STEP_POSITION ,
            size: this.size || DEFAULT_STEP_SIZE,
        };
        return {
            ...stepsLayout,
            [this.stepId]: this.stepLayout
        };
    }
    applyAddStepProperty(stepProperty) {
        let newProperties = _.map(propertiesByStepType[this.type], name => ({
            stepId: this.stepId,
            name: name === 'index' ? 'step_id' : name,
            value: _.has(defaultPropertyValues, name) ? defaultPropertyValues[name] : null
        }));
        return [...stepProperty, ...newProperties];
    }
    applyAddStepConnectionLayout(steps) {
        if(this.type !== 'filepickup') {
            return steps;
        }
        return [...steps, {startStepId: this.stepId, endStepId: null,  endPosition: null}];
    }


    revertAddStep(steps) {
        this.step = steps[this.stepId];
        return _.omitBy(steps, step => step.id === this.stepId);
    }
    revertAddStepLayout(steps) {
        this.stepLayout = steps[this.stepId];
        return _.omitBy(steps, step => step === this.stepLayout);
    }
    revertAddStepProperty(stepProperties) {
        return  _.filter(stepProperties, stepProperty => stepProperty.stepId !== this.stepId);
    }
    revertAddStepConnectionLayout(steps) {
        if(this.type !== 'filepickup') {
            return steps;
        }
        return  _.filter(steps, step => step.startStepId !== this.stepId);
    }
}
