import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './Transition.css';

import {HEADER_OFFSET} from "../../App";
import {RECT_OFFSET} from "../step/Step";
import {ICON_SIZE} from "./Transition.container";
import {translatedTransitionCheckboxType, translatedTransitionReportTypes} from "../../../dictionary"
import _ from 'lodash'

import certify from './icons/certify.svg';
import create_reports from './icons/create_reports.svg';
import create_report from './icons/create_report.svg';

import error from '../error_icon.svg';

export const ARROW_SIZE = 10;
const UPDATE_RADIUS = 25;
export const CREATE_REPORT_TYPE_OFFSET = 11;
const ERROR_ICON_SIZE = 20;
let RADIUS;

const ICONS = {
    certify,
    create_report,
    create_reports
};

class Transition extends Component {
    constructor(props) {
        super(props);

        this.state = {
            isAvailableDrop: false,
        };
    }

    setTransition = (transition) => {
        this.transition = transition;
    };

    render() {
        let path = this.buildPath();
        if (_.isNil(path)) {
            return null;
        }
        let transitionClassName = 'transition ' + this.props.className +
            (this.props.selected ? ' selected' : '') +
            (this.props.highlighted ? ' highlighted' : '') +
            (this.props.disabled ? ' disabled' : '') +
            (this.state.isAvailableDrop ? ' step-insert' : '') +
            (this.props.intersected ? ' step-insert' : '');
        return (
            <g className={transitionClassName}
               ref={this.setTransition}
               role="img"
               aria-label={this.getAriaLabel()}
               tabIndex="0"
               onKeyDown={this.onKeyDown}
               onDragOver={this.handleDragOver  /*(event) => event.preventDefault()*/}
               onDragLeave={this.handleDragLeave}
               onDrop={this.handleDrop}>
                {this.renderTypes()}
                {this.renderIcons()}
                <path className="visible-path" d={path}/>
                {this.renderArrow()}
                {this.renderSelectionPath(path)}
                {this.renderValidationTooltips()}
            </g>
        );
    }

    renderArrow = () => {
        let arrowPoints;
        if (this.props.arrowType) {
            let arrowPosition = Transition.arrowPosition(this.props.arrowType);
            arrowPoints = '' +
                (this.props.end.x + arrowPosition.x1) + ', ' + (this.props.end.y + arrowPosition.y1) + ' ' +
                (this.props.end.x + arrowPosition.x2) + ', ' + (this.props.end.y + arrowPosition.y2) + ' ' +
                this.props.end.x + ', ' + this.props.end.y;
        }

        return arrowPoints ? <polygon points={arrowPoints}/> : null;
    };

    renderSelectionPath = (path) => {
        return (
            <path className="selection-path"
                  d={path}
                  onMouseEnter={this.handleMouseEnter}
                  onMouseLeave={this.handleMouseLeave}
                  onMouseDown={this.handleMouseDown}
                  onClick={this.handleClick}/>
        );
    };

    renderTypes = () => {
        if (this.props.highlighted || this.props.showTransitionInfo) {
            return _.map(this.props.types, (type, index) => (
                <text key={type}
                      className={`transition-label ${type}`}
                      x={this.props.typesPoints[index].x}
                      y={this.props.typesPoints[index].y}>
                    {this.props.translate(translatedTransitionCheckboxType[type])}
                </text>
            ));
        }
    };

    renderIcons = () => {
        if (this.props.highlighted || this.props.showTransitionInfo) {
            return _.map(this.props.icons, (icon, index) => (
                <g key={`${icon.name}${index}g`}>
                    <image href={ICONS[icon.name]}
                           x={this.props.iconsPoints[index].x}
                           y={this.props.iconsPoints[index].y}
                           key={`${icon.name}${index}`}
                           width={ICON_SIZE}
                           height={ICON_SIZE}/>
                    {icon.innerText ? <text className='report-text'
                                            x={this.props.iconsPoints[index].x + ICON_SIZE / 2 - RECT_OFFSET}
                                            y={this.props.iconsPoints[index].y + ICON_SIZE - RECT_OFFSET}>
                        {icon.innerText}
                    </text> : null}
                    {icon.caption ? <text className='report-text'
                                          x={this.props.iconsPoints[index].x + CREATE_REPORT_TYPE_OFFSET}
                                          y={this.props.iconsPoints[index].y + ICON_SIZE + CREATE_REPORT_TYPE_OFFSET}
                                          fontSize="10"
                                          textAnchor="middle">
                        {this.props.translate(translatedTransitionReportTypes[icon.caption])}
                    </text> : null}
                </g>
            ));
        }
    };

    renderValidationTooltips = () => {
        if (this.props.validationTooltips && this.props.validationTooltips.length !== 0) {
            let coordinates = this.getErrorIconCoordinates();
            return (
                <image className='error-icon'
                       href={error}
                       x={coordinates.x}
                       y={coordinates.y}
                       height={ERROR_ICON_SIZE}
                       width={ERROR_ICON_SIZE}
                       onMouseEnter={this.handleMouseEnter}
                       onMouseLeave={this.handleMouseLeave}
                       onClick={this.handleClick}>
                    <title>{_.join(this.props.validationTooltips, '.\n')}
                    </title>
                </image>);
        }
    };

    componentDidMount() {
        if (this.props.selected) {
            this.transition.focus();
        }
    }

    componentDidUpdate(prevProps) {
        if (!prevProps.focused && this.props.focused) {
            this.transition.focus();
        }
    }

    static arrowPosition(arrowType) {
        switch (arrowType) {
            case 'top':
                return {'x1': -ARROW_SIZE / 2, 'x2': ARROW_SIZE / 2, 'y1': -ARROW_SIZE, 'y2': -ARROW_SIZE};
            case 'bottom':
                return {'x1': -ARROW_SIZE / 2, 'x2': ARROW_SIZE / 2, 'y1': ARROW_SIZE, 'y2': ARROW_SIZE};
            case 'right':
                return {'x1': ARROW_SIZE, 'x2': ARROW_SIZE, 'y1': ARROW_SIZE / 2, 'y2': -ARROW_SIZE / 2};
            case 'left':
                return {'x1': -ARROW_SIZE, 'x2': -ARROW_SIZE, 'y1': ARROW_SIZE / 2, 'y2': -ARROW_SIZE / 2};
            default:
                return 'Error';
        }
    }

    buildPath = () => {
        if (_.isNil(this.props.start)) {
            return null;
        }
        let path = 'M ' + this.props.start.x + ' ' + this.props.start.y; // Define start point

        for (let i = 1; i < this.props.points.length - 1; i++) {
            let direction = this.getLineDirection(this.props.points[i - 1], this.props.points[i]);
            let offsetCoordinate;
            switch (direction) {
                case 'H':
                    // Draw horizontal line and round it at the end
                    this.getRadius(this.props.points[i].x, this.props.points[i - 1].x);
                    offsetCoordinate = this.getOffsetCoordinate(this.props.points[i].x, this.props.points[i - 1].x);
                    path += ' L ' + offsetCoordinate + ' ' + this.props.points[i].y;
                    path += ' M ' + offsetCoordinate + ' ' + this.props.points[i].y;
                    this.getRadius(this.props.points[i].y, this.props.points[i + 1].y);
                    offsetCoordinate = this.getOffsetCoordinate(this.props.points[i].y, this.props.points[i + 1].y);
                    path += ' Q ' + this.props.points[i].x + ' ' + this.props.points[i].y + ' '
                        + this.props.points[i].x + ' ' + offsetCoordinate;

                    path += ' M ' + this.props.points[i].x + ' ' + offsetCoordinate;

                    break;
                case 'V':
                    // Draw vertical line and round it at the end
                    this.getRadius(this.props.points[i].y, this.props.points[i - 1].y);
                    offsetCoordinate = this.getOffsetCoordinate(this.props.points[i].y, this.props.points[i - 1].y);
                    path += ' L ' + this.props.points[i].x + ' ' + offsetCoordinate;
                    path += ' M ' + this.props.points[i].x + ' ' + offsetCoordinate;

                    this.getRadius(this.props.points[i].x, this.props.points[i + 1].x);
                    offsetCoordinate = this.getOffsetCoordinate(this.props.points[i].x, this.props.points[i + 1].x);
                    path += ' Q ' + this.props.points[i].x + ' ' + this.props.points[i].y + ' '
                        + offsetCoordinate + ' ' + this.props.points[i].y;
                    path += ' M ' + offsetCoordinate + ' ' + this.props.points[i].y;

                    break;
                default:
                    break;
            }
        }

        // Finish line
        path += ' L ' + this.props.points[this.props.points.length - 1].x + ' ' + this.props.points[this.props.points.length - 1].y;

        return path;
    };

    getLineDirection = (startPoint, endPoint) => {
        if (startPoint.x === endPoint.x) {
            return 'V';
        } else {
            return 'H';
        }
    };

    getOffsetCoordinate = (mainCoordinate, directiveCoordinate) => {
        return mainCoordinate - Math.sign(mainCoordinate - directiveCoordinate) * RADIUS;
    };

    getRadius = (startPoint, endPoint) => {
        if (Math.abs(startPoint - endPoint) < 20) {
            if (Math.abs(startPoint - endPoint) < 10) {
                RADIUS = 2;
            } else {
                RADIUS = 5;
            }
            return RADIUS;
        } else {
            RADIUS = 10;
            return RADIUS;
        }
    };

    getAriaLabel = () => {
        if (!_.isNil(this.props.types)) {
            switch (this.props.types.length) {
                case 0:
                    return 'transition';
                case 1:
                    return 'transition on ' + this.props.types[0];
                default:
                    return 'transition on ' + this.props.types.slice(0, this.props.types.length - 1).join(',') + ' and ' + this.props.types[this.props.types.length - 1];
            }
        } else {
            return ''
        }
    };

    handleClick = (e) => {
        e.stopPropagation();
        this.props.onTransitionSelect();
    };

    handleMouseDown = (e) => {
        // Avoid move viewport
        e.stopPropagation();
        if (!this.props.isLockedProcessPlan) {
            document.addEventListener('mousemove', this.handleMouseMove);
            document.addEventListener('mouseup', this.handleMouseUp);
            if (_.isFunction(this.props.onStartTransitionPointUpdate)) {
                this.baseStart = {
                    start: this.props.start,
                    end: this.props.start
                };
                let x = e.pageX * this.props.scale + this.props.svgPosition.x;
                let y = (e.pageY - HEADER_OFFSET) * this.props.scale + this.props.svgPosition.y;

                if (Math.pow(x - this.props.start.x, 2) + Math.pow(y - this.props.start.y, 2) <= Math.pow(UPDATE_RADIUS, 2)) {
                    this.props.onStartTransitionPointUpdate(
                        {
                            start: {x, y},
                            end: {x: this.props.end.x, y: this.props.end.y}
                        },
                        'start'
                    );
                } else if (Math.pow(x - this.props.end.x, 2) + Math.pow(y - this.props.end.y, 2) <= Math.pow(UPDATE_RADIUS, 2)) {
                    this.props.onStartTransitionPointUpdate(
                        {
                            start: {x: this.props.start.x - RECT_OFFSET, y: this.props.start.y - RECT_OFFSET},
                            end: {x, y}
                        },
                        'end'
                    );
                }
            }
        }
    };

    handleMouseUp = (e) => {
        // Avoid move viewport
        e.stopPropagation();
    };

    handleMouseEnter = () => {
        if (_.isFunction(this.props.onTransitionHighlight)) {
            this.props.onTransitionHighlight();
        }
    };

    handleMouseLeave = () => {
        if (_.isFunction(this.props.onTransitionHighlightReset)) {
            this.props.onTransitionHighlightReset();
        }
    };

    handleDragOver = (e) => {
        this.setState({isAvailableDrop: true});
        e.preventDefault();
    };

    handleDragLeave = (e) => {
        this.setState({isAvailableDrop: false});
        e.preventDefault();
    };

    handleDrop = (e) => {
        if (!this.props.isLockedProcessPlan) {
            let data;
            try {
                data = JSON.parse(e.dataTransfer.getData('text'));
            } catch (ignore) {
                return;
            }
            let position = {
                x: e.clientX - data.offsetX - (RECT_OFFSET),
                y: e.clientY - data.offsetY - (RECT_OFFSET) - HEADER_OFFSET
            };
            this.props.onInsertStep(data.stepType, position);
            this.setState({isAvailableDrop: false});
            e.preventDefault();
            e.stopPropagation();
        }
    };

    onKeyDown = (e) => {
        switch (e.key) {
            case 'Delete':
                e.stopPropagation();
                if (!this.props.isLockedProcessPlan) {
                    this.props.onRemoveTransition();
                }
                break;
            case 'ArrowRight':
                e.stopPropagation();
                this.props.onArrowKeyDown('right', e.ctrlKey);
                break;
            case 'ArrowLeft':
                e.stopPropagation();
                this.props.onArrowKeyDown('left', e.ctrlKey);
                break;
            case 'ArrowDown':
                e.stopPropagation();
                this.props.onArrowKeyDown('bottom');
                break;
            case 'ArrowUp':
                e.stopPropagation();
                this.props.onArrowKeyDown('top');
                break;
            case 'Enter':
                if (_.isFunction(this.props.onEnterKeyDown)) {
                    this.props.onEnterKeyDown();
                }
                break;
            default:
                break;
        }
    };

    getErrorIconCoordinates = () => {
        switch (this.props.end.position) {
            case 'left':
                return {
                    x: this.props.points[this.props.points.length - 1].x - RECT_OFFSET - ERROR_ICON_SIZE - ARROW_SIZE,
                    y: this.props.points[this.props.points.length - 1].y - ERROR_ICON_SIZE / 2
                };
            case 'top':
                return {
                    x: this.props.points[this.props.points.length - 1].x - ERROR_ICON_SIZE / 2,
                    y: this.props.points[this.props.points.length - 1].y - RECT_OFFSET - ERROR_ICON_SIZE - ARROW_SIZE
                };
            case 'right':
                return {
                    x: this.props.points[this.props.points.length - 1].x + RECT_OFFSET + ARROW_SIZE,
                    y: this.props.points[this.props.points.length - 1].y - ERROR_ICON_SIZE / 2
                };
            case 'bottom':
                return {
                    x: this.props.points[this.props.points.length - 1].x - ERROR_ICON_SIZE / 2,
                    y: this.props.points[this.props.points.length - 1].y + RECT_OFFSET + ARROW_SIZE
                };
            default:
                return null;
        }
    }
}

const PointShape = PropTypes.shape({
    x: PropTypes.number.isRequired,
    y: PropTypes.number.isRequired
});

const IconShape = PropTypes.shape({
    name: PropTypes.string.isRequired,
    innerText: PropTypes.string.isRequired,
    caption: PropTypes.string.isRequired
});

Transition.propTypes = {
    points: PropTypes.arrayOf(PointShape),
    className: PropTypes.string,
    start: PointShape.isRequired,
    end: PointShape.isRequired,
    arrowType: PropTypes.string,
    selected: PropTypes.bool.isRequired,
    focused: PropTypes.bool.isRequired,
    intersected: PropTypes.bool,
    disabled: PropTypes.bool.isRequired,
    scale: PropTypes.number.isRequired,
    svgPosition: PointShape.isRequired,
    highlighted: PropTypes.bool.isRequired,
    types: PropTypes.arrayOf(PropTypes.string),
    typesPoints: PropTypes.arrayOf(PointShape),
    icons: PropTypes.arrayOf(IconShape),
    iconsPoints: PropTypes.arrayOf(PointShape),
    showTransitionInfo: PropTypes.bool.isRequired,
    translate: PropTypes.func.isRequired,
    onTransitionSelect: PropTypes.func,
    onStartTransitionPointUpdate: PropTypes.func,
    onTransitionHighlight: PropTypes.func,
    onTransitionHighlightReset: PropTypes.func,
    onRemoveTransition: PropTypes.func,
    onArrowKeyDown: PropTypes.func,
    onEnterKeyDown: PropTypes.func,
    onInsertStep: PropTypes.func
};

Transition.defaultProps = {
    disabled: false
};

export default Transition;