export class Change {
    constructor(description) {
        this.description = description;
        this.isRedo = false;
    }

    applyStepsLayout(stepsLayout) {
        return stepsLayout;
    }
    revertStepsLayout(stepsLayout) {
        return stepsLayout;
    }
    applyRemoveStep(steps) {
        return steps;
    }
    applyRemoveStepLayout(stepsLayout) {
        return stepsLayout;
    }
    applyRemoveTransition(transitions) {
        return  transitions;
    }
    revertRemoveStep(stepsLayout) {
        return stepsLayout;
    }
    revertRemoveStepLayout(stepsLayout) {
        return stepsLayout;
    }
    revertRemoveTransition(transitions) {
        return  transitions;
    }

    applyAddStep(steps) {
        return steps;
    }
    applyAddStepLayout(stepsLayout) {
        return stepsLayout;
    }
    revertAddStep(steps) {
        return steps;
    }
    revertAddStepLayout(stepProperty) {
        return stepProperty;
    }
    applyAddStepProperty(stepProperty) {
        return stepProperty;
    }
    revertAddStepProperty(stepProperty) {
        return stepProperty;
    }

    applyStepPropertyChange(stepsProperties) {
        return stepsProperties;
    }
    revertStepPropertyChange(stepsProperties) {
        return stepsProperties;
    }

    applyProcessPlanNameChange(processPlan) {
        return processPlan;
    }
    revertProcessPlanNameChange(processPlan) {
        return processPlan;
    }

    applyProcessPlanPasswordChange(processPlan) {
        return processPlan;
    }
    revertProcessPlanPasswordChange(processPlan) {
        return processPlan;
    }

    applyProcessPlanEmailChange(processPlan) {
        return processPlan;
    }
    revertProcessPlanEmailChange(processPlan) {
        return processPlan;
    }

    applyProcessPlanCommentChange(processPlan) {
        return processPlan;
    }
    revertProcessPlanCommentChange(processPlan) {
        return processPlan;
    }
    applyRemoveTransitionProperty(transitionProperties) {
        return transitionProperties;
    }
    revertRemoveTransitionProperty(transitionProperties) {
        return transitionProperties;
    }

    applyTransitionPropertyChange(transitionProperties) {
        return transitionProperties;
    }
    revertTransitionPropertyChange(transitionProperties) {
        return transitionProperties;
    }

}