import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Variables from './variables/Variables.container';
import _ from 'lodash';

import './VariableInput.css'
import {
    getVariableDisplayText,
    VARIABLE_SCRIPT_DEFAULT_VALUE,
    VariableTypes
} from '../../../../redux/modules/availableObjects';

const TEXTAREA = 'textarea';
const BUTTON = 'button';
const ELEMENTS = [TEXTAREA, BUTTON];

const ARROW_DOWN_ICON = '';
const SPACE = '\u00A0';
const ELLIPSIS = '';
const MAX_VARIABLE_CHAR_NUMBER = 22;

const DIRECTION = {
    RIGHT: 'right',
    LEFT: 'left',
    UP: 'up',
    DOWN: 'down'
};

class VariableInput extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isActiveVariable: false,
            isModifiedVariable: false,
            activeVariableIndex: null,
            value: '',
            vars: [],
            cursorPosition: null,
            textAreaHeight: 0,
            focusedElementIndex: 0,
            changingActiveVariable: false,
            variableInputWidth: 0,
            activeVariableRange: [],
            selectionRangePosition: {
                start: null,
                end: null
            },
            keyCombinationPressed: false
        };
    }

    setVariableInput = (variableInput) => {
        this.variableInput = variableInput;
    };

    setVariablesContainer = (variablesContainer) => {
        this.variablesContainer = variablesContainer;
    };

    setAddButton = addButton => {
        this.addButton = addButton;
    };

    render() {
        let className = 'input ' + (this.props.className || ' ');
        let inputID = `input-${this.props.label}`;
        return (
            <div className={className}
                 tabIndex="0"
                onClick={this.handleClick}>
                <label id={`input-label-${this.props.label}`}>
                    {this.props.label}
                </label>
                <div className='variable-input-container'
                     onKeyDown={this.handleKeyDown}
                     onKeyUp={this.handleKeyUp}>
                    <div id={inputID} className='variable-input'>
                        <textarea id='variable_input_textarea'
                                  className={`variable-input-textarea textarea-${this.props.label}`}
                                  ref={this.setVariableInput}
                                  value={this.state.value}
                                  disabled={this.props.isLockedProcessPlan ? 'disabled' : ''}
                                  onChange={this.handleInputChange}
                                  onDrop={(ev) => ev.preventDefault()}
                                  onMouseDown={this.handleMouseDown}
                                  onScroll={this.handleScroll}/>
                        <Variables className={`${this.props.label}-variables-group`}
                                   id={`${this.props.label}-variables-group`}
                                   isActive={this.state.isActiveVariable}
                                   inputValue={this.state.inputValue}
                                   inputType={this.props.label}
                                   focused={this.state.isActiveVariable}
                                   translate={this.props.translate}
                                   onSelect={this.handleVariableSelect}
                                   onExitVariable={this.handleExitVariable}
                                   onVariableCreated={this.handleVariableSelect}
                        />
                    </div>

                    <span title={this.props.translate("NG_PPE_Add_variable_long")}
                          id={"btn_add_variable_" + _.snakeCase(this.props.label)}
                          ref={this.setAddButton}
                          role='button'
                          aria-label={this.props.translate("NG_PPE_Add_variable_long")}
                          className={'btn-add-variable' + ((this.props.isLockedProcessPlan || this.state.isModifiedVariable) ? ' btn-add-variable-disabled' : '')}
                          onClick={this.addNewVariable}
                          tabIndex={0}>
                        <i className="material-icons">add_box</i>
                    </span>
                </div>
                <div className='variables-container'
                     ref={this.setVariablesContainer}
                     onScroll={this.handleScrollContainer}>
                    {this.getVariablesContainerChildren()}
                </div>
            </div>
        );
    }

    getVariablesContainerChildren = () => {
        let value = [];
        _.map(this.state.vars, (variable, index) => {
            if (index === 0) {
                value.push(this.state.value.slice(0, variable.offset));
            } else {
                value.push(this.state.value.slice(this.state.vars[index - 1].offset + this.state.vars[index - 1].length, variable.offset))
            }
            let variableValue = this.state.value.slice(variable.offset, variable.offset + variable.length);
            let variableParts = variableValue.split(ARROW_DOWN_ICON);
            let isActiveVariable = ((this.state.activeVariableIndex === index || _.includes(this.state.activeVariableRange, index)) ? 'active-variable' : '');
            value.push(
                <span
                    key={'variable_element_' + index}
                    title={variable.title}
                    onClick={this.handleVariableClick}
                    className={'variable-element ' + isActiveVariable + (this.props.isLockedProcessPlan ? ' variable-element-disabled ' : '')}>
                    {variableParts[0]}
                    <span className='variable-element-arrow'
                          onClick={this.handleArrowDownClick}>{ARROW_DOWN_ICON}</span>
                    {variableParts[1]}
                </span>
            );

            if (index === this.state.vars.length - 1) {
                value.push(this.state.value.slice(variable.offset + variable.length));
            }
        });
        if (this.state.vars.length === 0) {
            value.push(this.state.value);
        }
        return value;
    };

    convertToState = (props) => {
        let value = '';
        let vars = [];
        let offset;
        let resultValue;
        let title;
        if (props.value) {
            _.map(props.value.vars, (variable, index) => {
                let variableProps = _.find(props.variables, el => el.id === variable.id);
                if (index === 0) {
                    value += props.value.value.slice(0, variable.offset);
                    offset = variable.offset + 1;
                } else {
                    value += props.value.value.slice(props.value.vars[index - 1].offset + props.value.vars[index - 1].length, variable.offset);
                    offset = value.length + 1;
                }
                title = getVariableDisplayText(variableProps);
                resultValue = `${SPACE}${title.length > MAX_VARIABLE_CHAR_NUMBER ? title.slice(0, MAX_VARIABLE_CHAR_NUMBER - 1) + SPACE + ELLIPSIS : title}${SPACE}${ARROW_DOWN_ICON}${SPACE}`;
                value += ' ' + resultValue + ' ';
                if (index === props.value.vars.length - 1) {
                    value += props.value.value.slice(variable.offset + variable.length);
                }
                vars.push({
                    id: variable.id,
                    offset,
                    length: resultValue.length,
                    title
                })
            });
            if (!_.isNil(props.value.vars) && props.value.vars.length === 0) {
                value = props.value.value;
            }
        }
        this.setState({
            value,
            vars
        });
    };

    componentDidMount() {
        this.convertToState(this.props);
        this.updateVariableContainerView();
        window.addEventListener('resize', this.updateVariableContainerView);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateVariableContainerView);
    }

    componentDidUpdate(prevProps, prevState) {
        if (_.isNil(prevProps.value) && !_.isNil(this.props.value)) {
            this.convertToState(this.props);
        }

        if (prevProps.value !== this.props.value) {
            this.convertToState(this.props);
        }

        if (this.state.textAreaHeight !== this.variableInput.offsetHeight) {
            this.updateVariableContainerView();
        }

        if (this.state.value.length > prevState.value.length) {
            this.variableInput.style.height = this.variableInput.scrollHeight + 'px';
            this.updateVariableContainerView();
        }
        if (this.state.cursorPosition !== this.variableInput.selectionStart && this.state.selectionRangePosition.start === this.state.selectionRangePosition.end) {
            this.variableInput.selectionStart = this.state.cursorPosition;
            this.variableInput.selectionEnd = this.state.cursorPosition;
        }

        if ((!prevProps.focused && this.props.focused)) {
            if (ELEMENTS[this.state.focusedElementIndex] === TEXTAREA) {
                this.variableInput.focus();
                this.setState({cursorPosition: this.state.value.length});
            } else {
                this.addButton.focus();
            }
        }

        if ((this.state.focusedElementIndex !== prevState.focusedElementIndex)) {
            if (ELEMENTS[this.state.focusedElementIndex] === TEXTAREA) {
                this.variableInput.focus();
            } else {
                this.addButton.focus();
            }
        }

        if (this.variableInput.offsetWidth !== prevState.variableInputWidth ||
            this.variableInput.offsetTop !== _.parseInt(this.variablesContainer.style.top)) {
            this.updateVariableContainerView();
        }

        if (this.state.selectionRangePosition.start !== prevState.selectionRangePosition.start ||
            this.state.selectionRangePosition.end !== prevState.selectionRangePosition.end) {
            this.variableInput.selectionStart = this.state.selectionRangePosition.start;
            this.variableInput.selectionEnd = this.state.selectionRangePosition.end;
        }

        if (this.state.cursorPosition !== prevState.cursorPosition) {
            let maxScrollLeft = this.variableInput.scrollWidth - this.variableInput.clientWidth;
            let multiplier = maxScrollLeft / this.variableInput.textLength;
            let scrollPositionDelta = (this.state.cursorPosition - prevState.cursorPosition) * multiplier;

            if (this.state.cursorPosition === this.variableInput.textLength || this.state.cursorPosition === this.variableInput.textLength - 1) {
                this.variableInput.scrollLeft = maxScrollLeft;
                this.variablesContainer.scrollLeft = maxScrollLeft;
            } else if (this.state.cursorPosition > prevState.cursorPosition) {
                this.variableInput.scrollLeft = this.variableInput.scrollLeft + scrollPositionDelta;
                this.variablesContainer.scrollLeft = this.variablesContainer.scrollLeft + scrollPositionDelta;
            } else {
                this.variableInput.scrollLeft = this.variableInput.scrollLeft + scrollPositionDelta;
                this.variablesContainer.scrollLeft = this.variablesContainer.scrollLeft + scrollPositionDelta;
            }
        }

        if (this.state.isModifiedVariable !== prevState.isModifiedVariable) {
            this.variableInput.selectionStart = this.state.selectionRangePosition.start;
            this.variableInput.selectionEnd = this.state.selectionRangePosition.end;
        }

        if(!prevProps.selected && this.props.selected){
            this.variableInput.focus();
        }

        if(prevProps.selected && !this.props.selected){
            this.variableInput.blur();
            this.setState({...this.state, isActiveVariable: false});
        }

    };

    updateVariableContainerView = () => {
        this.variablesContainer.style.height = this.variableInput.offsetHeight + 'px';
        this.variablesContainer.style.width = this.variableInput.offsetWidth + 'px';
        this.variablesContainer.style.top = this.variableInput.offsetTop + 'px';
        this.variablesContainer.style.left = this.variableInput.offsetLeft + 'px';
        this.setState({
            textAreaHeight: this.variableInput.offsetHeight,
            variableInputWidth: this.variableInput.offsetWidth
        });
    };

    convertToProps = (value, vars) => {
        let propsValue = '';
        let propsVars = [];
        let offset;
        _.map(vars, (variable, index) => {
            let variableProps = _.find(this.props.variables, el => el.id === variable.id);
            let variableValue = variableProps.type === VariableTypes.VARIABLE ? variableProps.value : VARIABLE_SCRIPT_DEFAULT_VALUE;
            if (index === 0) {
                propsValue += value.slice(0, variable.offset - 1);
                offset = variable.offset - 1;
            } else {
                propsValue += value.slice(vars[index - 1].offset + vars[index - 1].length + 1, variable.offset - 1);
                offset = propsValue.length;
            }
            propsValue += variableValue;
            if (index === vars.length - 1) {
                propsValue += value.slice(variable.offset + variable.length + 1);
            }
            propsVars.push({
                id: variable.id,
                offset: offset,
                length: variableValue.length
            })
        });
        if (vars.length === 0) {
            propsValue = value;
        }
        return {
            value: propsValue,
            vars: propsVars
        }
    };

    handleInputChange = (e) => {
        let deltaOffset = e.target.value.length - this.state.value.length;
        let vars = [...this.state.vars];
        let value = e.target.value;
        if (!_.isNull(this.state.activeVariableIndex)) {
            let variableToRemove = this.state.vars[this.state.activeVariableIndex];
            if (variableToRemove) {
                _.remove(vars, variableToRemove);
                vars = _.map(vars, variable => {
                    if (variable.offset > variableToRemove.offset) {
                        return {
                            ...variable,
                            offset: variable.offset - variableToRemove.length + 1
                        }
                    }
                    return variable;
                });
                this.setState({
                    value,
                    vars,
                    cursorPosition: variableToRemove.offset - 1,
                    activeVariableIndex: null
                });
            }
        } else {
            vars = _.map(vars, (variable) => {
                if (variable.offset >= e.target.selectionStart - deltaOffset) {
                    return {
                        ...variable,
                        offset: variable.offset + deltaOffset
                    }
                } else {
                    return variable;
                }
            });
            this.setState({
                value,
                vars,
                cursorPosition: e.target.selectionStart
            });
        }
        this.props.onChange(this.convertToProps(value, vars));
    };

    handleVariableSelect = (newVariable) => {
        let value = this.state.value;
        let title = getVariableDisplayText(newVariable);
        let newVariableValue = `${SPACE}${title.length > MAX_VARIABLE_CHAR_NUMBER ? title.slice(0, MAX_VARIABLE_CHAR_NUMBER - 1) + SPACE + ELLIPSIS : title}${SPACE}${ARROW_DOWN_ICON}${SPACE}`;
        let vars = this.state.vars;
        let cursorPosition = this.state.cursorPosition;

        if (this.state.changingActiveVariable && this.state.vars[this.state.activeVariableIndex]) {
            value = this.state.value.slice(0, this.state.vars[this.state.activeVariableIndex].offset) + newVariableValue
                + this.state.value.slice(this.state.vars[this.state.activeVariableIndex].offset + this.state.vars[this.state.activeVariableIndex].length);
            let deltaLength = newVariableValue.length - this.state.vars[this.state.activeVariableIndex].length;
            vars = _.map(vars, (variable, index) => {
                if (index === this.state.activeVariableIndex) {
                    return {
                        ...variable,
                        id: newVariable.id,
                        length: newVariableValue.length
                    }
                } else if (index > this.state.activeVariableIndex) {
                    return {
                        ...variable,
                        offset: variable.offset + deltaLength
                    }
                } else {
                    return variable;
                }
            });
            cursorPosition = this.state.vars[this.state.activeVariableIndex].offset + newVariableValue.length + 1;
            this.setState({changingActiveVariable: false, activeVariableIndex: null});
        } else if (!_.isNull(this.state.activeVariableIndex) && this.state.vars[this.state.activeVariableIndex]) { //adding variable after active one
            let variableToRemove = this.state.vars[this.state.activeVariableIndex];
            if (variableToRemove) {
                value = value.slice(0, variableToRemove.offset - 1) + value.slice(variableToRemove.offset + variableToRemove.length + 1);
                _.remove(vars, variableToRemove);
                vars = _.map(vars, variable => {
                    if (variable.offset > variableToRemove.offset) {
                        return {
                            ...variable,
                            offset: variable.offset - variableToRemove.length - 2
                        }
                    }
                    return variable;
                });
                cursorPosition = variableToRemove.offset - 1;
            }
            value = value.slice(0, cursorPosition) + ' ' + newVariableValue + ' ' + value.slice(cursorPosition);
            let indexToInsert = 0;
            vars = _.map(vars, (variable, index) => {
                if (variable.offset < cursorPosition && (index === vars.length - 1 || vars[index + 1].offset - 1 >= cursorPosition)) {
                    indexToInsert = index + 1;
                }
                if (variable.offset - 1 >= cursorPosition) {
                    return {
                        ...variable,
                        offset: variable.offset + newVariableValue.length + 2
                    }
                } else {
                    return variable;
                }
            });
            vars.splice(indexToInsert, 0, {
                id: newVariable.id,
                offset: cursorPosition + 1,
                length: newVariableValue.length,
                title: title
            });
            cursorPosition = cursorPosition + newVariableValue.length + 2;
        } else if (!_.isNull(this.state.cursorPosition)) { //adding variable at current cursor position
            if (this.state.activeVariableRange.length !== 0) {
                let selectionStartPosition = this.state.selectionRangePosition.start;
                let selectionEndPosition = this.state.selectionRangePosition.end;
                let offset = selectionEndPosition - selectionStartPosition;

                value = value.slice(0, selectionStartPosition) + value.slice(selectionEndPosition, value.length);
                vars = _.chain(vars)
                    .filter((variable, index) => !_.includes(this.state.activeVariableRange, index))
                    .map((variable, index) => {
                        if (_.includes(this.state.activeVariableRange, index) || index > _.last(this.state.activeVariableRange) || variable.offset > selectionEndPosition) {
                            return {
                                ...variable,
                                offset: variable.offset - offset
                            }
                        } else {
                            return {
                                ...variable
                            }
                        }
                    })
                    .value();
                cursorPosition = selectionStartPosition;
            }
            value = value.slice(0, cursorPosition) + ' ' + newVariableValue + ' ' + value.slice(cursorPosition);
            let indexToInsert = 0;
            vars = _.map(vars, (variable, index) => {
                if (variable.offset < cursorPosition && (index === vars.length - 1 || vars[index + 1].offset - 1 >= cursorPosition)) {
                    indexToInsert = index + 1;
                }
                if (variable.offset - 1 >= cursorPosition) {
                    return {
                        ...variable,
                        offset: variable.offset + newVariableValue.length + 2
                    }
                } else {
                    return variable;
                }
            });
            vars.splice(indexToInsert, 0, {
                id: newVariable.id,
                offset: cursorPosition + 1,
                length: newVariableValue.length,
                title: title
            });
            cursorPosition = cursorPosition + newVariableValue.length + 2;
        } else { //adding variable at the end of textarea
            vars.push({
                id: newVariable.id,
                offset: this.state.value.length + 1,
                length: newVariableValue.length,
                title: title
            });
            value = this.state.value + ' ' + newVariableValue + ' ';
            cursorPosition = this.state.value.length + newVariableValue.length + 2;
        }
        this.setState({
            value,
            vars,
            cursorPosition,
            selectionRangePosition: {
                start: cursorPosition,
                end: cursorPosition
            },
            activeVariableRange: [],
            activeVariableIndex: null,
            focusedElementIndex: _.findIndex(ELEMENTS, el => el === TEXTAREA),
            isActiveVariable: false,
            isModifiedVariable: false
        });
        this.variableInput.focus();
        this.props.onChange(this.convertToProps(value, vars));
    };

    handleExitVariable = () => {
        this.setState({
            isActiveVariable: false,
            isModifiedVariable: false
        });
        switch (ELEMENTS[this.state.focusedElementIndex]) {
            case TEXTAREA:
                let activeVariable = this.state.vars[this.state.activeVariableIndex];
                this.setState({
                    isActiveVariable: false,
                    activeVariableIndex: null,
                    cursorPosition: _.isNil(activeVariable) ? undefined : activeVariable.offset + activeVariable.length + 1,
                    changingActiveVariable: false
                });
                this.variableInput.focus();
                break;
            case BUTTON:
                this.addButton.focus();
                this.setState({
                    isActiveVariable: false
                });
                break;
            default:
                break;
        }
    };

    addNewVariable = () => {
        if (!this.props.isLockedProcessPlan) {
            if (!this.state.isModifiedVariable) {
                this.setState({
                    isActiveVariable: !this.state.isActiveVariable
                });
                _.map(this.variablesContainer.children, element => {
                    element.classList.remove('active-variable');
                });

            }
        }
    };

    handleMouseDown = (e) => {
        e.stopPropagation();
        document.addEventListener('mouseup', this.handleMouseUp);
    };

    handleMouseUp = (e) => {
        e.stopPropagation();
        let variableSelectedRange = [];
        let activeVariableIndex = null;

        if (e.target.selectionStart === this.state.selectionRangePosition.start && e.target.selectionEnd === this.state.selectionRangePosition.end) {
            this.setState({
                activeVariableIndex: activeVariableIndex,
                isActiveVariable: false,
                activeVariableRange: variableSelectedRange,
                focusedElementIndex: _.findIndex(ELEMENTS, el => el === TEXTAREA),
            });
        } else {
            if (this.variableInput.selectionStart === this.variableInput.selectionEnd) {
                _.forEach(this.state.vars, (variable, index) => {
                    if (this.variableInput.selectionStart === variable.offset + variable.length ||
                        this.variableInput.selectionStart === variable.offset) {
                        activeVariableIndex = index;
                    }
                });
            } else {
                _.forEach(this.state.vars, (variable, index) => {
                    switch (this.variableInput.selectionDirection) {
                        case 'forward':
                            if ((variable.offset >= this.variableInput.selectionStart && variable.offset <= this.variableInput.selectionEnd) ||
                                (variable.offset + variable.length === this.variableInput.selectionStart) || (variable.offset === this.variableInput.selectionEnd)) {
                                variableSelectedRange.push(index);
                            }
                            break;
                        case 'backward':
                            if ((variable.offset + variable.length <= this.variableInput.selectionEnd && variable.offset + variable.length >= this.variableInput.selectionStart)) {
                                variableSelectedRange.push(index);
                            }
                            break;
                        default:
                            break;
                    }
                });
            }

            if (variableSelectedRange.length) {
                let firstRangeVariable = this.state.vars[_.head(variableSelectedRange)];
                let lastRangeVariable = this.state.vars[_.last(variableSelectedRange)];
                let selectionStart = this.variableInput.selectionStart < firstRangeVariable.offset ?
                    this.variableInput.selectionStart : firstRangeVariable.offset;
                let selectionEnd = this.variableInput.selectionEnd < lastRangeVariable.offset + lastRangeVariable.length ?
                    lastRangeVariable.offset + lastRangeVariable.length : this.variableInput.selectionEnd;

                this.variableInput.setSelectionRange(selectionStart, selectionEnd);
                this.setState({
                    activeVariableIndex: activeVariableIndex,
                    isActiveVariable: false,
                    activeVariableRange: variableSelectedRange,
                    focusedElementIndex: _.findIndex(ELEMENTS, el => el === TEXTAREA),
                    cursorPosition: this.variableInput.selectionDirection === 'forward' ? this.variableInput.selectionEnd : this.variableInput.selectionStart,
                    selectionRangePosition: {
                        start: selectionStart,
                        end: selectionEnd
                    }
                });
            } else {
                this.setState({
                    activeVariableIndex: activeVariableIndex,
                    isActiveVariable: false,
                    activeVariableRange: variableSelectedRange,
                    focusedElementIndex: _.findIndex(ELEMENTS, el => el === TEXTAREA),
                    cursorPosition: this.variableInput.selectionDirection === 'forward' ? this.variableInput.selectionEnd : this.variableInput.selectionStart,
                    selectionRangePosition: {
                        start: this.variableInput.selectionStart,
                        end: this.variableInput.selectionEnd
                    }
                });
            }
        }
        document.removeEventListener('mouseup', this.handleMouseUp);
    };

    handleVariableClick = (e) => {
        if (!this.props.isLockedProcessPlan) {
            let target = _.includes(e.target.className.split(/\s+/), 'variable-element') ? e.target : e.target.parentElement;
            let activeVariableIndex = _.findIndex(this.variablesContainer.children, target);
            if (activeVariableIndex !== -1) {
                this.setState({
                    activeVariableIndex,
                    cursorPosition: this.state.vars[activeVariableIndex].offset + 1,
                    focusedElementIndex: _.findIndex(ELEMENTS, el => el === TEXTAREA),
                    activeVariableRange: [],
                    selectionRangePosition: {
                        start: this.state.vars[activeVariableIndex].offset,
                        end: this.state.vars[activeVariableIndex].offset + this.state.vars[activeVariableIndex].length
                    }
                });
                this.variableInput.focus();
            }
        }
    };

    handleArrowDownClick = () => {
        this.addNewVariable();
        this.variableInput.selectionStart = this.state.cursorPosition + 1;
        this.setState({
            changingActiveVariable: true,
            isActiveVariable: !this.state.isActiveVariable,
            isModifiedVariable: !this.state.isModifiedVariable,
            cursorPosition: this.state.cursorPosition + 1,
            activeVariableIndex: null
        });

    };

    selectRangeByKeyboard = (e, direction) => {
        let startSelectionPosition = e.target.selectionStart;
        let endSelectionPosition = e.target.selectionEnd;
        let variableSelectedRange = [];

        _.forEach(this.state.vars, (variable, index) => {
            if (variable.offset > startSelectionPosition && variable.offset + variable.length < endSelectionPosition) {
                variableSelectedRange.push(index);
            } else if (direction === DIRECTION.RIGHT) {
                if (endSelectionPosition >= variable.offset && endSelectionPosition <= variable.offset + variable.length) {
                    variableSelectedRange.push(index);
                    endSelectionPosition = variable.offset + variable.length + 1;
                } else if (startSelectionPosition >= variable.offset && startSelectionPosition <= variable.offset + variable.length) {
                    startSelectionPosition = variable.offset + variable.length + 1;
                }
            } else if (direction === DIRECTION.LEFT) {
                if (startSelectionPosition >= variable.offset && startSelectionPosition <= variable.offset + variable.length) {
                    variableSelectedRange.push(index);
                    startSelectionPosition = variable.offset - 1;
                } else if (endSelectionPosition >= variable.offset && endSelectionPosition <= variable.offset + variable.length) {
                    endSelectionPosition = variable.offset - 1;
                }
            }
        });
        this.setState({
            cursorPosition: startSelectionPosition,
            activeVariableRange: variableSelectedRange,
            selectionRangePosition: {
                start: startSelectionPosition,
                end: endSelectionPosition
            }
        });
    };

    removeVariableByIndex = variableIndex => {
        let variableToRemove = this.state.vars[variableIndex];
        if (variableToRemove) {
            let value = this.state.value.slice(0, variableToRemove.offset - 1) + this.state.value.slice(variableToRemove.offset + variableToRemove.length + 1);
            let vars = [...this.state.vars];
            _.remove(vars, variableToRemove);
            vars = _.map(vars, variable => {
                if (variable.offset > variableToRemove.offset) {
                    return {
                        ...variable,
                        offset: variable.offset - variableToRemove.length - 2
                    }
                }
                return variable;
            });
            this.setState({
                value,
                vars,
                cursorPosition: variableToRemove.offset - 1,
                activeVariableIndex: null
            });
            this.props.onChange(this.convertToProps(value, vars));
        }
    };

    handleScroll = () => {
        this.variableInput.scrollLeft = Math.round(this.variableInput.scrollLeft);
        this.variablesContainer.scrollLeft = this.variableInput.scrollLeft;
    };

    handleScrollContainer = () => {
        this.variablesContainer.scrollLeft = Math.round(this.variablesContainer.scrollLeft);
        this.variableInput.scrollLeft = this.variablesContainer.scrollLeft;
    };

    deleteRange = () => {
        let selectionStartPosition = this.state.selectionRangePosition.start;
        let selectionEndPosition = this.state.selectionRangePosition.end;
        let offset = selectionEndPosition - selectionStartPosition;

        let value = this.state.value.slice(0, selectionStartPosition) + this.state.value.slice(selectionEndPosition, this.state.value.length);
        let vars = [...this.state.vars];
        vars = _.chain(vars)
            .filter((variable, index) => !_.includes(this.state.activeVariableRange, index))
            .map((variable, index) => {
                if (_.includes(this.state.activeVariableRange, index) || index > _.last(this.state.activeVariableRange) || variable.offset > selectionEndPosition) {
                    return {
                        ...variable,
                        offset: variable.offset - offset
                    }
                } else {
                    return {
                        ...variable
                    }
                }
            })
            .value();
        this.setState({
            value,
            vars,
            cursorPosition: selectionStartPosition,
            activeVariableRange: [],
            selectionRangePosition: {
                start: selectionStartPosition,
                end: selectionStartPosition
            }
        });
        this.props.onChange(this.convertToProps(value, vars));
    };

    deleteFullWord = (e) => {
        if (!_.isNull(this.state.activeVariableIndex)) {
            this.removeVariableByIndex(this.state.activeVariableIndex);
            return;
        } else if (this.state.selectionRangePosition.start !== this.state.selectionRangePosition.end) {
            this.deleteRange();
            return;
        }

        let startPositionToDelete = e.target.selectionStart;
        let firstSpace = this.state.value.slice(startPositionToDelete).match(/\s+/);
        let endPositionToDelete = _.isNull(firstSpace) ? this.state.value.length + 1 : startPositionToDelete + firstSpace.index;
        let activeVariableRange = [];

        if (startPositionToDelete === endPositionToDelete) {
            let variableToDeleteIndex = _.findIndex(this.state.vars, variable => variable.offset + 1 === endPositionToDelete + firstSpace[0].length);
            if (variableToDeleteIndex !== -1) {
                if (this.state.vars[variableToDeleteIndex].offset - 1 === endPositionToDelete) {
                    endPositionToDelete = this.state.vars[variableToDeleteIndex].offset + this.state.vars[variableToDeleteIndex].length + 1;
                    activeVariableRange.push(variableToDeleteIndex);
                } else {
                    endPositionToDelete = this.state.vars[variableToDeleteIndex].offset - 1;
                }
            } else {
                endPositionToDelete += firstSpace[0].length;
            }
        }

        this.setState({
            cursorPosition: startPositionToDelete,
            selectionRangePosition: {start: startPositionToDelete, end: endPositionToDelete},
            activeVariableRange
        }, this.deleteRange);
    };

    deleteFullWordByBackspace = (e) => {
        if (!_.isNull(this.state.activeVariableIndex)) {
            this.removeVariableByIndex(this.state.activeVariableIndex);
            return;
        } else if (this.state.selectionRangePosition.start !== this.state.selectionRangePosition.end) {
            this.deleteRange();
            return;
        }

        let endPositionToDelete = e.target.selectionStart;
        let firstSpaceWithText = this.state.value.slice(0, endPositionToDelete).match(/[^\s]*\s*$/);
        let startPositionToDelete = _.isNull(firstSpaceWithText) ? 0 : firstSpaceWithText.index;
        let activeVariableRange = [];

        let variableToDeleteIndex = _.findIndex(this.state.vars, variable => variable.offset + variable.length - 2 === startPositionToDelete);

        if (variableToDeleteIndex !== -1) {
            startPositionToDelete = this.state.vars[variableToDeleteIndex].offset - 1;
            activeVariableRange.push(variableToDeleteIndex);
        }

        this.setState({
            cursorPosition: startPositionToDelete,
            selectionRangePosition: {start: startPositionToDelete, end: endPositionToDelete},
            activeVariableRange
        }, this.deleteRange);
    };

    handleCursorPositionChanged = (e, direction) => {
        let cursorPosition = e.target.selectionStart;

        if (e.target.selectionEnd !== this.state.selectionRangePosition.end + 1 && direction === 'right') {
            cursorPosition = e.target.selectionEnd + 1;
        } else if (e.target.selectionEnd !== this.state.selectionRangePosition.end + 1 && direction === 'down') {
            cursorPosition = this.state.value.length;
        } else if (e.target.selectionStart !== this.state.selectionRangePosition.start + 1 && direction === 'up') {
            cursorPosition = 0;
        }

        let activeVariableIndex = null;

        _.forEach(this.state.vars, (variable, index) => {
            if (cursorPosition === variable.offset && direction === DIRECTION.LEFT) {
                cursorPosition = variable.offset - 1;
            } else if (cursorPosition === variable.offset + 2 && direction === DIRECTION.RIGHT) {
                cursorPosition = variable.offset + variable.length + 1;
            } else if (variable.offset <= cursorPosition && variable.offset + variable.length >= cursorPosition) {
                activeVariableIndex = index;
                cursorPosition = variable.offset + 1;
            }
        });
        this.setState({
            activeVariableIndex,
            activeVariableRange: [],
            cursorPosition: cursorPosition,
            selectionRangePosition: {start: cursorPosition, end: cursorPosition}
        });
    };

    handleKeyDown = (e) => {
        if (this.state.isActiveVariable) {
            return;
        }

        let variableToRemoveIndex;

        if (e.ctrlKey) {
            if (e.which === 65) { //A
                e.preventDefault();
                e.target.selectionStart = 0;
                e.target.selectionEnd = this.state.value.length;
                this.selectRangeByKeyboard(e);
            } else if (e.which === 8) { //BackSpace
                e.preventDefault();
                this.deleteFullWordByBackspace(e);
            } else if (e.which === 46) { //Delete
                e.preventDefault();
                this.deleteFullWord(e);
            }
        } else if (e.altKey) {
            switch (e.key) {
                case 'ArrowLeft':
                    e.preventDefault();
                    if (this.state.focusedElementIndex > 0) {
                        e.stopPropagation();
                        this.setState({focusedElementIndex: this.state.focusedElementIndex - 1});
                    }
                    break;
                case 'ArrowRight':
                    e.preventDefault();
                    if (this.state.focusedElementIndex < ELEMENTS.length - 1) {
                        e.stopPropagation();
                        this.setState({focusedElementIndex: this.state.focusedElementIndex + 1});
                    }
                    break;
                default:
                    break;
            }
        } else if (!e.shiftKey) {
            switch (e.key) {
                case 'ArrowDown':
                    if (this.state.selectionRangePosition.end !== this.state.selectionRangePosition.start) {
                        e.preventDefault();
                        e.target.selectionStart = this.state.selectionRangePosition.end;
                        e.target.selectionEnd = this.state.selectionRangePosition.end;
                    }
                    break;
                case 'ArrowUp':
                    if (this.state.selectionRangePosition.end !== this.state.selectionRangePosition.start) {
                        e.preventDefault();
                        e.target.selectionStart = this.state.selectionRangePosition.start;
                        e.target.selectionEnd = this.state.selectionRangePosition.start;
                    }
                    break;
                case 'Enter':
                case 'Return':
                    if (ELEMENTS[this.state.focusedElementIndex] === BUTTON && !this.state.isActiveVariable) {
                        e.preventDefault();
                        this.addNewVariable();
                    } else if (ELEMENTS[this.state.focusedElementIndex] === TEXTAREA && !_.isNull(this.state.activeVariableIndex)) {
                        e.preventDefault();
                        this.setState({changingActiveVariable: true});
                        this.addNewVariable();
                    } else if (!this.props.multiLine) {
                        e.preventDefault();
                    }
                    break;
                case 'PageUp':
                    this.setState({
                        cursorPosition: 0
                    });
                    this.variableInput.scrollLeft = 0;
                    this.variablesContainer.scrollLeft = 0;
                    break;
                case 'PageDown':
                    this.setState({
                        cursorPosition: this.variableInput.value.length + 1
                    });
                    let maxScrollLeft = this.variableInput.scrollWidth - this.variableInput.clientWidth;
                    this.variableInput.scrollLeft = maxScrollLeft;
                    this.variablesContainer.scrollLeft = maxScrollLeft;
                    break;
                case 'Delete':
                    if (ELEMENTS[this.state.focusedElementIndex] === TEXTAREA) {
                        if (this.state.activeVariableRange.length !== 0) {
                            e.preventDefault();
                            this.deleteRange();
                        } else {
                            variableToRemoveIndex = this.state.activeVariableIndex;
                            if (_.isNull(variableToRemoveIndex)) {
                                _.map(this.state.vars, (variable, index) => {
                                    if (e.target.selectionStart === variable.offset - 1) {
                                        variableToRemoveIndex = index;
                                    }
                                });
                            }
                            if (!_.isNull(variableToRemoveIndex)) {
                                e.preventDefault();
                                this.removeVariableByIndex(variableToRemoveIndex);
                            }
                        }
                    }
                    break;
                case 'Backspace':
                    if (ELEMENTS[this.state.focusedElementIndex] === TEXTAREA) {
                        if (this.state.activeVariableRange.length !== 0) {
                            e.preventDefault();
                            this.deleteRange();
                        } else {
                            variableToRemoveIndex = this.state.activeVariableIndex;
                            if (_.isNull(variableToRemoveIndex)) {
                                _.map(this.state.vars, (variable, index) => {
                                    if (e.target.selectionStart === variable.offset + variable.length + 1) {
                                        variableToRemoveIndex = index;
                                    }
                                });
                            }
                            if (!_.isNull(variableToRemoveIndex)) {
                                e.preventDefault();
                                this.removeVariableByIndex(variableToRemoveIndex);
                            }
                        }
                    }
                    break;
                default:
                    if (e.key !== 'ArrowLeft' && e.key !== 'ArrowRight') {
                        if (this.state.activeVariableRange.length !== 0) {
                            this.deleteRange();
                        }
                    }
                    break;
            }
        }
    };

    handleKeyUp = (e) => {
        if (this.state.isActiveVariable) {
            return;
        }

        if (e.ctrlKey) {
            if (e.shiftKey) {
                switch (e.key) {
                    case 'End':
                        this.variableInput.scrollLeft = this.variableInput.scrollWidth - this.variableInput.clientWidth;
                        this.variablesContainer.scrollLeft = this.variableInput.scrollWidth - this.variableInput.clientWidth;

                        e.target.selectionEnd = this.state.value.length;
                        this.selectRangeByKeyboard(e, DIRECTION.RIGHT);
                        break;
                    case 'PageDown':
                    case 'ArrowDown':
                        if (e.target.selectionStart === 0 && e.target.selectionEnd !== e.target.selectionStart) {
                            e.target.selectionStart = e.target.selectionEnd;
                        } else {
                            this.variableInput.scrollLeft = this.variableInput.scrollWidth - this.variableInput.clientWidth;
                            this.variablesContainer.scrollLeft = this.variableInput.scrollWidth - this.variableInput.clientWidth;

                            e.target.selectionEnd = this.state.value.length;
                        }
                        this.selectRangeByKeyboard(e, DIRECTION.RIGHT);
                        break;
                    case 'Home':
                        this.variableInput.scrollLeft = 0;
                        this.variablesContainer.scrollLeft = 0;

                        e.target.selectionStart = 0;
                        this.selectRangeByKeyboard(e, DIRECTION.LEFT);
                        break;
                    case 'PageUp':
                    case 'ArrowUp':
                        if (e.target.selectionEnd === this.state.value.length && e.target.selectionStart !== e.target.selectionEnd) {
                            e.target.selectionEnd = e.target.selectionStart;
                        } else {
                            this.variableInput.scrollLeft = 0;
                            this.variablesContainer.scrollLeft = 0;

                            e.target.selectionStart = 0;
                        }
                        this.selectRangeByKeyboard(e, DIRECTION.LEFT);
                        break;
                    case 'ArrowLeft':
                        this.selectRangeByKeyboard(e, DIRECTION.LEFT);
                        break;
                    case 'ArrowRight':
                        _.forEach(this.state.vars, variable => {
                            if (variable.offset + 1 === e.target.selectionEnd) {
                                e.target.selectionEnd = variable.offset - 1;
                            } else if (variable.offset + 1 === e.target.selectionStart) {
                                e.target.selectionStart = variable.offset - 1;
                            }
                        });
                        this.selectRangeByKeyboard(e, DIRECTION.RIGHT);
                        break;
                    default:
                        break;
                }
            }
        } else if (e.shiftKey) {
            switch (e.key) {
                case 'Home':
                case 'ArrowUp':
                case 'ArrowLeft':
                    this.selectRangeByKeyboard(e, DIRECTION.LEFT);
                    break;
                case 'End':
                case 'ArrowDown':
                case 'ArrowRight':
                    this.selectRangeByKeyboard(e, DIRECTION.RIGHT);
                    break;
                case 'PageDown':
                    if (e.target.selectionStart === 0 && e.target.selectionEnd !== e.target.selectionStart) {
                        e.target.selectionStart = e.target.selectionEnd;
                    } else {
                        e.target.selectionEnd = this.state.value.length;
                    }
                    this.selectRangeByKeyboard(e, DIRECTION.RIGHT);
                    break;
                case 'PageUp':
                    if (e.target.selectionEnd === this.state.value.length && e.target.selectionStart !== e.target.selectionEnd) {
                        e.target.selectionEnd = e.target.selectionStart;
                    } else {
                        e.target.selectionStart = 0;
                    }
                    this.selectRangeByKeyboard(e, DIRECTION.LEFT);
                    break;
                default:
                    break;
            }
        } else {
            switch (e.key) {
                case 'ArrowRight':
                    this.handleCursorPositionChanged(e, DIRECTION.RIGHT);
                    break;
                case 'ArrowLeft':
                    this.handleCursorPositionChanged(e, DIRECTION.LEFT);
                    break;
                case 'ArrowDown':
                    this.handleCursorPositionChanged(e, DIRECTION.DOWN);
                    break;
                case 'ArrowUp':
                    this.handleCursorPositionChanged(e, DIRECTION.UP);
                    break;
                default:
                    break;
            }
        }
    };

    handleClick = () => {
       if(!_.isUndefined(this.props.id)){
           this.props.onSetSelection()
       }
    };
}

const VarsShape = PropTypes.shape({
    id: PropTypes.string.isRequired,
    offset: PropTypes.number.isRequired,
    length: PropTypes.number.isRequired
});

const ValueShape = PropTypes.shape({
    value: PropTypes.string.isRequired,
    vars: PropTypes.arrayOf(VarsShape)
});

const VariablesShape = PropTypes.shape({
    id: PropTypes.string.isRequired,
    key: PropTypes.string.isRequired,
    value: PropTypes.string,
    type: PropTypes.oneOf([VariableTypes.VARIABLE, VariableTypes.SCRIPT])
});

VariableInput.propTypes = {
    id: PropTypes.string,
    label: PropTypes.string.isRequired,
    value: PropTypes.oneOfType([ValueShape, PropTypes.string]),
    variables: PropTypes.arrayOf(VariablesShape),
    multiLine: PropTypes.bool.isRequired,
    translate: PropTypes.func.isRequired,
    onChange: PropTypes.func.isRequired,
    onSetSelection: PropTypes.func.isRequired
};

export default VariableInput;
