import {createAction, handleActions} from 'redux-actions';
import {ofType} from 'redux-observable';
import {filter, map} from 'rxjs/operators';
import api from "../../api";
import {
    convertProcessPlan,
    convertToJson,
    generateStepsLayout,
    generationOptions
} from "../../api/processPlanConversion";
import {areSettingsLoaded, fitTheView, getViewSettings, isZoomToFitEnabled, loadViewSettings} from './drawingArea';
import _ from 'lodash';
import {getProcessPlanAvailableObjects} from './availableObjects';

export const DRAWING_AREA = 'drawing-area';
export const SIDE_PANEL = 'side-panel';

//- Actions
export const loadProcessPlan = createAction('PROCESS_PLAN_LOAD', sampleName => api.loadProcessPlan(sampleName).then(convertProcessPlan));
export const showProcessPlanInfo = createAction('SHOW_PROCESS_PLAN_INFO', type => type);
export const setFocusOnGlobalArea = createAction('FOCUS_ON_GLOBAL_AREA_SET', globalArea => globalArea);
export const setProcessPlanJson = createAction('PROCESS_PLAN_JSON_SET', processPlanJson => processPlanJson);
export const setProcessPlan = createAction('PROCESS_PLAN_LOAD', processPlan => processPlan);
export const setPasswordFlag = createAction('CHANGE_PASSWORD', hasPassword => hasPassword);
export const lockProcessPlan = createAction('CHANGE_PLAN_LOCK', locked => locked);


export const exportProcessPlan = () => (dispatch, getState) => {
    dispatch(setProcessPlanJson(convertToJson(getState())));
};

export const isNeedToUpdateLayout = () => (dispatch, getState) => {
    let processPlan = convertToJson(getState());
    if (processPlan !== 'Error') {
        processPlan = JSON.parse(processPlan);
        return !_.isEqual(processPlan.layout.stepsLayout, generateStepsLayout(processPlan, generationOptions))
    } else {
        return false
    }
};

export const updateLayout = () => (dispatch, getState) => {
    let processPlan = convertToJson(getState());
    processPlan = JSON.parse(processPlan);
    delete processPlan.layout;
    return {
        currentProcessPlan: getState(),
        newProcessPlan: convertProcessPlan(processPlan)
    }
};

api.subscribe('saveProcessPlan', () => {
    const state = window.store.getState();
    api.updateSettings(getViewSettings(state)).finally(
        () => api.saveProcessPlan(convertToJson(state))
    );
});

//- State
const initialState = {
    id: null,
    name: null,
    author: null,
    comment: null,
    email: null,
    locked: null,
    hasPassword: null,
    group: null,
    dialog: null,
    loaded: false,
    isHidden: true,
    focusedArea: null,
    validationSucceed: true,
    addFilesStep: false,
    processPlanJson: ''
};

//- Reducer
export default handleActions({
    PROCESS_PLAN_LOAD: (state, action) => {
        return {...state, ...action.payload.baseInfo, loaded: true};
    },
    CHANGE_PASSWORD: (state, action) => {
        return {...state, hasPassword: action.payload};
    },
    CHANGE_PLAN_LOCK: (state, action) => {
        return {...state, locked: action.payload};
    },
    CHANGE_APPLY: (state, {payload: change}) => {
        switch (change.description) {
            case 'Change process plan name':
                return change.applyProcessPlanNameChange(state);
            case 'Change process plan author':
                return change.applyProcessPlanAuthorChange(state);
            case 'Change process plan email':
                return change.applyProcessPlanEmailChange(state);
            case 'Change process plan comment':
                return change.applyProcessPlanCommentChange(state);
            case 'Change process plan group':
                return change.applyProcessPlanGroupChange(state);
            case 'Change process plan dialog':
                return change.applyProcessPlanDialogChange(state);
            default:
                return state;
        }
    },
    CHANGE_REVERT: (state, {payload: change}) => {
        switch (change.description) {
            case 'Change process plan name':
                return change.revertProcessPlanNameChange(state);
            case 'Change process plan author':
                return change.revertProcessPlanAuthorChange(state);
            case 'Change process plan email':
                return change.revertProcessPlanEmailChange(state);
            case 'Change process plan comment':
                return change.revertProcessPlanCommentChange(state);
            case 'Change process plan group':
                return change.revertProcessPlanGroupChange(state);
            case 'Change process plan dialog':
                return change.revertProcessPlanDialogChange(state);
            default:
                return state;
        }
    },
    SHOW_PROCESS_PLAN_INFO: (state, action) => {
        return {...state, isHidden: action.payload};
    },
    FOCUS_ON_GLOBAL_AREA_SET: (state, action) => {
        return {...state, focusedArea: action.payload};
    },
    PROCESS_PLAN_JSON_SET: (state, action) => {
        return {...state, processPlanJson: action.payload};
    }
}, initialState);

//- Selectors
export const getProcessPlanInfo = state => state.processPlan;
export const isProcessPlanLoaded = state => getProcessPlanInfo(state).loaded;
export const getFocusedArea = state => getProcessPlanInfo(state).focusedArea;
export const getProcessPlanJson = state => getProcessPlanInfo(state).processPlanJson;
export const getProcessPlanAccess = state => getProcessPlanInfo(state).locked;
export const getAddFilesAvailability = state => getProcessPlanInfo(state).addFilesStep;
export const getProcessPlanName = state => getProcessPlanInfo(state).name;

export const getProcessPlanNameValidationTooltip = state => {
    let processPlanName = !_.isNull(getProcessPlanName(state)) ? getProcessPlanName(state).value : null;
    return (!_.isNull(processPlanName) && (!_.isUndefined(_.find(getProcessPlanAvailableObjects(state), object => object.name === processPlanName)) || processPlanName.length === 0))
};

//-Epics
export const processplanSettingsLoadEpic = (action$, state$) => action$.pipe(
    ofType(
        loadProcessPlan.toString(),
        loadViewSettings.toString()
    ),
    filter(() =>
        isProcessPlanLoaded(state$.value) &&
        areSettingsLoaded(state$.value) &&
        isZoomToFitEnabled(state$.value)
    ),
    map(() => fitTheView(state$.value.stepsLayout))
);
