import {Change} from './Change';
import _ from 'lodash';
import {availableTransitionsByStepType, postProcessingOptionsByStartStepType} from "../../api/processPlanConversion";

export class AddTransitionChange extends Change {
    constructor(tempTransition) {
        super('Add transition');
        this.tempTransition = tempTransition;
    }

    applyAddTransition(transitions) {
        this.transitions = transitions;
        return {
            ...transitions, [this.tempTransition.id]: {
                id: this.tempTransition.id,
                startStepId: this.tempTransition.start.stepId,
                endStepId: this.tempTransition.end.stepId,
                selected: false,
                highlighted: false,
                intersected: false
            }
        }
    }

    revertAddTransition(transitions) {
        this.transitions = _.omit(transitions, [this.tempTransition.id]);
        return this.transitions;
    }

    applyAddTransitionLayout(transitions) {
        if (_.isNil(this.tempTransition)) {
            return transitions;
        }
        return {
            ...transitions, [this.tempTransition.id]: {
                start: {position: this.tempTransition.start.position},
                end: {position: this.tempTransition.end.position},
            }
        }
    }

    revertAddTransitionLayout(transitionsLayout) {
        return _.omit(transitionsLayout, [this.tempTransition.id]);
    }

    applyAddTransitionProperties(transitionProperties) {
        if (_.isNil(this.tempTransition)) {
            return transitionProperties;
        }
        let properties = [...transitionProperties];
        if (this.tempTransition.start.stepId === 'start') {
            properties.push({transitionId: this.tempTransition.id, name: 'types', value: ['out']});
        } else {
            let availableTypes = availableTransitionsByStepType[this.tempTransition.start.type];
            _.forEach(this.transitions, transition => {
                if (transition.startStepId === this.tempTransition.start.stepId) {
                    let checkedTransitionValues = _.find(properties, property => property.transitionId === transition.id).value;
                    availableTypes = _.difference(availableTypes, checkedTransitionValues);
                }
            });

            properties.push({
                transitionId: this.tempTransition.id,
                name: 'types',
                value: availableTypes
            });
        }

        _.forEach(postProcessingOptionsByStartStepType[this.tempTransition.start.type], option => {
            switch (option) {
                case 'certify':
                    properties.push({transitionId: this.tempTransition.id, name: 'certify', value: false});
                    break;
                case 'create_report':
                    properties.push({
                        transitionId: this.tempTransition.id,
                        name: 'create_report',
                        value: {checked: false, values: []}
                    });
                    break;
                default:
                    break;
            }
        });
        return properties;
    }

    revertAddTransitionProperties(transitionProperties) {
        return _.filter(transitionProperties, transitionProperty => transitionProperty.transitionId !== this.tempTransition.id);
    }
}
