import React, {Component} from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';

import SidePanel from '../SidePanel';
import Select from './select/Select.container';
import CheckBox from './checkBox/CheckBox.container';
import VariableCheckBox from './variableCheckbox/VariableCheckBox.container';
import Input from './input/Input.container';
import VariableInput from './variableInput/VariableInput.container';
import CheckBoxGroup from './checkBoxGroup/CheckBoxGroup.container';
import CheckableMultiSelect from './multiSelect/CheckableMultiSelect.container';
import ExternalObjectSelect from './ExternalObjectSelect/ExternalObjectSelect.container';

import {translatedTransitionCheckboxType} from "../../../dictionary"

import './PropPanel.css';


class PropPanel extends Component {
    constructor(props) {
        super(props);

        this.properties = []; // TODO: move to state
        this.state = {
            selectedPropertyIndex: 0
        }
    }

    setPropPanel = (propPanel) => {
        this.propPanel = propPanel;
    };

    render() {
        return (
            <div className='properties-panel' ref={this.setPropPanel} onKeyDown={this.handleKeyDown}
                 id='properties-panel' tabIndex="0">
                <SidePanel
                    children={this.renderPropertyGroups()}
                    isLockedProcessPlan={this.props.isLockedProcessPlan}
                />
            </div>
        );
    }

    renderPropertyGroups = () => {
        if (_.isNil(this.props.propertyGroups)) {
            return null;
        }
        if (this.properties.length !== 0) {
            this.properties = [];
        }
        return _.map(this.props.propertyGroups, group => {
            let label = _.isNil(group.name) ? null : <label>{this.props.translate(translatedTransitionCheckboxType[group.name])}</label>;
            return (
                <div className="property-group" key={label}>
                    {label}
                    {_.map(group.properties, this.renderProperty)}
                </div>
            );
        })
    };

    renderProperty = (property) => {
        let key = `${property.name}-${this.props.objectId}`;
        this.properties.push(key);

        let onChange = this.handlePropertyChange(property.objectId, property.name);
        let groupLabelId = _.isNil(property.controlDescriptor.group) ? ' ' : `group-label-${property.controlDescriptor.group.replace(' ', '-')}`;
        switch (property.controlDescriptor.type) {
            case 'external-object-select':
                return (
                    <ExternalObjectSelect key={key} className='property'
                                          id={key}
                                          type={property.type}
                                          focused={this.properties[this.state.selectedPropertyIndex] === key && this.props.selected}
                                          label={property.controlDescriptor.label}
                                          value={property.value}
                                          options={property.controlDescriptor.options}
                                          disabled={property.disabled || this.props.isLockedProcessPlan}
                                          translate={this.props.translate}
                                          onChange={onChange}
                                          onSelect={this.handleSelect}/>
                );
            case 'select':
                return (
                    <Select key={key} className='property'
                            id={key}
                            focused={this.properties[this.state.selectedPropertyIndex] === key && this.props.selected}
                            label={property.controlDescriptor.label}
                            value={property.value}
                            options={property.controlDescriptor.options}
                            disabled={property.disabled  || this.props.isLockedProcessPlan}
                            translate={this.props.translate}
                            onChange={onChange}
                            onSelect={this.handleSelect}/>
                );
            case 'checkbox':
                return (
                    <CheckBox key={key} className={'property' + (property.disabled ? ' disabledProperty ' : '')}
                              id={key}
                              focused={this.properties[this.state.selectedPropertyIndex] === key && this.props.selected}
                              groupLabelId={groupLabelId}
                              label={property.controlDescriptor.label}
                              value={property.value || false}
                              disabled={property.disabled || this.props.isLockedProcessPlan}
                              translate={this.props.translate}
                              onChange={onChange}
                              onSelect={this.handleSelect}/>
                );
            case 'variable-checkbox':
                return (
                    <VariableCheckBox key={key} className='property'
                                      id={key}
                                      selected={this.properties[this.state.selectedPropertyIndex] === key}
                                      focused={this.properties[this.state.selectedPropertyIndex] === key && this.props.selected}
                                      groupLabelId={groupLabelId}
                                      label={property.controlDescriptor.label}
                                      value={property.value || false}
                                      disabled={property.disabled || this.props.isLockedProcessPlan}
                                      translate={this.props.translate}
                                      onChange={onChange}
                                      onSelect={this.handleSelect}/>
                );
            case 'input':
                return (
                    <Input key={key} className='property'
                           id={key}
                           focused={this.properties[this.state.selectedPropertyIndex] === key && this.props.selected}
                           label={property.controlDescriptor.label}
                           value={property.value || ''}
                           disabled={property.disabled || this.props.isLockedProcessPlan}
                           translate={this.props.translate}
                           onChange={onChange}
                           onSelect={this.handleSelect}/>
                );
            case 'variable-input':
                return (
                    <VariableInput key={key} className='property'
                                   id={key}
                                   selected={this.properties[this.state.selectedPropertyIndex] === key}
                                   focused={this.properties[this.state.selectedPropertyIndex] === key && this.props.selected}
                                   label={property.controlDescriptor.label}
                                   value={property.value || ''}
                                   isLockedProcessPlan={property.disabled || this.props.isLockedProcessPlan}
                                   translate={this.props.translate}
                                   onChange={onChange}
                                   onSelect={this.handleSelect}
                                   multiLine={true}/>
                );
            case 'checkbox-group':
                return (
                    <CheckBoxGroup key={key} className='property'
                                   id={key}
                                   focused={this.properties[this.state.selectedPropertyIndex] === key && this.props.selected}
                                   label={property.controlDescriptor.label}
                                   values={property.value}
                                   options={property.controlDescriptor.options}
                                   selectedOptions={property.controlDescriptor.selectedOptions}
                                   disabled={property.disabled  || this.props.isLockedProcessPlan}
                                   translate={this.props.translate}
                                   onChange={onChange}
                                   onSelect={this.handleSelect}/>
                );
            case 'checkable-multiselect':
                return (
                    <CheckableMultiSelect key={key} className={'property' + (property.disabled ? ' disabledProperty ' : '')}
                                          id={key}
                                          value={property.value}
                                          focused={this.properties[this.state.selectedPropertyIndex] === key && this.props.selected}
                                          label={property.controlDescriptor.label}
                                          groupLabelId={groupLabelId}
                                          options={property.controlDescriptor.options}
                                          disabled={property.disabled  || this.props.isLockedProcessPlan}
                                          parentId={this.props.objectId}
                                          translate={this.props.translate}
                                          onChange={onChange}
                                          onSelect={this.handleSelect}/>
                );
            default:
                return null;
        }
    };

    componentDidUpdate(prevProps) {
        if (this.props.objectId !== prevProps.objectId) {
            this.setState({...this.state, selectedPropertiesIndex: 0})
        }
    }

    handlePropertyChange = (objectId, name) => (value) => {
        this.props.onPropertyChange(objectId, name, value);
    };

    handleKeyDown = (e) => {
        switch (e.key) {
            case 'ArrowDown':
                if (this.state.selectedPropertyIndex < this.properties.length - 1) {
                    this.setState({...this.state, selectedPropertyIndex: this.state.selectedPropertyIndex + 1});
                }
                break;
            case 'ArrowUp':
                if (this.state.selectedPropertyIndex > 0) {
                    this.setState({...this.state, selectedPropertyIndex: this.state.selectedPropertyIndex - 1});
                }
                break;
            default:
                break;
        }
    };

    handleSelect = (selectedType) => {
        this.setState({...this.state, selectedPropertyIndex: _.findIndex(this.properties, property => property === selectedType)});
    };
}

const ControlDescriptorShape = PropTypes.shape({
    type: PropTypes.string.isRequired,
    group: PropTypes.string,
    label: PropTypes.string,
    options: PropTypes.array
});

const PropertyShape = PropTypes.shape({
    objectId: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    value: PropTypes.any,
    type: PropTypes.string,
    controlDescriptor: ControlDescriptorShape,
});

const PropertyGroupShape = PropTypes.shape({
    name: PropTypes.string,
    properties: PropTypes.arrayOf(PropertyShape)
});

PropPanel.propTypes = {
    propertyGroups: PropTypes.arrayOf(PropertyGroupShape),
    translate: PropTypes.func.isRequired,
    onPropertyChange: PropTypes.func.isRequired,
    onDeleteElement: PropTypes.func,
    onSelect: PropTypes.func
};

export default PropPanel;
