import {createAction, handleActions} from 'redux-actions';
import {createSelector} from 'reselect';
import _ from 'lodash';

import {
    getSelectedTransition, getTransitionIds, getTransitions, getTransitionsByEndStepId,
    getTransitionsByStartStepId,
    selectTransition
} from "./transitions";
import {getMaxStepPositionX, getMaxStepPositionY, getMinStepPositionX, getMinStepPositionY} from "../../redux/modules/stepsLayout"
import {calculateAnchorCoordinatesByPosition, getAllStepIds, selectStep} from "./steps";
import {
    continueTransitionCreation, endTransitionCreation, getTempTransitionEndPointPosition, startTempTransition
} from "./tempTransition";
import {getStepLayoutById, getStepsLayout} from "./stepsLayout";
import {setCreatingTransitionByShortcutsType} from "./transitionActionType";
import {copyTransition, getTransitionCopy} from "./transitionCopy";
import {DRAWING_AREA, setFocusOnGlobalArea} from "./processPlan";
import api from "../../api";

const NEXT_POSITION = {
    left: 'top',
    top: 'right',
    right: 'bottom',
    bottom: 'left'
};
const PREVIOUS_POSITION = {
    left: 'bottom',
    bottom: 'right',
    right: 'top',
    top: 'left'
};
const SCALE_STEP = 0.05;

//- Actions
export const selectDrawingArea = createAction("DRAWING_AREA_UPDATE_VISUALIZATION");
export const toggleTransitionInfo = createAction("TRANSITION_INFO_TOGGLE");
export const fitTheViewBox = () => (dispatch, getState) => dispatch(fitTheView(getState().stepsLayout));
export const updateViewportSize = createAction("DRAWING_AREA_VIEWPORT_SIZE_UPDATE", size => size);
export const updateViewportPosition = createAction("DRAWING_AREA_VIEWPORT_POSITION_UPDATE", position => position);
export const updateOriginalViewportSize = createAction("DRAWING_AREA_ORIGINAL_VIEWPORT_SIZE_UPDATE", size => size);
export const updateOriginalViewportPosition = createAction("DRAWING_AREA_ORIGINAL_VIEWPORT_POSITION_UPDATE", position => position);
export const incrementZoomScale = createAction("DRAWING_AREA_ZOOM_SCALE_INCREMENT", (sign) => sign);
export const resetToTheBaseViewBox = createAction('DRAWING_AREA_ORIGINAL_VIEWPORT_SIZE_RESET');
export const fitTheView = createAction('FIT_THE_VIEW', stepsLayout => stepsLayout);
export const toggleSnapToGrid = createAction('SNAP_TO_GRID_TOGGLE');
export const changeScreenSize =  createAction('DRAWING_AREA_RESIZE_VIEWPORT_SIZE');

export const loadViewSettings = createAction('PROCESS_PLAN_VIEW_SETTINGS_LOAD', () => api.loadSettings());
export const saveViewSettings = createAction('PROCESS_PLAN_VIEW_SETTINGS_SAVE', settings => api.updateSettings(settings));

export const moveToAnotherTransitionFromSameStep = (direction) => (dispatch, getState) => {
    let selectedTransition = getSelectedTransition(getState());
    if (selectedTransition) {
        let allTransitionsIds = _.map(getTransitionsByStartStepId(getState(), selectedTransition.startStepId), transition => transition.id);
        let index = _.findIndex(allTransitionsIds, transitionId => transitionId === selectedTransition.id);
        let sign = direction === 'left' ? 1 : -1;
        dispatch(selectTransition(allTransitionsIds[(index + sign + allTransitionsIds.length) % allTransitionsIds.length]))
    }
};

export const moveToAnotherTransitionToSameStep = (direction) => (dispatch, getState) => {
    let selectedTransition = getSelectedTransition(getState());

    if (selectedTransition) {
        let allTransitionIds = _.map(getTransitionsByEndStepId(getState(), selectedTransition.endStepId), transition => transition.id);
        let index = _.findIndex(allTransitionIds, transitionId => transitionId === selectedTransition.id);
        let sign = direction === 'left' ? 1 : -1;
        dispatch(selectTransition(allTransitionIds[(index + sign + allTransitionIds.length) % allTransitionIds.length]));
    }
};

export const selectAnotherElement = (direction) => (dispatch, getState) => {
    let selectedTransition = getSelectedTransition(getState());
    let selectedStepId = getSelectedStepId(getState());

    if (!_.isNil(selectedTransition)) {
        let stepId = direction === 'bottom' ? selectedTransition.endStepId : selectedTransition.startStepId;
        if (stepId !== 'end') {
            dispatch(selectStep(stepId));
        } else {
            dispatch(setFocusOnGlobalArea(DRAWING_AREA));
        }
        dispatch(selectTransition(null));
    } else if (!_.isNil(selectedStepId)) {
        let allTransitions = direction === 'bottom' ? getTransitionsByStartStepId(getState(), selectedStepId) : getTransitionsByEndStepId(getState(), selectedStepId);
        if (_.keys(allTransitions).length !== 0) {
            dispatch(selectTransition(allTransitions[_.keys(allTransitions)[0]].id));
        } else {
            dispatch(setFocusOnGlobalArea(DRAWING_AREA));
        }
        dispatch(selectStep(null));
    } else {
        let stepsIds = _.filter(getAllStepIds(getState()), id => id !== 'end');
        let transitionsIds = getTransitionIds(getState());

        if (stepsIds.length !== 0) {
            dispatch(selectStep(stepsIds[0]));
            dispatch(selectDrawingArea(null));
            dispatch(selectTransition(null));
        }
        else if (transitionsIds.length !== 0) {
            dispatch(selectTransition(transitionsIds[0]));
            dispatch(selectDrawingArea(null));
            dispatch(selectStep(null));
        }
    }
};

export const startTempTransitionByShortcut = (position, selectedStepId) => (dispatch, getState) => {
    if (selectedStepId) {
        let availableStepsLayout = _.chain(getStepsLayout(getState()))
            .pickBy((layout, stepId) => stepId !== 'start' && stepId !== selectedStepId)
            .mapValues((layout, stepId) => ({...layout, stepId: stepId}))
            .values()
            .value();

        if (availableStepsLayout.length !== 0) {
            dispatch(startTempTransition(selectedStepId, position));
            dispatch(setCreatingTransitionByShortcutsType());
            dispatch(continueTransitionCreation({position: 'top', ...calculateAnchorCoordinatesByPosition('top', availableStepsLayout[0])}));
            dispatch(copyTransition({
                start: {stepId: selectedStepId},
                end: {stepId: availableStepsLayout[0].stepId, position: 'top'}
            }));
        }
    }
};

export const updateTempTransitionByShortcut = (direction) => (dispatch, getState) => {
    let transitionCopy = getTransitionCopy(getState());

    let availableStepsLayout = _.chain(getStepsLayout(getState()))
        .pickBy((layout, stepId) => stepId !== 'start' && stepId !== transitionCopy.start.stepId)
        .mapValues((layout, stepId) => ({...layout, stepId: stepId}))
        .values()
        .value();

    let sign = direction === 'left' ? 1 : -1;

    let index = _.findIndex(availableStepsLayout, layout => layout.stepId === transitionCopy.end.stepId);
    index = (index + sign + availableStepsLayout.length) % availableStepsLayout.length;

    dispatch(continueTransitionCreation({position: transitionCopy.end.position, ...calculateAnchorCoordinatesByPosition(transitionCopy.end.position, availableStepsLayout[index])}));

    dispatch(copyTransition({
        start: {stepId: transitionCopy.start.stepId},
        end: {stepId: availableStepsLayout[index].stepId, position: transitionCopy.end.position}
    }));
};

export const changeTempTransitionPositionByShortcut = (direction) => (dispatch, getState) => {
    let currentPosition = getTempTransitionEndPointPosition(getState());

    if (currentPosition) {
        let newPosition = direction === 'bottom' ? NEXT_POSITION[currentPosition] : PREVIOUS_POSITION[currentPosition];
        let endPointStepId = getTransitionCopy(getState()).end.stepId;
        let newPoint = calculateAnchorCoordinatesByPosition(newPosition, getStepLayoutById(getState(), endPointStepId));

        dispatch(continueTransitionCreation({position: newPosition, ...newPoint}));

        let transitionCopy = getTransitionCopy(getState());
        dispatch(copyTransition({
            start: {stepId: transitionCopy.start.stepId},
            end: {stepId: transitionCopy.end.stepId, position: newPosition}
        }));
    }
};

export const endTransitionCreationByShortcut = () => (dispatch, getState) => {
    let transitionCopy = getTransitionCopy(getState());

    dispatch(endTransitionCreation(transitionCopy.end.stepId, transitionCopy.end.position));
};

const markGroup = (steps, labels, label, stepId) => {
    labels[stepId] = label;
    _.map(steps[stepId], connectedStepId => {
        if (labels[connectedStepId] === 0) {
            labels = markGroup(steps, labels, label, connectedStepId)
        }
    });
    return labels;
};

export const moveToAnotherGroup = () => (dispatch, getState) => {
    let selectedStepId = getSelectedStepId(getState());
    let transitions = getTransitions(getState());
    let stepIds = _.filter(getAllStepIds(getState()), stepId => stepId !== 'end');

    let steps = _.chain(stepIds)
        .map(stepId => [stepId, []])
        .fromPairs()
        .value();

    if (!_.isNil(selectedStepId)) {
        if (_.isNil(_.find(transitions, transition => transition.startStepId === 'start'))) {
            steps.start = [];
        }

        let labels = _.chain(steps)
            .map((connection, stepId) => [stepId, 0])
            .fromPairs()
            .value();

        _.map(transitions, transition => {
            if (transition.endStepId !== 'end') {
                steps[transition.startStepId].push(transition.endStepId);
                steps[transition.endStepId].push(transition.startStepId);
            }
        });

        let maxLabel = 0;
        _.mapValues(steps, (connection, stepId) => {
            if (labels[stepId] === 0) {
                maxLabel++;
                labels = markGroup(steps, labels, maxLabel, stepId);
            }
        });

        let currentLabel = labels[selectedStepId];

        if (maxLabel !== 1) {
            let nextLabel = currentLabel + 1;
            if (nextLabel > maxLabel) {
                nextLabel = 1;
            }
            let nextStepId = _.findKey(labels, label => label === nextLabel);
            dispatch(selectStep(nextStepId));
        }
    } else if (stepIds.length !== 0) {
        dispatch(selectStep(stepIds[0]));
        dispatch(selectTransition(null));
        dispatch(selectDrawingArea(null));
    }
};

//- State
const initialState = {
    selected: false,
    scale: 1,
    viewportSize: {
        width: 600,
        height: 500
    },
    originalViewportSize: {
        width: 600,
        height: 500
    },
    originalViewportPosition:{
        x: 0,
        y: 0
    },
    viewportPosition: {
        x: 0,
        y: 0
    },
    showTransitionInfo: false,
    selectedStepId: null,
    snapToGrid: false,
    zoomToFit: false,
    fontSize: 14,
    settingsLoaded: false,
    translations:{}
};

//- Reducers
export default handleActions({
    DRAWING_AREA_VIEWPORT_SIZE_UPDATE: (state, action) => {
        return  {
            ...state,
            viewportSize: action.payload,
            zoomToFit: false
        };
    },
    DRAWING_AREA_VIEWPORT_POSITION_UPDATE: (state, action) => {
        return  {
            ...state,
            viewportPosition: action.payload,
            zoomToFit: false
        };
    },
    DRAWING_AREA_ORIGINAL_VIEWPORT_SIZE_UPDATE: (state, action) => {
        return  {
            ...state,
            originalViewportSize: action.payload,
            zoomToFit: false
        };
    },
    DRAWING_AREA_ORIGINAL_VIEWPORT_POSITION_UPDATE: (state, action) => {
        return  {
            ...state,
            originalViewportPosition: action.payload,
            zoomToFit: false
        };
    },
    DRAWING_AREA_ZOOM_SCALE_INCREMENT : (state, action) => {
        let scaleValue = +(state.scale + SCALE_STEP * action.payload).toFixed(2);    // We use '+' to convert string returned by toFixed back to number
        let width = Math.round(state.originalViewportSize.width * scaleValue);
        let height = Math.round(state.originalViewportSize.height * scaleValue);
        if (width !== 0 && height !==0 ) {
            return {
                ...state,
                scale: scaleValue,
                viewportSize: {width, height},
                zoomToFit: false
            };
        } else {
            return {...state};
        }
    },
    DRAWING_AREA_ORIGINAL_VIEWPORT_SIZE_RESET: state => {
        return {
            ...state,
            viewportSize: state.originalViewportSize,
            viewportPosition: {x:0, y:0},
            scale: 1,
            zoomToFit: false
        };
    },
    DRAWING_AREA_RESIZE_VIEWPORT_SIZE: state => {
        return {
            ...state,
            viewportSize: state.originalViewportSize,
            viewportPosition: state.viewportPosition,
            scale: 1,
            zoomToFit: false
        };
    },
    FIT_THE_VIEW: (state, action) => {
        let fittedViewportPosition = {
            x: getMinStepPositionX(action.payload),
            y: getMinStepPositionY(action.payload)
        };
        let fittedViewportSize =  {
            width: Math.round(getMaxStepPositionX(action.payload) - fittedViewportPosition.x),
            height: Math.round(getMaxStepPositionY(action.payload) - fittedViewportPosition.y)
        };
        let scaleValue;
        let scaleValueForWidth = +(fittedViewportSize.width / state.originalViewportSize.width).toFixed(2);
        let scaleValueForHeight = +(fittedViewportSize.height / state.originalViewportSize.height).toFixed(2);

        if (scaleValueForWidth > scaleValueForHeight) {
            fittedViewportSize.height = Math.round(state.originalViewportSize.height * scaleValueForWidth);
            scaleValue = scaleValueForWidth;
        } else{
            fittedViewportSize.width =  Math.round(state.originalViewportSize.width * scaleValueForHeight);
            scaleValue = scaleValueForHeight;
        }
        return  {
            ...state,
            viewportSize: fittedViewportSize,
            viewportPosition: fittedViewportPosition,
            svgPosition: fittedViewportPosition,
            scale: scaleValue,
            zoomToFit: true
        };
    },
    DRAWING_AREA_UPDATE_VISUALIZATION: (state, action) => {
        return {...state, selected: !_.isNil(action.payload)};
    },
    TRANSITION_INFO_TOGGLE: (state) => {
        return {...state, showTransitionInfo: !state.showTransitionInfo};
    },
    STEP_SELECT: (state, action) => {
        return {...state, selectedStepId: action.payload};
    },
    SNAP_TO_GRID_TOGGLE: (state) => {
        return {...state, snapToGrid: !state.snapToGrid};
    },
    PROCESS_PLAN_VIEW_SETTINGS_LOAD: (state, action) => {
        let {
            fontSize,
            showTransitionInfo,
            snapToGrid,
            zoomFactor,
            zoomToFit,
            translations
        } = action.payload;
        let width = Math.round(state.originalViewportSize.width * zoomFactor);
        let height = Math.round(state.originalViewportSize.height * zoomFactor);
        return {
            ...state,
            fontSize,
            showTransitionInfo,
            snapToGrid,
            scale: zoomFactor,
            zoomToFit,
            viewportSize: {width, height},
            translations
        };
    }
}, initialState);

//- Selectors
export const getDrawingArea = state => state.drawingArea;

export const getViewSettings = createSelector(
    getDrawingArea,
    drawingArea => ({
        showTransitionInfo: drawingArea.showTransitionInfo,
        snapToGrid: drawingArea.snapToGrid,
        zoomToFit: drawingArea.zoomToFit,
        zoomFactor: drawingArea.scale,
        fontSize: drawingArea.fontSize,
        translations: drawingArea.translations
    })
);

export const getScale = createSelector(
    getDrawingArea,
    drawingArea => drawingArea.scale
);

export const getSvgPosition = createSelector(
    getDrawingArea,
    drawingArea => drawingArea.viewportPosition
);

export const isTransitionsInfoShown = createSelector(
    getDrawingArea,
    drawingArea => drawingArea.showTransitionInfo
);

export const getSelectedStepId = createSelector(
    getDrawingArea,
    drawingArea => drawingArea.selectedStepId
);

export const getSnapToGrid = createSelector(
    getDrawingArea,
    drawingArea => drawingArea.snapToGrid
);

export const isZoomToFitEnabled = createSelector(
    getDrawingArea,
    drawingArea => drawingArea.zoomToFit
);

export const areSettingsLoaded = createSelector(
    getDrawingArea,
    drawingArea => drawingArea.settingsLoaded
);

export const getTranslations = createSelector(
    getDrawingArea,
    drawingArea => drawingArea.translations
);

