import {Change} from './Change';
import _ from 'lodash';

export class RemoveConnection extends Change {
    constructor(startStepId) {
        super('Remove connection');
        this.startStepId = startStepId;

        this.connectedLayout = null;
        this.connectedProperties = null;
    }

    applyRemoveConnection(connections) {
        this.connectedLayout = _.filter(connections, connection => connection.startStepId === this.startStepId);
        return _.filter(connections, connection => connection.startStepId !== this.startStepId);
    }
    revertRemoveConnection(transitions) {
        return _.concat(transitions, this.connectedLayout);
    }

    applyRemoveConnectionProperty(properties){
        return _.map(properties, property => {
            if (property.stepId === this.startStepId && property.name === "step_id"){
                this.connectedProperties = property.value;
                property.value = null;
            }
            return property
        });
    }

    revertRemoveConnectionProperty(properties){
        return _.map(properties, property => {
            if (property.stepId === this.startStepId && property.name === "step_id"){
                property.value = this.connectedProperties;
            }
            return property
        });
    }
}

