import {createAction, handleActions} from 'redux-actions';
import _ from 'lodash';

const VIEWPORT_OFFSET = 60;

//- Actions
export const updateStepsLayoutPosition = createAction("STEPS_LAYOUT_POSITION_UPDATE", (id, position) => ({id, position}));
export const updateStepsLayoutSize = createAction("STEPS_LAYOUT_SIZE_UPDATE", (id, size) => ({id, size}));
export const moveStep = createAction("STEP_MOVE", (id, direction) => ({id, direction}));

//- State
const initialState = {};
const DELTA = 5;

export const DELTA_BY_DIRECTION = {
    left: {deltaX: -DELTA, deltaY: 0},
    top: {deltaX: 0, deltaY: -DELTA},
    right: {deltaX: DELTA, deltaY: 0},
    bottom: {deltaX: 0, deltaY: DELTA}
};

//- Reducers
export default handleActions({
    PROCESS_PLAN_LOAD: (state, action) => {
        return action.payload.stepsLayout;
    },
    STEPS_LAYOUT_POSITION_UPDATE: (state, action) => {
        return _.mapValues(state, (stepLayout, id) => {
            if (id === action.payload.id) {
                let scaledPosition = {
                    x: action.payload.position.x ,
                    y: action.payload.position.y
                };
                return {...stepLayout,  position: scaledPosition};
            }
            return stepLayout;
        });
    },
    STEPS_LAYOUT_SIZE_UPDATE: (state, action) => {
        return _.mapValues(state, (stepLayout, id) => {
            if (id === action.payload.id) {
                return {...stepLayout, size: action.payload.size};
            }
            return stepLayout;
        });
    },
    CHANGE_APPLY: (state, {payload: change} ) => {
        switch(change.description) {
            case 'Move step':
                return change.applyStepsLayout(state);
            case 'Remove step':
                return change.applyRemoveStepLayout(state);
            case 'Add step':
                return change.applyAddStepLayout(state);
            case 'Insert new step into transition':
                return change.applyInsertStepLayout(state);
            case 'Generate auto layout':
                return change.applyLayoutAutoGenerationForStepsLayout(state);
            default:
                return state;
        }
    },
    CHANGE_REVERT: (state, {payload: change} ) => {
        switch(change.description) {
            case 'Move step':
                return change.revertStepsLayout(state);
            case 'Remove step':
                return change.revertRemoveStepLayout(state);
            case 'Add step':
                return change.revertAddStepLayout(state);
            case 'Insert new step into transition':
                return change.revertInsertStepLayout(state);
            case 'Generate auto layout':
                return change.revertLayoutAutoGenerationForStepsLayout(state);
            default:
                return state;
        }
    },
    STEP_MOVE: (state, action) => {
        let deltaX = action.payload.direction.length > 0 ? DELTA_BY_DIRECTION[action.payload.direction[0]].deltaX : 0;
        deltaX += action.payload.direction.length > 1 ? DELTA_BY_DIRECTION[action.payload.direction[1]].deltaX : 0;

        let deltaY = action.payload.direction.length > 0 ? DELTA_BY_DIRECTION[action.payload.direction[0]].deltaY : 0;
        deltaY += action.payload.direction.length > 1 ? DELTA_BY_DIRECTION[action.payload.direction[1]].deltaY : 0;
        return _.mapValues(state, (layout, id) => id === action.payload.id ? {...layout, position: {x: layout.position.x + deltaX, y: layout.position.y + deltaY}} : layout);
    }
}, initialState);

//- Selectors
export const getStepsLayout = state => state.stepsLayout;

export const getStepPositionById = (state, id) => {
    return _.find(getStepsLayout(state), (layout, stepId) => stepId === id).position;
};

export const getStepLayoutById = (state, id) => {
    return _.find(getStepsLayout(state), (layout, stepId) => stepId === id);
};

export const getMaxStepPositionX = (stepsLayout) => {
    let firstStepKey = Object.keys(stepsLayout)[0];
    let maxStepPositionX = _.get(stepsLayout, firstStepKey + '.position.x') +
                           _.get(stepsLayout, firstStepKey + '.size.width');
    for (let id in stepsLayout) {
        if (maxStepPositionX < stepsLayout[id].position.x + stepsLayout[id].size.width) {
            maxStepPositionX = stepsLayout[id].position.x + stepsLayout[id].size.width;
        }
    }
    return maxStepPositionX + VIEWPORT_OFFSET;
};

export const getMaxStepPositionY = (stepsLayout) => {
    let firstStepKey = Object.keys(stepsLayout)[0];
    let maxStepPositionY = _.get(stepsLayout, firstStepKey + '.position.y') +
                           _.get(stepsLayout, firstStepKey + '.size.height');
    for (let id in stepsLayout) {
        if (maxStepPositionY < stepsLayout[id].position.y + stepsLayout[id].size.height) {
            maxStepPositionY = stepsLayout[id].position.y + stepsLayout[id].size.height;
        }
    }
    return maxStepPositionY + VIEWPORT_OFFSET;
};

export const getMinStepPositionX = (stepsLayout) => {
    let firstStepKey = Object.keys(stepsLayout)[0];
    let minStepPositionX = _.get(stepsLayout, firstStepKey + '.position.x');
    for (let id in stepsLayout) {
        if (minStepPositionX > stepsLayout[id].position.x ) {
            minStepPositionX = stepsLayout[id].position.x;
        }
    }
    return minStepPositionX - VIEWPORT_OFFSET;
};

export const getMinStepPositionY = (stepsLayout) => {
    let firstStepKey = Object.keys(stepsLayout)[0];
    let minStepPositionY = _.get(stepsLayout, firstStepKey + '.position.y');
    for (let id in stepsLayout) {
        if (minStepPositionY> stepsLayout[id].position.y) {
            minStepPositionY = stepsLayout[id].position.y;
        }
    }
    return minStepPositionY - VIEWPORT_OFFSET;
};