import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './Anchor.css';
import {HEADER_OFFSET} from "../../../App";
import _ from 'lodash';
const ANCHOR_OFFSET = 15;

class Anchor extends Component {
    render() {
        let outlineClassName = 'anchor-outline ' + (this.props.anchorHighlighted ? ' anchorHighlighted ' : '') + this.props.stepClassName + (this.props.hover ? ' hover ' : '') + (this.props.selectable  ? ' selectable ' : '') + (this.props.active ? ' active ' : '') + (this.props.selected ? ' selected ' : '');
        let anchorClassName = 'anchor-point ' + this.props.stepClassName + (this.props.hover ? ' hover ' : '') + (this.props.selectable  ? ' selectable ' : '') + (this.props.active  ? ' active ' : '') + (this.props.selected ? ' selected ': '');
        return (
            <g className="anchor">
                <circle className={outlineClassName}
                        cx={this.props.x}
                        cy={this.props.y}
                        r={this.props.outlineRadius}
                        onMouseUp={this.onMouseUp}
                        onMouseOver={this.onMouseOver}
                        onMouseOut={this.onMouseOut}/>

                <circle className={anchorClassName}
                        cx={this.props.x}
                        cy={this.props.y}
                        r={this.props.pointRadius}
                        onMouseDown={this.onMouseDown}
                        onMouseUp={this.onMouseUp}
                        onTouchStart={this.onHandleTouchStart}
                        onTouchEnd={this.handleTouchEnd}
                        onTouchEndCapture={this.handleCreateTransitionByTouch}
                        onTouchCancel={this.handleDeleteTempTransition}
                        onMouseOver={this.onMouseOver}/>
            </g>
        );
    }

    onMouseUp = () => {
        if (this.props.hover) {
            this.props.handleMouseUp(this.props.position)
        }
    };

    onMouseOver = () => {
        if (this.props.hover) {
            this.props.handleMouseOver(this.props.position);
        }
    };

    onMouseDown = (e) => {
        // Avoid move viewport
        e.stopPropagation();
        if (this.props.active) {
            this.props.handleMouseDown(this.props.position)
        }
    };

    onHandleTouchStart= (e) => {
        // Avoid move viewport
        e.stopPropagation();
        if (this.props.active) {
        this.props.handleMouseDown(this.props.position)
        }
        document.addEventListener('touchmove', this.handleTouchMove);
    };

    handleTouchMove = (e) => {
        this.computedPosition = {
            x: e.targetTouches[0].pageX,
            y: e.targetTouches[0].pageY ,
        };
        this.props.handleTouchTransitionMove({x: e.targetTouches[0].pageX,y: e.targetTouches[0].pageY - HEADER_OFFSET});
    };

    handleTouchEnd = (e) => {
        e.stopPropagation();
        if (!_.isUndefined(this.computedPosition)){
            let anchor = document.elementFromPoint(this.computedPosition.x, this.computedPosition.y);
            if (!_.isNull(anchor) && !_.isNull(anchor.cx) && !_.isUndefined(anchor.cx) && !_.isNull(anchor.cy) && !_.isUndefined(anchor.cy)){
                let position = anchor.className.baseVal.split(' ')[1];
                let step;
                switch (position) {
                    case 'top':
                        step = document.elementFromPoint(this.computedPosition.x, this.computedPosition.y + ANCHOR_OFFSET);
                        break;
                    case 'right':
                        step = document.elementFromPoint(this.computedPosition.x - ANCHOR_OFFSET, this.computedPosition.y);
                        break;
                    case 'bottom':
                        step = document.elementFromPoint(this.computedPosition.x, this.computedPosition.y - ANCHOR_OFFSET);
                        break;
                    case 'left':
                        step = document.elementFromPoint(this.computedPosition.x + ANCHOR_OFFSET, this.computedPosition.y);
                        break;
                    default:
                        break;
                }
                this.props.handleCreateTransitionByTouch(step.id, position);
            }else{
                this.props.handleDeleteTempTransition();
            }
        }
        document.removeEventListener('touchmove', this.handleTouchMove);
    };

    onMouseOut = () => {
        if (this.props.hover) {
            this.props.handleMouseOut();
        }
    };
}

Anchor.propTypes = {
    stepClassName: PropTypes.string.isRequired,
    x: PropTypes.number.isRequired,
    y: PropTypes.number.isRequired,
    pointRadius: PropTypes.number.isRequired,
    outlineRadius: PropTypes.number.isRequired,
    position: PropTypes.string.isRequired,
    selected: PropTypes.bool.isRequired,
    active: PropTypes.bool.isRequired,
    hover: PropTypes.bool.isRequired,
    selectable: PropTypes.bool.isRequired,
    anchorHighlighted: PropTypes.bool,
    handleMouseDown: PropTypes.func,
    handleMouseUp: PropTypes.func,
    handleMouseOver: PropTypes.func,
    handleMouseOut: PropTypes.func,
    handleTouchTransitionMove: PropTypes.func,
    handleDeleteTempTransition: PropTypes.func,
    handleCreateTransitionByTouch: PropTypes.func
};

Anchor.defaultProps = {
    pointRadius: 5,
    outlineRadius: 12
};

export default Anchor;
