import React, { Component } from 'react';
import './SidePanel.css';

class SidePanel extends Component {
    render() {
        return (
            <div className="side-panel">
                <div className="side-panel-content">
                    <div>{this.props.children}</div>
                </div>
            </div>
        );
    }
}

export default SidePanel;
