import {handleActions, createAction} from 'redux-actions';

//- Actions
export const copyTransition = createAction("TRANSITION_COPY", transition => transition);

//- State
const initialState = {};

//- Reducers
export default handleActions({
    TRANSITION_COPY: (state, action) => {
        return {...action.payload};
    }
}, initialState);

//- Selectors
export const getTransitionCopy = state => state.transitionCopy;