import {connect} from 'react-redux';
import {getTranslate} from 'react-localize-redux';
import _ from 'lodash';
import {
    SELECTABLE_PROPERTIES_NUMBER_BY_STEP_PROPERTY_NAME
} from "../../../redux/modules/stepProperties";
import PropPanel from './PropPanel';
import {getFocusedArea, SIDE_PANEL} from "../../../redux/modules/processPlan";
import {getSelectedStepId} from "../../../redux/modules/drawingArea";
import {applyChange} from "../../../redux/modules/changeStack";
import {ChangeStepProperty} from "../../../redux/model/ChangeStepProperty";
import {getSelectedStepPropsWithControlDescriptors} from '../../../redux/modules/compositeSelectors';

export const mapStateToProps = (state) => {
    let propertyGroups = getSelectedStepPropsWithControlDescriptors(state);
    let selectablePropertiesNumber = _.chain(propertyGroups)
        .map(group => group.properties)
        .flatten()
        .map(property => SELECTABLE_PROPERTIES_NUMBER_BY_STEP_PROPERTY_NAME[property.controlDescriptor.type])
        .sum()
        .value();

    return ({
        objectId: getSelectedStepId(state),
        propertyGroups: getSelectedStepPropsWithControlDescriptors(state),
        selectablePropertiesNumber: selectablePropertiesNumber,
        selected: getFocusedArea(state) === SIDE_PANEL,
        translate: getTranslate(state.localize)
    })
};

const mapDispatchToProps = (dispatch) => {
    return {
        onPropertyChange: (stepId, name, value) => {
            dispatch(applyChange(new ChangeStepProperty(stepId, name, value)));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(PropPanel)