import {Change} from './Change';
import _ from 'lodash';
import {
    availableTransitionsByStepType,
    defaultPropertyValues,
    postProcessingOptionsByStartStepType,
    propertiesByStepType
} from "../../api/processPlanConversion";
import {GRID_CELL_SIZE} from "../../components/drawingArea/step/Step";
import {DEFAULT_STEP_POSITION, DEFAULT_STEP_SIZE} from "./AddStep";

export class InsertNewStepIntoTransition extends Change {
    constructor(stepId, stepType, position, id, newTransitionId, scale, translate, snapToGrid) {
        super('Insert new step into transition');
        this.stepId = stepId;
        this.stepType = stepType;
        this.position = position;
        this.id = id;
        this.newTransitionId = newTransitionId;
        this.scale = scale;
        this.translate = translate;
        this.snapToGrid = snapToGrid;

        this.step = null;
        this.stepLayout = null;
    }

    applyInsertStep(steps) {
        this.step = {
            id: this.stepId,
            type: this.stepType,
            selected: false,
            highlighted: false,
            intersected: false,
            intersectedAnchors: []
        };
        return {
            ...steps,
            [this.stepId]: this.step
        };
    }

    revertInsertStep(steps) {
        this.step = steps[this.stepId];
        return _.omitBy(steps, step => step.id === this.stepId);
    }

    applyInsertStepLayout(stepsLayout) {
        let scaledPosition;
        if (this.position) {
            (this.snapToGrid) ?
                scaledPosition = {
                    x: Math.round((this.position.x * this.scale + this.translate.x) / GRID_CELL_SIZE) * GRID_CELL_SIZE,
                    y: Math.round((this.position.y * this.scale + this.translate.y) / GRID_CELL_SIZE) * GRID_CELL_SIZE
                } : scaledPosition = {
                    x: this.position.x * this.scale + this.translate.x,
                    y: this.position.y * this.scale + this.translate.y
                };
        }
        this.stepLayout = {
            position: scaledPosition || DEFAULT_STEP_POSITION,
            size: DEFAULT_STEP_SIZE,
        };
        return {
            ...stepsLayout,
            [this.stepId]: this.stepLayout
        };
    }

    revertInsertStepLayout(stepsLayout) {
        this.stepLayout = stepsLayout[this.stepId];
        return _.omitBy(stepsLayout, step => step === this.stepLayout);
    }

    applyInsertStepProperty(stepProperty) {
        let newProperties = _.map(propertiesByStepType[this.stepType], name => ({
            stepId: this.stepId,
            name: name === 'index' ? 'step_id' : name,
            value: _.has(defaultPropertyValues, name) ? defaultPropertyValues[name] : null
        }));
        return [...stepProperty, ...newProperties];
    }

    revertInsertStepProperty(stepProperties) {
        return _.filter(stepProperties, stepProperty => stepProperty.stepId !== this.stepId);
    }

    applyInsertStepConnectionLayout(steps) {
        if (this.stepType !== 'filepickup') {
            return steps;
        }
        return [...steps, {startStepId: this.stepId, endStepId: null, endPosition: null}];
    }

    revertInsertStepConnectionLayout(steps) {
        if (this.stepType !== 'filepickup') {
            return steps;
        }
        return _.filter(steps, step => step.startStepId !== this.stepId);
    }

    applyAddTransitionForInsertStep(transitions) {
        this.prevTransition = _.find(transitions, transition => transition.id === this.id);
        return {
            ...transitions,
            [this.id]: {
                id: this.id,
                startStepId: this.prevTransition.startStepId,
                endStepId: this.stepId,
                selected: false,
                highlighted: false,
                intersected: false
            },
            [this.newTransitionId]: {
                id: this.newTransitionId,
                startStepId: this.stepId,
                endStepId: this.prevTransition.endStepId,
                selected: false,
                highlighted: false,
                intersected: false
            }
        }
    }

    revertAddTransitionForInsertStep(transitions) {
        transitions = _.omit(transitions, [this.newTransitionId]);
        return {
            ...transitions,
            [this.id]: {
                id: this.id,
                startStepId: this.prevTransition.startStepId,
                endStepId: this.prevTransition.endStepId,
                selected: false,
                highlighted: false,
                intersected: false
            }
        }
    }

    applyAddTransitionLayoutForInsertStep(transitionsLayout) {
        let prevTransitionLayout = _.find(transitionsLayout, (layout, transitionId) => transitionId === this.id);
        return {
            ...transitionsLayout, [this.newTransitionId]: {
                start: {position: prevTransitionLayout.start.position},
                end: {position: prevTransitionLayout.end.position},
            }
        }
    }

    revertAddTransitionLayoutForInsertStep(transitionsLayout) {
        return _.omit(transitionsLayout, [this.newTransitionId])
    }

    applyAddTransitionPropertiesForInsertStep(transitionProperties) {
        let properties = [...transitionProperties];
        properties.push({
            transitionId: this.newTransitionId,
            name: 'types',
            value: availableTransitionsByStepType[this.stepType]
        });

        _.forEach(postProcessingOptionsByStartStepType[this.stepType], option => {
            switch (option) {
                case 'certify':
                    properties.push({
                        transitionId: this.newTransitionId,
                        name: 'certify',
                        value: false
                    });
                    break;
                case 'create_report':
                    properties.push({
                        transitionId: this.newTransitionId,
                        name: 'create_report',
                        value: {checked: false, values: []}
                    });
                    break;
                default:
                    break;
            }
        });
        return properties
    }

    revertAddTransitionPropertiesForInsertStep(transitionProperties) {
        return _.filter(transitionProperties, transitionProperty => transitionProperty.transitionId !== this.newTransitionId)
    }
}