import {Change} from './Change';
import _ from 'lodash';

export class ChangeConnectionPosition extends Change {
    constructor(tempTransition) {
        super('Change connection position');
        this.tempTransition = tempTransition;
        this.connectedLayout = null;
        this.properties= null;
    }

    applyConnectionLayoutPositionChange(transitions) {
        this.connectedLayout = transitions;
        return _.map(transitions, connectionLayout => connectionLayout.startStepId === this.tempTransition.start.stepId ?
            {...connectionLayout, endStepId: this.tempTransition.end.stepId,  endPosition: this.tempTransition.end.position} : connectionLayout);

    }
    revertConnectionLayoutPositionChange(transitions) {
        return this.connectedLayout;
    }

    applyChangeConnectedStepProperty (properties){
        this.properties = properties;
        return _.map(properties, property => property.stepId === this.tempTransition.start.stepId && property.name === 'step_id' ? {
            ...property,
            value: this.tempTransition.end.stepId
        } : property);
    }
    revertChangeConnectedStepProperty(properties){
        return this.properties ;
    }
}